.article {
	background: $color-bg-lighter;
	display: inline-block;
	position: relative;
	width: 100%;
	padding-top: 0;

	h2,
	h3,
	h4,
	h5,
	h6 {
		margin: 1em 0;
		width: 90%;

		@media (max-width: $mobile-breakpoint-max) {
			width: 100%;
			padding-left: 0;
		}
	}

	.emph {
		transition: $transition-fast;
		display: inline-table;

		&:hover {
			color: $color-text-default;
		}
	}

	.hero-img {
		margin-bottom: $vertical-space-widgets-half;

		img {
			display: block;
			height: auto;
			width: 100%;
		}
	}

	.article__header__link {
		color: $black;
		transition: $transition-fast;

		&:hover {
			color: $color-text-default;
		}
	}

	.article__header__tag {
		background: $color-emph;
		color: $white;
		padding: .25em .5em;
		font-size: $font-size-text-small;
		font-weight: normal;
		margin-left: 1em;
		clear: both;
		font-family: $font-default;
		text-transform: uppercase;
		display: table;
	}

	@media (max-width: $mobile-breakpoint-max) {
		margin-top: 0;
		padding-top: $vertical-space-widgets;
	}

	.pin-container .pin-wrapper {
		@include span-columns(4);

		@media (max-width: $mobile-breakpoint-max) {
			@include span-columns(12);
			clear: both;
		}
	}

	.pin-container {
		display: inline-block;
		max-width: 960px;
		width: 100%;
	}

	p {
		color: $color-text-black;
		font-size: $font-size-extras-medium;
		font-weight: $font-weight-regular;
		line-height: 1.5em;
	}

	h2 {
		color: $color-text-black;
	}

	h3 {
		color: $color-text-black;
		font-size: $font-size-extras-medium-big;
	}

	.article__header {
		@include clearfix;
	}

	.article__header__kicker {
		color: $color-emph;
		display: none;
		font-size: .5em;
		line-height: 1em;
	}

	.article__header__headline {
		margin-bottom: $vertical-space-widgets;

		@media (max-width: $mobile-breakpoint-max) {
			margin-bottom: $vertical-space-widgets-half;
		}

		.article__header__headline__heading {
			color: $color-text-headline-default;
			display: inline;
			font-size: $article-heading-fontsize;
			line-height: $medium-lineheight;

			@media (max-width: $mobile-breakpoint-max) {
				font-size: $mobile-article-heading-fontsize;
			}
		}
	}

	.article__header__intro .article__header__intro__text {
		font-family: $font-family-meta;
		font-size: 1.125em;
		font-weight: 700;
		line-height: 1.4;
	}

	.article__header__intro {
		clear: both;
		margin-bottom: 0;
	}

	.article__header__heading {
		color: $color-text-headline-default;
		font-family: $font-headline;
		font-size: $article-heading-fontsize;
		font-weight: normal;
		font-style: normal;
		line-height: $medium-lineheight;
		margin: 0 0 $vertical-space-widgets;
		padding: $vertical-space-widgets;

		@media (max-width: $mobile-breakpoint-max) {
			font-size: 2em;
			padding: 0;
		}

		@include media(max-width $mobile-breakpoint-mini) {
			font-size: 1.375em;
		}
	}

	.hero-img__text,
	.hero-img__credits,
	.hero-video__text,
	.hero-video__credits,
	.hero-media__caption-datum,
	.hero-media__caption-text,
	.hero-media__caption-credits,
	.inline-media__caption-text,
	.inline-media__caption-credits {
		color: $color-text-light;
		font-family: $font-family-meta;
		font-size: 0.75em;
		font-weight: normal;
		margin-top: $vertical-space-widgets-half;
		padding: 0;
	}

	.hero-img__text,
	.hero-video__text,
	.hero-img__caption-toggle,
	.hero-media__caption-datum {
		@include span-columns(12);

		@media (max-width: $mobile-breakpoint-max) {
			@include span-columns(12);
			@include omega;
		}
	}

	.hero-img__text {
		p {
			margin: 0;
		}
	}

	.hero-media__caption-datum {
		font-weight: $font-weight-bold;
		margin-bottom: $vertical-space-widgets-half;
	}

	.hero-img__credits,
	.hero-video__credits,
	.hero-media__caption-credits {
		@include span-columns(12);
		text-align: right;

		@media (max-width: $mobile-breakpoint-max) {
			@include span-columns(12);
			@include omega;
		}
	}

	.hero-img__caption-toggle {
		display: none;
	}

	/* address,
	time {
		color: $color-text-light;
		font-family: $font-family-meta;
		font-size: $font-size-extras-medium;
		font-style: normal;
		font-weight: $font-weight-regular;
		margin-bottom: $vertical-space-widgets;
	} */

	.sidebar {
		min-width: 320px;
		padding-left: $vertical-space-widgets-half;

		@media (max-width: $mobile-breakpoint-max) {
			padding-right: $vertical-space-widgets-half;
		}
	}

	.article__body {
		@include span-columns(8);
		@include omega;
		float: right;
		padding: 0 $vertical-space-widgets-half;

		h2,
		h3,
		h4,
		h5,
		h6 {
			padding-left: $vertical-space-widgets;
			width: 90%;

			@media (max-width: $mobile-breakpoint-max) {
				padding-left: 0;
				width: 100%;
			}
		}

		p {
			margin: 1.5em 0;
			margin-right: $vertical-space-widgets;
			padding-right: $vertical-space-widgets;
			padding-left: $vertical-space-widgets;

			&:first-of-type {
				margin-top: 0;
			}

			@media (max-width: $mobile-breakpoint-max) {
				margin: 1em 0;
				padding-right: 0;
				padding-left: 0;
			}
		}

		@media (max-width: $mobile-breakpoint-max) {
			@include span-columns(12);
		}

		.twitter-tweet {
			min-width: 80px !important;
		}
	}

	.article__body p,
	.article__body li {
		color: $color-text-default;
		font-size: $font-size-text-default;
		line-height: $bigger-lineheight;

		@media (max-width: $mobile-breakpoint-max) {
			font-size: 1em;
			line-height: 1.5em;
		}
	}

	&.article--single-column {
		@media (max-width: $mobile-breakpoint-max) {
			margin-bottom: $vertical-space-widgets-half;
		}

		.article__body {
			@include span-columns(12);
		}
	}

	figure {
		margin: 0;
	}

	.article__header {
		margin-bottom: $vertical-space-widgets-half;
	}

	.article__content,
	.article__header {
		padding: $vertical-space-widgets-half;
	}

	.container-meta-social {
		@include span-columns(5);
		position: relative;

		@media (max-width: $mobile-breakpoint-max) {
			@include span-columns(12);
		}

		.ad__rectangle {
			margin: 0;
		}
	}

	.container-article-content {
		@include span-columns(12);

		@media (max-width: $mobile-breakpoint-max) {
			@include span-columns(12);
		}
	}

	.inline-media img {
		@include span-columns(12);
		@include omega;
		height: auto;
	}

	.inline-media img:last-child {
		margin-right: auto;
	}

	.container-article-inline-media {
		display: inline-block;
	}

	.carousel__list {
		margin: 0;
	}

	.container-article-quote p {
		border-left: .25em solid $color-text-light;
		color: $color-text-light;
		display: inline-block;
		font-style: italic;
		padding-left: .75em;
	}

	.article-meta {
		color: $color-text-light;
		font-size: .75em;
		font-weight: $font-family-meta;
		margin-bottom: $vertical-space-widgets;
	}

	&.article--type-news .article__body .video-inline iframe {
		margin-bottom: 0;
	}

	.video-inline {
		&.inline-block--wide {
			width: calc(100% - 16px);

			@media (max-width: $mobile-breakpoint-max) {
				width: 100%;
			}
		}

		&.inline-block--left {
			padding-left: $vertical-space-widgets;
			margin-top: 8px;

			@media (max-width: $mobile-breakpoint-max) {
				padding-left: 0;
				margin-top: 0;
			}
		}

		&.inline-block--right {
			padding-right: $vertical-space-widgets;
			margin-top: 8px;

			@media (max-width: $mobile-breakpoint-max) {
				padding-right: 0;
				margin-top: 0;
			}
		}
	}

	.twitter-video {
		min-width: 100px !important;
	}

	.teaser-article-summary {
		.teaser-article-summary-headline .article__link {
			&:before {
				@include icon;
				@include icon--arrow-right-circle-invert;
				margin-right: $vertical-space-widgets-half;
			}
			color: $color-text-default;
			display: inline-flex;
			font-family: inherit;
			font-size: 20px;
			font-weight: 300;
			line-height: 1.5em;

			&:before,
			&:hover {
				color: $color-emph;
			}
		}
	}

	figure.teaser-media {
		margin-left: 1.5em;
		margin-bottom: 1em;
	}

	.inline-block--wide {
		margin-right: $vertical-space-widgets;
		padding-right: $vertical-space-widgets;
		padding-left: $vertical-space-widgets;

		@media (max-width: $mobile-breakpoint-max) {
			margin-right: 0;
			padding: 0;
		}
	}
}

.article.article--type-gallery,
.article.article--type-video {
	background: none;
	margin: 0;
	padding-top: 0;

	.article__header {
		background-color: $dark-grey;
		clear: both;
		padding: 1em;
	}

	.article__header__heading {
		color: $white;
		padding: 0;
	}

	.article__header__headline {
		.article__header__headline__heading {
			color: $white;
		}
	}

	.article__header__date {
		color: $color-text-light;
		font-family: $font-family-meta;
		font-size: .75em;
		font-weight: $font-weight-bold;
		margin: 0;
		padding: 0;
	}

	.socialbar__channels {
		float: right;

		@media (max-width: $mobile-breakpoint-max) {
			float: none;
		}
	}

	.inline-block--wide {
		float: none;
		margin-right: $vertical-space-widgets;

		@media (max-width: $mobile-breakpoint-max) {
			margin-right: 0;
		}
	}
}

// Flowplayer
.fp-brand,
.fp-timeline-tooltip.fp-tooltip,
.fp-dropdown.fp-dropup {
	display: none;
}

.main > .article:not(last-child):last-of-type {
	.teaser-article-summary {
		@media (max-width: $mobile-breakpoint-max) {
			margin-bottom: $vertical-space-widgets;
		}
	}
}

.next_article .hero-img__img {
	cursor: pointer;
}