// article__field__multi-tag_author

.article {
	.article__body {
		.author-info__name-link,
		.author-info__image-link {
			&:before {
				display: none !important;
			}
		}
	}
}

.author-info_wrapper {
	margin-bottom: 1rem;

	&.author-info_wrapper-date + .author-info__social {
		margin-top: -1rem;
	}
}

// Custom Klasse am Artikelfeld um den Autor auf die nchste Zeite zu zwingen
.custom-author-info__clear {
	clear: both;
}
