.redirect-notification {
	display: flex;
	background-color: $color-emph;
	color: #fff;
	width: 100%;
	padding: 0 20px;
	margin-bottom: 20px;

	p {
		text-align: center;
		margin: 5px 0;
		font-weight: 400;
		font-size: 15px;
		width: 100%;

		@include media(max-width $mobile-breakpoint-max) {
			text-align: left;
		}

		a {
			color: #fff;
			text-decoration: underline;
		}
	}

	.redirect-close {
		display: flex;
		align-items: center;
		margin-left: 25px;

		&::before {
			font-size: 21px;
			@include icon;
			@include icon--close;
		}

		&:hover {
			cursor: pointer;
		}
	}
}