/* Custom Constants */

$font-default: "DIN Next Rounded LT", Arial, Helvetica, Verdana, sans-serif;
$font-headline: "DIN Next Rounded LT", Arial, Helvetica, Verdana, sans-serif;
$font-family-meta: "DIN Next Rounded LT", Arial, Helvetica, Verdana, sans-serif;

/* Article Constants */
$article-heading-fontsize: 36px;

$medium-lineheight: 1.2;
$bigger-lineheight: 1.67;

/* Buttons load-more, read-more */
$buttons-more-background: #333;
$buttons-more-color: #fff;
$buttons-more-font: $font-default;
$buttons-more-font-size: 14px;
$buttons-more-font-weight: normal;

// Swiper Colors
$color-swiper-buttons: #fff;
$color-swiper-bullets: $color-emph;
$color-swiper-bullets-active: #4a7e3e;
$color-swiper-caption-text: #999;
$color-swiper-caption-credits: #999;
$color-swiper-pagecount: #444;
$color-swiper-pagecount-border: #666;

// ------ Author Info Styles -------

//  Author Info Name
$author-info_name-color: #333;
$author-info_name-font: $font-default;
$author-info_name-fontStyle: normal;
$author-info_name-fontSize: 12px;
$author-info_name-prefix-color: #939599;
$author-info_name-prefix-font: $font-default;
$author-info_name-prefix-fontStyle: normal;
$author-info_name-prefix-fontSize: 12px;
$author-info_name-suffix-color: #939599;
$author-info_name-suffix-font: $font-default;
$author-info_name-suffix-fontStyle: normal;
$author-info_name-suffix-fontSize: 12px;

// Author Info Date
$author-info_date-color: #333;
$author-info_date-font: $font-default;
$author-info_date-fontSize: 12px;

// ------ Breaking News -------
$breaking-news_space-top: 175px;
$breaking-news_space-right: 15px;
$breaking-news_space-sticky-top: 175px;
$breaking-news_mobile-space-top: 95px;
$breaking-news_mobile-space-sticky-top: 10px;
