.teaser.fullwidth {
	&.prio-1,
	&.prio-2,
	&.prio-4 {
		padding: 0;

		@include media(max-width $mobile-breakpoint-max) {
			margin: 0 10px 15px;
		}

		.teaser-stream-content {
			padding: 0;
			width: 100%;
		}
		.headline {
			font-size: 18px;
			margin-bottom: 14px;
			padding: 20px 20px 0;
			@include media(max-width $mobile-breakpoint-max) {
				padding: 20px 10px 0;
			}
		}

		.hashtag-bar {
			padding: 0 20px;
			text-transform: uppercase;
			@include media(max-width $mobile-breakpoint-max) {
				padding: 0 10px;
			}
		}

	}
	&.prio-3,
	&.prio-5,
	&.prio-6 {
		line-height: 1;
		min-height: 145px;
		padding: 0;

		@include media(max-width $mobile-breakpoint-max) {
			margin: 0 10px 15px;
		}

		.headline {
			font-size: 18px;
			line-height: 20px;
			margin-bottom: 14px;
			@include media(max-width $mobile-breakpoint-max) {
				min-height: 80px;
				word-wrap: break-word;
			}
		}

		.teaser-media {
			@include center(v, a);
			right: 0;
			height: 145px;
			a,
			img {
				height: 100%;
			}
			img {
				width: auto;
			}
			@include media(max-width $mobile-breakpoint-max) {
				height: 80px;
				width: 80px;
				top: 20px;
				transform: none;
				right: 10px;
			}
		}

		.advertisement {
			top: 5px;
			right: 5px;
			@include media(max-width $mobile-breakpoint-max) {
				background: rgba(255, 255, 255, .8);
				right: 10px;
				top: 20px;
			}
		}

		.hashtag-bar {
			text-transform: uppercase;
		}

		.teaser-stream-text {
			display: block;
			font-weight: normal;
			font-size: 16px;
			@include media(max-width $mobile-breakpoint-max) {
				display: none;
			}
		}
	}

	.headline {
		font-weight: 500;
	}

	.socialbar {
		left: 0;
		&.animate .socialbar__channels {
			@include center(v);
		}
	}

	.teaser-stream-content {
		padding: 20px;
		@include media(max-width $mobile-breakpoint-max) {
			padding: 20px 10px;
		}
	}

	.teaser-article-summary {
		display: none;
	}

	&.prio-5 {
		.teaser-article-summary {
			display: block;
		}
	}
}