// footer
// collapsable Menue

.text-centered {
	text-align: center;
}

.block-header {
	@include media(max-width $mobile-breakpoint-max) {
		padding-bottom: $vertical-space-imageblock;
	}
}

.collapsable {
	.block-header {
		.block-header__icon {
			@include media(max-width $mobile-breakpoint-max) {
				color: $black;
				display: block;
				position: static;

				&:before {
					bottom: 0;
					display: block;
					font-size: 2.5em;
					position: absolute;
					right: 30px;
				}
			}
		}
	}

	&.open {
		.block-header {
			.block-header__icon {
				@include media(max-width $mobile-breakpoint-max) {
					&:before {
						transform: rotate(-90deg);
					}
				}
			}
		}
	}

	&.open .menu__list {
		@include media(max-width $mobile-breakpoint-max) {
			display: block;
		}
	}
}

//footer

footer {
	border-top: 1px solid;
	clear: both;
	font-family: $font-extra;
	padding-top: 1.125em;
	padding-left: $vertical-space-widgets;

	@include media(min-width $mobile-breakpoint-max) {
		.block-header .block-header__icon:before {
			display: none;
		}
	}

	@include media(max-width $mobile-breakpoint-max) {
		margin-bottom: 2em;
		padding-left: 0;
	}

	.menu__list {
		margin-top: 0;
		padding: 0;

		@include media(max-width $mobile-breakpoint-max) {
			display: none;
			padding: $space-innerpage;
		}

		li {
			line-height: 1.125em;
			list-style: none;

			a {
				color: $lighter-grey;
				font-size: 0.75em;
				font-weight: normal;
			}
		}
	}

	.block-header {
		border-bottom: none;
		font-size: 1em;

		@include media(max-width $mobile-breakpoint-max) {
			font-family: $font-default;
			font-size: 1.125em;
		}

		@include media(min-width $mobile-breakpoint-max) {
			font-weight: $font-weight-bold;
			line-height: 1.5em;
			margin: 0;
			margin-bottom: 1em;
			padding: 0;
		}
	}

	.block-header a {
		@include media(min-width $mobile-breakpoint-max) {
			color: $color-text-light;
			text-decoration: none;
		}
	}

	.block-header--small::after {
		@include media(min-width $mobile-breakpoint-max) {
			border-bottom: none;
		}
	}
}

.footer {
	.has-one-columns,
	.footer__column {
		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
			@include omega;
			column-count: 1;
		}
	}

	.has-two-columns {
		@include span-columns(2 of 6);

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
			@include omega;
			column-count: 1;
		}
	}

	.has-three-columns {
		@include span-columns(3 of 6);

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
			@include omega;
			column-count: 1;
		}

		ul {
			margin: 0;
			padding: 0;

			@include media(min-width $mobile-breakpoint-max) {
				@include span-columns(4, block-collapse);
			}
		}
	}
}

.footer .footer__column {
	@include omega(6n);

	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(12 of 12);
		@include omega;
		@include clearfix;
	}

	.menu__list {
		@include media(max-width $mobile-breakpoint-max) {
			margin-bottom: $vertical-space-section;
			overflow: hidden;
			padding: 1em;
		}
	}

	.menu__list li {
		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(6 of 12);
			@include omega(2n);
		}
	}
}

.footer__row {
	@include media(min-width $mobile-breakpoint-min) {
		@include span-columns(12 of 12);
		@include clearfix;
		@include omega;
		border-bottom: 1px solid $grey;
		padding-bottom: $vertical-space-widgets;
		padding-top: $vertical-space-widgets;
	}

	&:last-child {
		@include media(min-width $mobile-breakpoint-min) {
			border-bottom: none;
		}
	}

	&:first-child {
		@include media(min-width $mobile-breakpoint-min) {
			padding-top: 0;
		}
	}
}

.footer__column-wide {
	.small-print__list__item__link,
	.small-print__list__item,
	.small-print__summary {
		color: $lighter-grey;
	}

	.small-print__list__item {
		.small-print__list__item__link {
			padding: 0 0.5em;
		}

		&:after {
			content: "|";
		}

		&:last-child:after {
			content: none;
		}
	}
}
