.socialbar {
	@include clearfix;
	margin-bottom: .5em;

	@include media(max-width $mobile-breakpoint-max) {
		margin-bottom: $vertical-space-widgets;
		text-align: center;
	}

	.socialbar__channels {
		position: relative;

		@include media(max-width $mobile-breakpoint-max) {
			display: inline-block;
			position: relative;
		}
	}

	.share-button__count {
		.count {
			color: $white;
		}
	}

	.share-button,
	.share-button-static {
		cursor: pointer;
		display: inline-block;
		float: left;
		margin: 0;

		&:last-child {
			margin: 0;
		}

		.share-button__icon {
			color: $white;
			display: inline-block;
			float: left;
			font-size: 1.25em;
			line-height: 2em;
			text-align: center;
			width: 2.75em;
		}

		.share-button__count {
			color: $white;
			float: left;
			font-size: .6875em;
			line-height: 25px;
			text-align: center;
			width: 3.75em;

			@include media(max-width $mobile-breakpoint-max) {
				line-height: 1.75em;
			}
		}

		.share-button__icon:before {
			@include icon;
		}

		&.facebook .share-button__icon {
			&:before {
				@include icon--facebook;
			}
			background-color: $facebook-dark;
		}

		&.facebook .share-button__count {
			background-color: $facebook-light;
			display: none;
			float: left;
		}

		&.twitter .share-button__icon {
			&:before {
				@include icon--twitter;
			}
			background-color: $twitter-dark;
		}

		&.twitter .share-button__count {
			background-color: $twitter-light;
			display: none;
			float: left;
		}

		&.google-plus .share-button__icon {
			background-color: $google-plus-dark;

			&:before {
				@include icon--googleplus;
			}

		}

		&.google-plus .share-button__count {
			background-color: $google-plus-light;
			float: left;
			padding-top: .0625em;
		}

		&.whatsapp .share-button__icon {
			background-color: $whatsapp-dark;

			&:before {
				@include icon--whatsapp;
			}
		}

		&.email .share-button__icon {
			background-color: $grey;

			&:before {
				@include icon--mail;
			}
		}

		&.email .share-button__icon:before,
		&.whatsapp .share-button__icon:before {
			margin: 0;
		}

		&.print .share-button__icon {
			background-color: $grey;
			display: none;
			margin: 0;

			&:before {
				@include icon--print;
			}
		}

		/* Mobile position, height, and width*/

		&.facebook .share-button__icon,
		&.twitter .share-button__icon,
		&.google-plus .share-button__icon,
		&.whatsapp .share-button__icon,
		&.email .share-button__icon,
		&.print .share-button__icon {
			@include media(max-width $mobile-breakpoint-max) {
				font-size: 1.375em;
				height: 2em;
				margin: 0;
			}

			@include media(max-width $mobile-breakpoint-mini) {
				font-size: 1em;
				height: 2em;
				margin: 0;
			}
		}

		&.whatsapp .share-button__icon,
		&.email .share-button__icon {
			@include media(max-width $mobile-breakpoint-max) {
				line-height: 2em;
			}
		}

		&.facebook .share-button__count,
		&.twitter .share-button__count,
		&.google-plus .share-button__count {
			@include media(max-width $mobile-breakpoint-max) {
				clear: both;
				margin-top: 1px;
			}
		}
	}

	&.sticky,
	&.solid {
		margin-bottom: 0;
		margin-left: -3.75em;
		padding-bottom: 1.875em;
		z-index: 999;

		.socialbar__channels {
			clear: both;
			float: none;
			text-align: left;
			width: 1.875em;
		}

		.share-button {
			@include span-columns(12 of 12);
			margin-bottom: 1.25em;
		}

		.share-button span,
		.share-button .share-button__count {
			clear: both;
			width: 100%;
		}

		.facebook .share-button__icon,
		.twitter .share-button__icon,
		.google-plus .share-button__icon {
			font-size: 1.25em;
			margin-top: .2em;
		}

		.email .share-button__icon,
		.print .share-button__icon {
			line-height: 1.5625em;
		}

		.print .share-button__icon {
			font-size: 1.25em;
		}

		.share-button .share-button__icon {
			height: 1.5625em;
			margin-bottom: 1px;
		}

		.email .share-button__icon {
			font-size: 1em;
			height: 2em;
			line-height: 2em;
		}

		.share-button .email .share-button__icon,
		.share-button .print .share-button__icon {
			color: $white;
			font-size: 1.25em;
		}
	}

	&.sticky {
		display: none;
		margin-left: -3.75em;
		position: fixed;
		top: .5em;

		.mobile-only {
			display: none;

			@include media(max-width $mobile-breakpoint-max) {
				display: block;
			}
		}
	}

	&.solid {
		// Top is set by the stickyContent plugin.
		bottom: 0;
		position: absolute;

		.mobile-only {
			display: none;

			@include media(max-width $mobile-breakpoint-max) {
				display: block;
			}
		}
	}

	.mobile-only {
		display: none;

		@include media(max-width $mobile-breakpoint-max) {
			display: block;
		}
	}

	&.minified {

		.share-button {
			@include media(max-width $mobile-breakpoint-max) {
				font-size: .75em;
				margin-right: 1.25em;
				width: auto;
			}
		}

		.share-button__count {
			@include media(max-width $mobile-breakpoint-max) {
				display: none;
			}
		}

		.email,
		.print {
			display: none;
		}

		.google-plus {
			margin: 0;
		}

		.socialbar__channels {
			position: absolute;
			right: .5em;

			@include media(max-width $mobile-breakpoint-max) {
				position: absolute;
			}
		}
	}
}

.body__print {
	.socialbar {
		display: none;
	}
}