// author-info__date

.article__field__multi {
	.teaser-stream-time {
		display: inline-block;
		margin-bottom: 0.5rem;
		font-family: $author-info_date-font;
		font-size: $author-info_date-fontSize;
		color: $author-info_date-color;
		font-weight: $author-info_date-fontWeight;
		font-style: $author-info_date-fontStyle;
		text-align: left;
		margin-left: 0;

		@media (max-width: $mobile-breakpoint-max) {
			font-size: $author-info_date-fontSize-mobile;
		}
	}
}

.author-info_date {
	color: $author-info_date-color;
	display: block;
	font-family: $author-info_date-font;
	font-size: $author-info_date-fontSize;
	font-weight: $author-info_date-fontWeight;
	font-style: $author-info_date-fontStyle;
	margin-bottom: 0.5rem;
	margin-left: 0;
	text-align: left;

	@media (max-width: $mobile-breakpoint-max) {
		font-size: $author-info_date-fontSize-mobile;
	}
}
