.article .article__body .teaser-article-summary {
	background: transparent;
	font-size: 18px;
	line-height: 1.9em;
	font-family: $font-default;
	font-weight: bold;
	margin-bottom: 1em;
	padding: 0 16px;

	@include media (max-width $mobile-breakpoint-max) {
		padding: 0;
	}

	.teaser-article-summary-headline {
		color: $color-cool-grey;
		font-size: 14px;
		line-height: 1.5;
		margin-bottom: 0;
		text-transform: uppercase;
	}

	p:not(.teaser-article-summary-headline) {
		padding-left: 2.3em;

		&:before {
			color: $color-emph;
			@include icon;
			@include icon--bulletpoint;
			display: inline-block;
			font-size: .75em;
			margin-left: -3em;
			margin-right: 20px;
			margin-top: -5px;
			width: 1.3em;
		}

		&:last-of-type {
			margin-bottom: .25em;

			@include media (max-width $mobile-breakpoint-max) {
				margin-bottom: 1.25em;
			}
		}
	}
}