// Als Custom Klasse an verschienden Widgets auf den Seiten Publication/autoren/NameDesAutors

.custom-author--detailpage {
	@include shift(1 of 16);

	&.trending-widget-wrapper {
		margin-left: 0;
		margin-top: $vertical-space-widgets;

		.content--teaser--container {
			margin: 0;

			.teaser-list {
				padding: 0 $vertical-space-widgets;
				margin-bottom: 0;

				.teaser__img--left {
					max-width: 55px;
					margin-right: $vertical-space-widgets-half;
				}

				.teaser__header {
					text-align: left;
				}
			}
		}
	}

	&.article__header__inner {
		margin-left: 0;

		.article__header__heading {
			padding-left: 0;
		}
	}

	.author-info__email {
		margin-left: $vertical-space-widgets;
	}

	.author-info__social {
		@include media(max-width $mobile-breakpoint-max) {
			margin-top: 1em;
		}
	}
}

.article {
	.article__body {
		.custom-author--detailpage {
			.teaser-article-summary {
				padding: 0;
				margin-bottom: $vertical-space-widgets;


				.teaser-article-summary-headline {
					color: $color-text-default;
					font-size: 1em;
					text-transform: none;
					letter-spacing: 0;

					&:before {
						display: none;

					}
				}
			}

		}
	}

	.custom-author--detailpage {
		.article__header__kicker {
			background: $color-emph;
			color: #fff;
			display: inline-block;
			font-size: 12px;
			margin-left: 0;
			padding: 0.5em;
			padding-bottom: 0.1em;
			padding-left: 0.75em;
			padding-right: 0.75em;
			text-transform: uppercase;
			line-height: 1.5em;
			width: auto;
		}
	}
}
