// small-print
.small-print {
	font-size: .75em;
	margin: 2em;
	text-align: center;
}

.small-print .small-print__summary {
	display: inline;
	font-weight: normal;

	&:first-child {
		font-weight: bold;
	}
}

.small-print .small-print__list {
	list-style: none;
	padding: 0;
}

.small-print .small-print__list li {
	display: inline;
}

.small-print .small-print__copy {
	font-size: 1em;
}