.kachel-teaser {
	float: left;
	display: block;

	&.teaser {
		border-bottom: 0;
		margin-bottom: 2.66743%;
		padding: 0;
		min-height: 420px;
		width: 310px;
		overflow: visible;

		@include media(max-width $mobile-breakpoint-max) {
			width: 100%;
		}

		&:nth-last-child(1),
		&:nth-last-child(2) {
			margin-bottom: 0;

			@include media(max-width $mobile-breakpoint-max) {
				margin-bottom: 25px;
				min-height: inherit;
			}
		}
	}

	&.teaser-stream {
		@include media(max-width $mobile-breakpoint-max) {
			width: 100%;
			margin-bottom: 25px;
			overflow: visible;
			margin-left: 0;
			margin-right: 0;
		}

		.headline {
			font-size: 18px;
			display: inline;
			line-height: 1.1em;
			margin-bottom: 0;
		}

		.hashtag-bar {
			font-family: $font-default;
			margin-bottom: 0;
			margin-top: 4px;

			.emph {
				&.headline_hashtag {
					font-size: 14px;
					text-transform: uppercase;
					display: inline-block;
				}
			}
		}

		.teaser-stream-content {
			width: 100%;

			@include media(max-width $mobile-breakpoint-max) {
				min-height: inherit;
				padding-bottom: 0;
			}

			.teaser-sub-block {
				position: relative;
				padding: 20px;

				@include media(max-width $mobile-breakpoint-max) {
					padding-top: 10px;
				}
			}

			.socialbar {
				border: 0;
				height: 100%;
				margin: 0;
				padding: 0;
				transition: all 0.4s cubic-bezier(0.25, 0.1, 0.25, 1);
				background: transparent;
				position: absolute;
				top: 0;
				visibility: hidden;
				width: 100%;
				z-index: 100;

				&.animate {
					background: rgba(0, 0, 0, 0.8);
					visibility: visible;

					.socialbar__channels {
						top: 50%;
						opacity: 1;
					}
				}

				.socialbar__channels {
					transition: all 0.4s cubic-bezier(0.25, 0.1, 0.25, 1);
					display: table;
					float: none;
					margin: auto;
					top: 25%;
					opacity: 0;
					width: auto;
					margin-top: -1em;
					position: relative;
				}
			}
		}
	}
	a.emph.headline_section {
		margin-bottom: 15px;
		@include media(max-width $mobile-breakpoint-max) {
			margin-bottom: 5px;
		}
	}
	.advertisement {
		padding: 0;
		line-height: 1;
		right: 20px;
		@include media(max-width $mobile-breakpoint-max) {
			top: 12px;
		}
	}
}

article.kachel-teaser:nth-child(even) {
	@include media(min-width $mobile-breakpoint-max) {
		@include shift(8 of 300);
		margin-left: 0;
		margin-right: 15px;
		max-width: 310px;
	}
}

.teaser-stream-news {
	clear: both;
}
