.trending-widget-wrapper {
	background-color: #fff;
	padding-top: 0;

	.teaser,
	.teaser-list li {
		border-bottom: 1px solid #eaeaea;

		&:last-child {
			padding: .35em 0 1.5em;
			border: 0;
		}
	}

	.teaser__img {
		&.teaser__img--left,
		&.teaser__img--right {
			width: 100%;

			& + .teaser__header,
			& ~ .teaser__body {
				width: 100%;
			}
		}
	}

	.teaser__kicker,
	.teaser__date {
		font-size: .625em;
	}

	.teaser__headline {
		color: #000;
		font-size: 1em;
		line-height: 1.11;
		padding-bottom: 10px;
	}

	.teaser__date {
		color: $color-link-hover-on-dark;
		bottom: .75em;
		left: 0;
		position: absolute;
	}

	.teaser-list__item:last-child .teaser .teaser__headline {
		padding-bottom: 30px;
	}
	.teaser.advertisement .teaser__tag {
		background: transparent;
		color: $color-cool-grey;
		font-size: 11px;
		padding-left: 0;
	}
}

.container-sidebar .trending-widget-wrapper {
	padding-left: 16px;
	padding-right: 16px;
}
