.teaser.teaser24 {
	height: 283px;
	padding: 0;
	border-bottom: 0;

	&:hover {
		.teaser-headline-block {
			.teaser__header,
			.teaser__intro {
				color: $color-emph;
			}
		}
	}

	.overlay {
		background-image: linear-gradient(180deg, rgba(20, 20, 20, 0) 0%, rgba(20, 20, 20, 0) 50%, rgba(20, 20, 20, 0.9) 100%);
		height: 100%;
		width: 100%;
		cursor: pointer;
		position: absolute;
	}

	picture,
	img {
		height: 100%;
	}

	img {
		object-fit: cover;
		object-position: center;
	}

	.teaser-headline-block {
		bottom: 0;
		left: 0;
		padding: 0 20px 15px;
		position: absolute;

		@include media(max-width $mobile-breakpoint-max) {
			width: 100%;
		}

		.teaser__icon--gallery::before,
		.teaser__icon--video::before {
			@include icon;
			font-size: 26px;
			margin-bottom: 10px;
		}

		.teaser__icon--gallery::before {
			@include icon--galery-2;
		}

		.teaser__icon--video::before {
			@include icon--video;
		}

		.teaser__header {
			color: #fff;
			font-family: $font-headline;
			font-weight: 400;
			font-size: 22px;
			line-height: 1.2;
			overflow: visible;

			&:hover {
				color: $color-emph;
			}
		}

		.teaser__intro {
			color: #fff;
			font-size: 14px;
			margin-top: 15px;
			margin-bottom: 0;

			&.hidden {
				display: none;
			}
		}

		.emph-hashtags a {
			float: left;
			margin-right: 10px;
			margin-top: 12px;

			&:hover {
				color: #fff;
			}
		}
	}
}