// Custom Classes am Widget

$back-to-home-button-space: 200;
$back-to-home-button-space-mobile: 40;
$back-to-home-button-size: 180;
$back-to-home-button-size-mobile: 70;
$back-to-home-button-inset-size: 10;
$back-to-home-button-inset-size-mobile: 6;
$back-to-home-button-inset-fontSize: 20;
$back-to-home-button-inset-fontSize-mobile: 10;

.back-to-home {
	@-webkit-keyframes redirect-animation {
		0% {
			transform: rotate(0deg);
		}
		100% {
			transform: rotate(360deg);
		}
	}
	@-moz-keyframes redirect-animation {
		0% {
			transform: rotate(0deg);
		}
		100% {
			transform: rotate(360deg);
		}
	}
	@-o-keyframes redirect-animation {
		0% {
			transform: rotate(0deg);
		}
		100% {
			transform: rotate(360deg);
		}
	}
	@keyframes redirect-animation {
		0% {
			transform: rotate(0deg);
		}
		100% {
			transform: rotate(360deg);
		}
	}

	.radial-progress {
		background-color: #d6dadc;
		bottom: #{$back-to-home-button-space}px;
		border-radius: 50%;
		box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3);
		height: #{$back-to-home-button-size}px;
		left: 50%;
		margin-left: -#{$back-to-home-button-size / 2}px;
		position: fixed;
		width: #{$back-to-home-button-size}px;
		z-index: 999;
		transition: bottom 0.2s;

		@include media(max-width $mobile-breakpoint-max) {
			bottom: #{$back-to-home-button-space-mobile}px;
			height: #{$back-to-home-button-size-mobile}px;
			width: #{$back-to-home-button-size-mobile}px;
			margin-left: -#{$back-to-home-button-size-mobile / 2}px;
		}

		&.no-fix {
			position: absolute;
			bottom: 50%;
			transition: none;
		}

		.circle {
			height: #{$back-to-home-button-size}px;
			width: #{$back-to-home-button-size}px;
			position: absolute;

			@include media(max-width $mobile-breakpoint-max) {
				height: #{$back-to-home-button-size-mobile}px;
				width: #{$back-to-home-button-size-mobile}px;
			}

			.mask,
			.fill,
			.shadow {
				position: absolute;
				border-radius: 50%;
				height: #{$back-to-home-button-size}px;
				width: #{$back-to-home-button-size}px;

				@include media(max-width $mobile-breakpoint-max) {
					height: #{$back-to-home-button-size-mobile}px;
					width: #{$back-to-home-button-size-mobile}px;
				}
			}

			.mask {
				clip: rect(0px, #{$back-to-home-button-size}px, #{$back-to-home-button-size}px, #{$back-to-home-button-size / 2}px);

				@include media(max-width $mobile-breakpoint-max) {
					clip: rect(0px, #{$back-to-home-button-size-mobile}px, #{$back-to-home-button-size-mobile}px, #{$back-to-home-button-size-mobile / 2}px);
				}

				.fill {
					clip: rect(0px, #{$back-to-home-button-size / 2}px, #{$back-to-home-button-size}px, 0px);

					@include media(max-width $mobile-breakpoint-max) {
						clip: rect(0px, #{$back-to-home-button-size-mobile / 2}px, #{$back-to-home-button-size-mobile}px, 0px);
					}
				}
			}
		}

		.inset {
			background-color: #fbfbfb;
			border-radius: 50%;
			box-shadow: 6px 6px 10px rgba(0, 0, 0, 0.2);
			width: #{$back-to-home-button-size - $back-to-home-button-inset-size}px;
			height: #{$back-to-home-button-size - $back-to-home-button-inset-size}px;
			line-height: #{$back-to-home-button-size - $back-to-home-button-inset-size}px;
			margin-left: #{$back-to-home-button-inset-size / 2}px;
			margin-top: #{$back-to-home-button-inset-size / 2}px;
			position: absolute;
			text-align: center;

			@include media(max-width $mobile-breakpoint-max) {
				width: #{$back-to-home-button-size-mobile - $back-to-home-button-inset-size-mobile}px;
				height: #{$back-to-home-button-size-mobile - $back-to-home-button-inset-size-mobile}px;
				line-height: #{$back-to-home-button-size-mobile - $back-to-home-button-inset-size-mobile}px;
				margin-left: #{$back-to-home-button-inset-size-mobile / 2}px;
				margin-top: #{$back-to-home-button-inset-size-mobile / 2}px;
			}

			.inset-text {
				display: inline-block;
				font-weight: bold;
				font-size: #{$back-to-home-button-inset-fontSize}px;
				line-height: normal;
				padding: 0 0.5em;
				vertical-align: middle;
				margin-top: -1em;

				@include media(max-width $mobile-breakpoint-max) {
					font-size: #{$back-to-home-button-inset-fontSize-mobile}px;
				}

				&:after {
					@extend .icon;
					@extend .icon--plus:before;
					bottom: 1.5rem;
					font-size: 1.5em;
					transform: rotate(90deg);
					position: absolute;
					left: 0;
					width: 100%;

					@include media(max-width $mobile-breakpoint-max) {
						bottom: 0.75rem;
						font-size: 3em;
					}
				}
			}
		}
	}

	&.back-to-home_homepage {
		.radial-progress {
			.inset {
				.inset-text {
					&:after {
						@extend .icon--immobilien:before;
						transform: rotate(0deg);
					}
				}
			}
		}
	}

	&.back-to-home_redirect {
		.circle {
			animation: redirect-animation 1.25s linear; /* IE 10+, Fx 29+ */
			animation-iteration-count: infinite;
			box-shadow: none;
			.mask {
				&.full {
					.fill {
						transform: rotate(180deg);
					}
				}

				&.half {
					transform: rotate(180deg);

					.fill {
						transform: rotate(180deg);
					}
				}
			}
		}

		.radial-progress {
			.inset {
				.inset-text {
					&:after {
						@extend .icon--richtig:before;
						font-weight: normal;
						transform: rotate(0);
					}
				}
			}
		}
	}

	&.back-to-home_disableRedirect {
		.circle {
			.mask.full {
				.fill {
					transform: rotate(180deg);
					background: $color-emph;
				}
			}

			.mask.half {
				transform: rotate(180deg);

				.fill {
					background: $color-emph;
					transform: rotate(180deg);
				}
			}
		}
	}

	&.back-to-home_hidden {
		.radial-progress {
			bottom: -#{$back-to-home-button-size}px;

			@include media(max-width $mobile-breakpoint-max) {
				bottom: -#{$back-to-home-button-size-mobile}px;
			}
		}
	}

	// ---- Themes ----

	// Light
	&.light {
		.radial-progress {
			.circle {
				.mask {
					.fill {
						background-color: $color-emph;
					}
				}
			}

			.inset {
				.inset-text {
					color: $color-emph;

					@include media(max-width $mobile-breakpoint-max) {
						color: transparent;

						&:after {
							color: $color-emph;
							position: relative;
						}
					}
				}
			}
		}

		&.back-to-home_redirect {
			.mask {
				&.full {
					.fill {
						background: linear-gradient(to bottom, $color-emph 0%, #fff 50%, $color-emph 100%) $color-emph;
					}
				}

				&.half {
					.fill {
						background: linear-gradient(to bottom, $color-emph 0%, #fff 50%, $color-emph 100%) $color-emph;
					}
				}
			}
		}

		&.back-to-home_disableRedirect {
			.circle {
				.mask {
					&.full {
						.fill {
							background: $color-emph;
						}
					}

					&.half {
						.fill {
							background: $color-emph;
						}
					}
				}
			}
		}
	}

	// Dark
	&.dark {
		.radial-progress {
			background: #fff;

			.mask {
				.fill {
					background-color: $color-emph;
				}
			}

			.inset {
				background: $color-emph;
				border: 2px solid #fff;

				.inset-text {
					color: #fff;
				}
			}
		}

		&.back-to-home_redirect {
			.mask {
				&.full {
					.fill {
						background: linear-gradient(to bottom, $color-emph 0%, #fff 50%, $color-emph 100%) $color-emph;
					}
				}

				&.half {
					.fill {
						background: linear-gradient(to bottom, $color-emph 0%, #fff 50%, $color-emph 100%) $color-emph;
					}
				}
			}
		}

		&.back-to-home_disableRedirect {
			.mask {
				&.full {
					.fill {
						background: $color-emph;
					}
				}

				&.half {
					.fill {
						background: $color-emph;
					}
				}
			}
		}
	}

	.no-animation {
		transition: none;
	}
}

.back-to-home_overlay {
	position: relative;
	z-index: 1;

	a {
		pointer-events: none;
		cursor: default;
	}

	.content-wide__column {
		&::before {
			content: " ";
			position: absolute;
			height: 100%;
			width: 100%;
			display: block;
			z-index: 20;
		}
	}
}
footer .header {
	z-index: 0;
}