.teaser-stream-gallery .hero-media__caption {
	padding: 0 16px;
	font-family: "DIN Next Rounded LT", Arial, Helvetica, Verdana, sans-serif;
	font-weight: normal;
	font-style: normal;
}

.swiper-container {

	.hero-media__caption {
		border-left: 1px solid #939599;
		font-size: 16px;
		padding: .25em 1.25em .5em;
		position: relative;
		margin: 1em 0 0 59px;
		width: calc(100% - 59px);
		margin-top: 0;
		&:before {
			@include icon;
			@include icon--camera;
			@include center(v, a);
			color: $color-emph;
			font-size: 1.5em;
			left: -1.85em;

			@include media (max-width $mobile-breakpoint-max) {
				display: none;
			}
		}
	}

	.hero-media__caption-text,
	.hero-media__caption-credits {
		color: #999;
	}

	.teaser-media-img__credits,
	.hero-img__credits,
	.hero-media__caption-credits,
	.inline-media__caption-credits {
		text-align: left;
	}

	.teaser-media-img__text,
	.hero-img__text,
	.hero-media__caption-text,
	.inline-media__caption-text,
	.teaser-media-img__credits:last-child,
	.hero-img__credits:last-child,
	.hero-media__caption-credits:last-child,
	.inline-media__caption-credits:last-child {
		font-size: 16px;
		line-height: 1.2;
		margin: 0;
	}

}

.teaser-stream .swiper-container {
	padding-bottom: 8px;
}

.teaser-stream .swiper-pagecount {
	color: #999;
	font-family: "DIN Next Rounded LT", Arial, Helvetica, Verdana, sans-serif;
	font-weight: bold;
	font-style: normal;
	padding-bottom: 8px;
	padding-top: 8px;
	border-top: 1px solid rgba(255, 255, 255, 0.1);
	display: none;
}

.swiper-button-prev,
.swiper-button-next {
	background: #454345;
	border-radius: 5px;
	width: 35px;
	height: 35px;
	&:before {
		color: #fff;
		font-size: 14px;
		text-align: center;
		width: 100%;
		padding-top: 0.7em;
	}
}

.swiper-button-prev {
	&:before {
		@include icon--arrow-2-left;
	}
}

.swiper-button-next {
	&:before {
		@include icon--arrow-2-right;
	}
}