.teaser-stream {
	padding: 20px;
	.headline {
		font-weight: 500;
		font-size: 24px;
	}
	a.emph.headline_section {
		font-size: 11px;
		display: block;
		text-transform: uppercase;
		font-weight: 500;
		margin-bottom: 4px;
		.updated {
			color: $color-cool-grey;
			&:before {
				content: "";
				color: $orange;
				font-size: 2em;
				position: relative;
				top: 2px;
				margin-right: 2px;
				margin-left: 5px;
			}
		}
	}
	.teaser-stream-time {
		display: none;
	}

	.hashtag-bar {
		font-size: 14px;
	}

	.advertisement {
		background: #fff;
		color: $color-text-headline-default;
		position: absolute;
		font-size: 11px;
		text-transform: none;
		z-index: 100;
		padding: 5px 5px 3px;
		top: 21px;
		right: 15px;
	}

	.teaser-stream-content {
		@include span-columns(12);
		margin: 0;
	}
}

.teaser-stream-gallery,
.teaser-stream-video {
	background: #fff;
	.headline {
		.teaser__link:hover {
			color: inherit;
		}
	}
}

.teaser-stream-gallery .headline .teaser__link,
.teaser-stream-video .headline .teaser__link {
	color: $color-text-default;
}

@media screen and (max-width: 767px) {
	.flex-box.inline-block--wide {
		padding-left: 10px;
		padding-right: 10px;
	}
}