.player-fixed {
	max-width: 960px;
}

.article--type-news {
	.video-opener {
		.player-fixed--close {
			display: inherit;
			width: inherit;
		}
	}
}

.article {
	.article__body {
		.video-placeholder {
			width: calc(100% - #{$vertical-space-widgets});
		}
	}
	.video-inline,
	.hero-img {
		&.player-fixed--mobile {
			padding: 0;
			top: 8px;

			.hero-media__caption,
			.inline-media__caption {
				padding: 1rem;
				padding-right: 2rem;
				padding-left: 1rem;

				@include media(max-width $mobile-breakpoint-mini) {
					padding-left: 0.5rem;
					padding-right: 1.5rem;
				}
			}
			.hero-img__caption,
			.hero-media__caption {
				background: transparent;
			}

			.inline-media {
				.inline-media__caption {
					background: transparent;
				}
			}
		}
	}
}

