// Longform Article Zitat
.theme-longform {
	.article--type-longform {
		.article__field__multi-quote {
			width: 33%;
			margin: auto;
			margin-bottom: 1em;

			@media screen and (max-width: $longform-breakpoint-mobile) {
				float: none;
				margin: $element-space-large auto;
				width: 100%;
				padding: 0 $element-space-mobile;
			}

			&.article__field__multi-quote_left {
				float: left;
				margin-right: 1.5em;
				margin-left: 4%;
				width: 25%;

				@media screen and (max-width: $longform-breakpoint-mobile) {
					float: none;
					margin: $element-space-large auto;
					width: 100%;
					padding: 0 $element-space-mobile;
				}

			}

			&.article__field__multi-quote_right {
				float: right;
				margin-left: 1.5em;
				margin-right: 4%;
				width: 25%;

				@media screen and (max-width: $longform-breakpoint-mobile) {
					float: none;
					margin: $element-space-large auto;
					width: 100%;
				}
			}
		}

		.article__body {
			.article__field__multi-quote {
				padding: 0;

				@media screen and (max-width: $longform-breakpoint-mobile) {
					padding: 0 $element-space-mobile;
				}

				.teaser-article-summary {
					margin-bottom: 1em;
					padding: 2rem 0;

					@media screen and (max-width: $longform-breakpoint-mobile) {
						padding: 1rem 0;
						margin-bottom: 0.5em;
					}
				}

				.article__header__intro,
				.teaser-article-summary {
					border-top: 1px solid $color-emph;
					border-bottom: 1px solid $color-emph;

					&:before {
						display: none;
					}

					p.article__header__intro__text,
					p.teaser-article-summary-headline {
						font-weight: normal;
						font-size: 30px;
						line-height: 40px;
						width: 100%;
						margin-bottom: 0;
						font-style: italic;
						color: #666;

						@media screen and (max-width: $longform-breakpoint-mobile) {
							font-size: 18px;
							line-height: 26px;
							padding: 0;
						}
					}
				}

				.article__header__originator {
					font-size: 12px;
				}
			}
		}
	}
}