.teaser-stream-content {
	li.swiper-slide {
		min-height: 370px;

		@media (max-width: $mobile-breakpoint-max) {
			min-height: 150px;
		}
	}

	.swiper-pagecount {
		@media (max-width: $mobile-breakpoint-max) {
			padding: 0.5em;
		}
	}
}

.teaser-stream-gallery {
	@media (max-width: $mobile-breakpoint-max) {
		.teaser-stream-share-container,
		.teaser-stream-share-container.social-open {
			margin-top: 0;
		}
	}
}

.gallerie-teaser {
	min-height: 370px;

	@media (max-width: $mobile-breakpoint-max) {
		min-height: 150px;
	}

	.teaser__img--articleportrait img {
		max-height: 370px;
		width: auto;
		object-fit: contain;

		@media (max-width: $mobile-breakpoint-max) {
			max-height: 310px;
			width: inherit;
		}
	}

	picture {
		min-height: 370px;

		@media (max-width: $mobile-breakpoint-max) {
			min-height: 100px;
		}
	}
}

