.container-leader-opener.with-sidebar {

	.teaser-opener-subblock {
		.headline {
			&.headline-l {
				margin-bottom: 14px;
				font-weight: 500;
			}
		}
	}

	.teaser-image-block {
		width: 100%;
	}

	@include media(max-width $mobile-breakpoint-max) {
		padding-bottom: 0;
	}

	.teaser-leader-l {
		@include media(max-width $mobile-breakpoint-max) {
			padding-bottom: 73px;
			margin-left: 0;
			margin-right: 0;
		}
	}
}

.container-stream {

	.container-leader-opener {
		@extend .container-leader-opener.with-sidebar;
	}

	.header--category__title {
		font-size: 36px;
		text-transform: uppercase;
		line-height: 1.1;
		padding: 1.3em .5em .4em;
		font-weight: 500;
		@include media(max-width $mobile-breakpoint-max) {
			font-size: 24px;
			line-height: 1.25;
		}
	}
}