/*
 * @name: search-bar
 *
 * @description: search module for Navigation
 *
 */

.header .search-bar {
	font-family: $font-default;
	width: 100%;
	background: #f4f4f4;

	@include media(max-width $mobile-breakpoint-max) {
		height: 100%;
		position: fixed;
		background: rgba(0, 0, 0, .8);
		padding-top: 80px;
		top: 86px;
	}

	.toggle-search {
		display: none;
	}

	.errorSearchMessage {
		color: #d23c1e;
		left: 197px;
		top: -14px;
		font-size: 10px;
		text-transform: uppercase;
		@include media(max-width $mobile-breakpoint-max) {
			font-weight: bold;
			font-size: 12px;
			@include center(h,a);
			text-align: center;
			top: 35px;
		}
	}

	.search-bar-input-wrapper {
		max-width: 620px;
		@include center(h);
		padding-top: 0;
		.form-fields .form-fields-search {
			font-family: $font-default;
			font-weight: 500;
			font-size: 24px;
			padding: .25em .5em;
			background: transparent;
			border-bottom: 2px solid #d8d8d8;
			&::-webkit-input-placeholder {
				color: $color-text-headline-default;
			}
			&:-moz-placeholder {
				color: $color-text-headline-default;
			}
			&::-moz-placeholder {
				color: $color-text-headline-default;
			}
			&:-ms-input-placeholder {
				color: $color-text-headline-default;
			}
		}
		@include media(max-width $mobile-breakpoint-max) {
			width: 100%;
			.form-fields {
				width: 100%;
				.form-fields-search {
					color: $white;
					text-align: center;
					padding-bottom: 16px;
					margin-bottom: 20px;
					&::-webkit-input-placeholder {
						color: $white;
					}
					&:-moz-placeholder {
						color: $white;
					}
					&::-moz-placeholder {
						color: $white;
					}
					&:-ms-input-placeholder {
						color: $white;
					}
				}
			}
		}
	}

	.search-bar-input-wrapper .form-actions {
		padding-top: 12px;
		padding-bottom: 10px;
		background: $color-text-headline-default;
		font-size: 14px;
		font-weight: bold;
		border-radius: 4px;
		margin-left: 20px;
		@include media(max-width $mobile-breakpoint-max) {
			width: 100%;
			margin-left: 0;
			background: $white;
			color: $color-text-headline-default;
		}
	}
}