.main .reaction-teaser {
	height: auto;
	display: inline-block;
	padding-left: 10px;

	.block-header {
		color: #222;
		font-size: 19px;
		padding-bottom: 8px;
		margin: 0;
	}

	ul {
		list-style-type: none;
		padding: 0;
		margin-top: 0;
		display: inline-block;

		@include media(max-width $mobile-breakpoint-max) {
			margin: 0 auto;
			max-width: 300px;
			position: relative;
		}

		li {
			@include span-columns(6 of 12);
			margin-right: 0;
			display: inline-block;
			border-top: 1px solid #d6d6d6;
			padding-top: 0.75em;
			padding-bottom: 0.75em;

			@include media(max-width $mobile-breakpoint-max) {
				@include span-columns(12 of 12);
				margin-right: 0;
			}

			&:first-child {
				border-top: 0;
			}

			a {
				font-family: Arial;
				color: #222;
				display: block;
				font-weight: normal;
				line-height: 1.3;
				font-size: 16px;
			}
		}
	}

	span.container {
		display: inline-grid;
		text-align: center;
		float: left;
		margin-right: .5em;
		height: 61px;
	}

	span.counter {
		margin-left: -12px;
		font-family: Arial;
		font-size: 14px;
	}

	span.like {
		background-image: url("../img/recommendations/like.jpg");
		background-repeat: no-repeat;
		margin-right: 10px;
		background-size: 40px 40px;
		width: 40px;
		height: 40px;

	}

	span.love {
		background-image: url("../img/recommendations/love.jpg");
		background-repeat: no-repeat;
		margin-right: 10px;
		background-size: 40px 40px;
		width: 40px;
		height: 40px;

	}
	span.sad {
		background-image: url("../img/recommendations/sad.jpg");
		background-repeat: no-repeat;
		margin-right: 10px;
		background-size: 40px 40px;
		width: 40px;
		height: 40px;

	}
	span.haha {
		background-image: url("../img/recommendations/haha.jpg");
		background-repeat: no-repeat;
		margin-right: 10px;
		background-size: 40px 40px;
		width: 40px;
		height: 40px;

	}
	span.wow {
		background-image: url("../img/recommendations/wow.jpg");
		background-repeat: no-repeat;
		margin-right: 10px;
		background-size: 40px 40px;
		width: 40px;
		height: 40px;

	}
	span.angry {
		background-image: url("../img/recommendations/angry.jpg");
		background-repeat: no-repeat;
		margin-right: 10px;
		background-size: 40px 40px;
		width: 40px;
		height: 40px;
	}
}

.sidebar {
	.reaction-teaser {
		ul {
			li {
				@include span-columns(12 of 12);
			}
		}
	}
}