.poll--background {
	background-color: $lightest-grey;
	position: relative;
	margin-bottom: $vertical-space-widgets;

	.poll__head {
		position: relative;
	}

	.poll__form {
		fieldset {
			border: none;
			margin: 0;
			padding: 0;
		}
	}

	.poll__submit {
		padding: 1em 0;
	}

	.poll__content {
		padding: 1.5em;

		@include media(max-width $mobile-breakpoint-max) {
			padding: 0 0 1.5em;
		}
	}

	.voting__row--submit {
		margin-bottom: 0;
	}

	.poll__text {
		color: $dark-grey;
		font-family: $font-default;
		text-align: center;
		margin: 0;
		padding: 0.5em 1em;

		p {
			margin-bottom: 0;
		}
	}

	.poll__answer__label {
		color: $dark-grey;
		margin: 0;
		text-align: left;
	}

	.poll__answer__share {
		color: $dark-grey;
	}

	.poll__background {
		display: block;
		position: relative;
		text-align: center;

		&:before {
			background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 100));
			bottom: 0;
			content: "";
			height: 70%;
			left: 0;
			position: absolute;
			width: 100%;
		}
	}

	img {
		width: 100%;
		height: auto;
	}

	.poll__button {
		background-color: $dark-grey;
		border: .07142857143em solid $lighter-grey;
		color: $dark-grey;

		&:hover {
			background-color: #000;
			color: $dark-grey;
		}
	}

	.voting__form .voting__row__label {
		color: $white;
	}

	.voting__button-wrapper {
		border-top: .0625em solid #000;
		clear: both;
		padding-top: 1em;
		text-align: center;
	}
}

