.article {
	.inline-carousel {
		.teaser__img--articleportrait {
			img {
				float: inherit;
				max-height: 405px;
				width: auto;
			}
		}

		.inline-media__caption-text {
			margin-top: 0;
			line-height: 1.5em;
		}

		.inline-media__caption {
			margin: 0;
		}
	}

	.inline-carousel.carousel-ajax.swiper-container {
		.inline-media__caption {
			font-size: 18px;
		}

		.inline-media__caption-text {
			@media (max-width: $mobile-breakpoint-max) {
				font-size: 0.75em;
			}
		}
	}
}

.flex-box.inline-block--wide {
	padding: 0 $vertical-space-widgets;
	width: 100%;

	@media (max-width: $mobile-breakpoint-max) {
		padding: 0;
	}

	.carousel-ajax {
		@media (max-width: $mobile-breakpoint-max) {
			max-width: inherit;
			padding-right: 0;
		}
	}
}
