.socialbar {
	.socialbar__channels {
		&.socialbar__channels--square {
			.share-button {
				.share-button__count {
					left: 0;
					top: 1px;
				}
			}
		}
	}

	&.socialbar--ishidden {
		.socialbar__channels {
			position: relative;
			display: none;

			&.socialbar__channels--round {
				.share-button {
					&.print {
						@include media(max-width $mobile-breakpoint-max) {
							display: none;
						}
					}
				}
			}
		}

		&.socialbar--ishidden--left,
		&.socialbar--ishidden--right {
			position: relative;

			.socialbar__channels {
				&.socialbar__channels--square {
					.share-button {
						.share-button__count {
							left: 1px;
							top: 0;
							padding: 0.6rem 0;

							@include media(max-width $mobile-breakpoint-max) {
								left: 0;
								top: 1px;
							}
						}
					}
				}
			}
		}

		&.socialbar--ishidden--right {
			.socialbar__channels {
				float: right;
				margin-right: 0.5rem;
			}

			.socialbar__share--button {
				float: right;
			}
		}

		&.socialbar--ishidden--left {
			.socialbar__channels {
				float: left;
				margin-left: 0.5rem;
			}

			.socialbar__share--button {
				float: left;
			}
		}

		&.socialbar--ishidden--bottom {
			&.sticky {
				.socialbar__channels {
					top: 50px;
				}
			}

			.socialbar__share--button {
				width: 40px;

				span {
					width: 40px;
				}
			}

			.socialbar__channels {
				margin-top: 0.5rem;
				position: absolute !important;

				@include media(max-width $mobile-breakpoint-max) {
					margin: 1em auto 0;
					position: relative !important;
				}

				&.socialbar__channels--square {
					.share-button {
						.share-button__count {
							left: 0;
							top: 1px;
						}
					}
				}

				&.socialbar__channels--square,
				&.socialbar__channels--square_without_count {
					.share-button__icon,
					.share-button__count {
						width: 40px;
					}
				}
			}
		}

		.socialbar__share {
			display: none;
		}

		.socialbar__share--button {
			color: $color-emph;
			transition: all 0.5s;
			cursor: pointer;
			display: inline-block;
			text-align: center;

			span {
				float: left;
				font-family: Arial;
				font-size: 12px;
			}

			&:before {
				@include icon;
				@include icon--stream-share;
				display: block;
				font-size: 1.5em;
				margin-bottom: 5px;

				@include media(max-width $mobile-breakpoint-max) {
					text-align: center;
					width: 100%;
				}
			}
		}

		.socialbar__share[type=checkbox] {
			&:checked {
				& ~ .socialbar__channels {
					display: table;
				}

				& ~ .socialbar__share--button {
					color: #333;
				}
			}
		}
	}

	.socialbar__channels--square_without_count {
		.share-button__count {
			display: none;
		}

		.share-button {
			margin-bottom: 1em;
		}
	}

	&.sticky {
		.socialbar__channels--square_without_count {
			.share-button__count {
				display: none;
			}

			.share-button {
				margin-bottom: 1em;
			}
		}
	}
}

.socialbar__channels {
	&.socialbar__channels--round {
		.share-button__icon {
			border-radius: 50%;
			height: 2.5em;
			line-height: 1.5em;
			padding: 0.5em;
			width: 2.5em;
		}

		.share-button__count {
			display: none !important;
		}

		.share-button {
			display: table;
			margin-bottom: 0.5em;
		}

		.share-button__icon {
			display: table-cell;
			padding: 0.5em;
			text-align: center;
			vertical-align: middle;
		}
	}
}

.article--type-news .socialbar {
	&.socialbar--ishidden {
		width: auto;
	}
}