.hero-carousel.swiper-container {
	margin-bottom: $vertical-space-widgets;
}

.hero-carousel {
	.hero__interrupt {
		height: 615px;
	}
}

.article {
	.hero-carousel {
		.hero-media__caption-text {
			margin-top: 0;
		}

		.hero-media__caption-credits {
			text-align: right;

			@media (max-width: $mobile-breakpoint-max) {
				text-align: left;
			}
		}
	}
}

.article__header {
	.flex-box.inline-block--wide {
		padding-right: 0;
		padding-left: 0;
	}
}
