.teaser-leader-single-topic {
	@include span-columns(12);
	@include omega;
	background-color: $color-bg-black;
	height: 320px;
	margin-bottom: 16px;
	overflow: hidden;
	padding: 16px;
	position: relative;

	.teaser-leader-img-container {
		background-position: center center;
		background-repeat: no-repeat;
		background-size: cover;
		bottom: 0;
		left: 0;
		margin: 8px;
		position: absolute;
		right: 0;
		top: 0;
		z-index: 2;

		p {
			color: $color-text-ondark-light;
		}
	}

	.teaser-leader-text-container {
		bottom: 0;
		left: 0;
		padding: 16px;
		position: absolute;
		right: 0;
		top: 0;
		z-index: 6;

		p {
			color: $color-text-ondark-light;
			margin: 32px 0 16px;
		}
	}

	.overlay {
		background-color: rgba(0, 0, 0, .65);
		bottom: 0;
		left: 0;
		position: absolute;
		right: 0;
		top: 0;
		z-index: 4;
	}
}
