.article__header__heading  .ispayedcontent {
	background-size: contain;
	display: inline-block;
	height: 24px;
	width: 80px;
	position: relative;
	margin-left: 10px;

	@include media(max-width $mobile-breakpoint-max) {
		height: 18px;
		width: 60px;
		margin-left: 6px;
	}
}

.teaser--hero.teaser--background
.ispayedcontent.teaserimage.desktop {
	height: 24px;
	width: 86px;
	display: inline-block;
	left: 24px;
	margin-top: 24px;
	position: absolute;
	z-index: 1;
	@include media(max-width $mobile-breakpoint-max) {
		display: none;
	}
}

.teaser--hero.teaser--background
.ispayedcontent.teaserimage.mobile {
	display: none;
	@include media(max-width $mobile-breakpoint-max) {
		height: 18px;
		width: 60px;
		display: inline-block;
		position: absolute;
		left: 16px;
		margin-top: 16px;
		z-index: 1;
	}
}

.teaser--medium.teaser--background .ispayedcontent.teaserimage.mobile {
	display: none;
	@include media(max-width $mobile-breakpoint-max) {
		display: inline-block;
		height: 18px;
		width: 60px;
		position: absolute;
		left: 10px;
		top: 10px;
		z-index: 100000;
	}
}

.teaser--medium
.ispayedcontent.teaserimage.desktop {
	height: 18px;
	width: 60px;
	display: inline-block;
	left: 14px;
	margin-top: 14px;
	position: absolute;
	z-index: 1;
	@include media(max-width $mobile-breakpoint-max) {
		display: none;
	}
}

span.teaser__kicker.teaser__kicker_paidcontent {
	display: block;
}

span.teaser__headline.teaser_headline_paidcontent {
	margin-right: 10px;
	display: inline;
}

.ispayedcontent.headline.desktop {
	height: 18px;
	width: 60px;
	display: inline-block;
	//margin-left: 10px;

	@include media(max-width $mobile-breakpoint-max) {
		display: none;
	}
}

.ispayedcontent.headline.mobile {
	display: none;

	@include media(max-width $mobile-breakpoint-max) {
		height: 14px;
		width: 46px;
		display: inline-block;
		//margin-left: 6px;
	}
}

.teaser--medium .ispayedcontent.teaserimage.mobile {
	display: none;
	@include media(max-width $mobile-breakpoint-max) {
		display: inline-block;
		height: 14px;
		width: 42px;
		position: absolute;
		left: 4px;
		top: 4px;
		z-index: 100000;
	}
}

.teaser--hero.teaser--background .ispayedcontent.teaserimage.mobile {
	display: none;
	@include media(max-width $mobile-breakpoint-max) {
		display: inline-block;
		left: 16px;
	}
}