.container-leader.grosse-nachrichtenlage {
	max-width: 940px;
	margin-bottom: 20px;
	background-color: #333;

	@include media(max-width $mobile-breakpoint-max) {
		max-width: 100%;
		background-color: #fff;
	}

	.overlay {
		background-image: linear-gradient(180deg, transparent 45%, rgba(0, 0, 0, 0.2) 55%, rgba(51, 51, 51, 1) 100%);
	}

	&.count-7 {
		.teaser-leader {
			&.teaser-leader-s {
				&:nth-child(4) {
					margin-right: 0;
				}
				&:nth-child(5) {
					padding-top: 20px;
				}

				&:nth-child(6) {
					padding-top: 20px;
				}

				&:nth-child(7) {
					padding-top: 20px;
				}

				@include media(max-width $mobile-breakpoint-max) {
					&:nth-child(5) {
						padding-top: 0px;
					}

					&:nth-child(6) {
						padding-top: 0px;
					}

					&:nth-child(7) {
						padding-top: 0px;
					}
				}
			}
		}
	}

	a {
		color: #fff;

		&.emph.headline_section {
			font-size: 14px;
			line-height: 1.67em;
			display: none;
		}
	}

	.teaser__location {
		color: #ccc;
		font-size: 12px;
		text-transform: uppercase;
		padding-right: .5em;
	}

	.headline.headline-s a.emph {
		display: none;
	}

	.teaser-leader {

		&.teaser-leader-w {
			@include span-columns(12 of 12);
			margin: 20px 0;
			margin-top: 0;
			font-size: 36px;
			line-height: 1.33em;
			height: 530px;
			padding: 0;

			@include media(max-width $mobile-breakpoint-max) {
				height: 56vw;
				font-size: 24px;
				background-color: #333;
				margin: 0;
			}

			.headline {
				@include span-columns(9 of 12);
				bottom: 4.5em;
				padding: 0 30px;
				margin: 0;

				@include media(max-width $mobile-breakpoint-max) {
					bottom: 2.5em;
					padding: 0 10px;
				}
			}

			.teaser__link__text {
				display: inline;
				position: absolute;
				bottom: 5em;
				left: 30px;
				font-size: 18px;
				line-height: 1.33em;
				padding-right: 30px;
				font-family: $font-default;

				@include media(max-width $mobile-breakpoint-max) {
					display: none;
				}
			}

			.teaser__link__more {
				position: absolute;
				bottom: 2em;
				left: 30px;
				font-size: 14px;
				line-height: 2em;
				border: solid 2px #666;
				background-color: #333;
				border-radius: .25em;
				text-transform: uppercase;
				text-align: center;
				padding: 0 1em;

				@include media(max-width $mobile-breakpoint-max) {
					left: 10px;
				}
			}

			.teaser-image-block img {
				@include media(max-width $mobile-breakpoint-max) {
					width: 100%;
					margin: 0;
				}
			}
		}

		&.teaser-leader-m {
			@include span-columns(6 of 12);
			margin-right: 20px;
			height: 345px;
			padding: 0;
			font-size: 26px;
			line-height: 1.33em;

			@include media(max-width $mobile-breakpoint-max) {
				height: 100px;
				background-color: #333;
				margin: 1px 0;
				width: 100%;
			}

			.headline {
				bottom: 0.5em;
				padding: 0 20px;
				margin: 0;

				@include media(max-width $mobile-breakpoint-max) {
					bottom: 0;
					top: 10px;
					padding-left: 110px;
					font-size: 16px;
					line-height: 1.25em;
				}
			}

			.overlay {
				@include media(max-width $mobile-breakpoint-max) {
					display: none;
				}
			}

			.teaser-image-block img {
				@include media(max-width $mobile-breakpoint-max) {
					width: 100px;
					margin: 0;
				}
			}
		}

		&.teaser-leader-s {
			@include span-columns(2 of 6);
			height: 225px;
			padding: 0;
			margin-bottom: 0px;
			font-size: 24px;
			line-height: 1.33em;

			@include media(max-width $mobile-breakpoint-max) {
				height: 100px;
				background-color: #333;
				margin: 1px 0;
				width: 100%;
			}

			.headline {
				bottom: 1em;
				padding: 0 20px;
				margin: 0;
				font-size: 24px;
				line-height: 1.33;

				@include media(max-width $mobile-breakpoint-max) {
					bottom: 0;
					top: 10px;
					padding-left: 110px;
					font-size: 16px;
					line-height: 1.3em;
				}
			}

			.overlay {
				background-image: linear-gradient(180deg, transparent 45%, rgba(0, 0, 0, 0.5) 55%, rgba(51, 51, 51, 1) 100%);
				@include media(max-width $mobile-breakpoint-max) {
					display: none;
				}
			}

			.teaser-image-block img {
				@include media(max-width $mobile-breakpoint-max) {
					width: 100px;
					margin: 0;
				}
			}
		}
	}
}

