.breadcrumb {
	background: #fff;
	font-size: $font-size-extras-tiny;
	font-weight: bold;
	padding: $vertical-space-widgets;

	@include media(max-width $mobile-breakpoint-max) {
		padding: 10px;
		padding-bottom: 0;
		position: relative;
		text-align: center;
	}

	h1 {
		color: $color-text-default;
		display: inline-block;
		font-size: inherit;
	}

	.breadcrumb__link {
		color: $color-emph;
	}

	.breadcrumb__item {
		line-height: 1.5em;
	}

	.breadcrumb__item {
		@include media(max-width $mobile-breakpoint-max) {
			display: none;
		}
	}

	.breadcrumb__item--last {
		@include media(max-width $mobile-breakpoint-max) {
			color: $color-emph;
			display: inline-block;
			padding: 0 $vertical-space-imageblock;
			position: relative;
		}
	}
}