/* FDC-313 Used in fuzo/config -> Top -> [FUZO] Men Off-Canvas */

.offcanvas-toggle {
	display: none;
}

.offcanvas-active {
	overflow: hidden;
}

.page-wrapper .page.theme-default {
	@include media(max-width $mobile-breakpoint-max) {
		header,
		main,
		footer {
			will-change: auto;
			transition: transform .5s ease-in-out;
		}
	}
}

.custom__nav-bar--offcanvas {
	display: none;

	&.nav-bar-mobile {
		position: fixed;
		overflow: inherit;
		height: 100%;
		width: 210px;
		z-index: 10001;
		left: 0;
		top: 0;
		transition: .5s transform ease-in-out;
		transform: translate3d(-210px, 0, 0);

		.nav-main {
			background: #eaeaea;
			box-shadow: inset -7px 0px 6px -6px rgba(0, 0, 0, .25);
			overflow: inherit;
			border: none;
		}

		.nav-main,
		.nav-main ul {
			height: 100%;
		}

		.nav-main ul {
			padding-left: 0;
			box-shadow: none;
		}

		.nav-main ul li {
			display: block;
			float: none;
			width: 100%;
			position: relative;

			&:hover {
				background: transparent;
			}

			&:first-child a {
				border-color: $color-text-headline-default;
			}

			& + li {
				margin: 10px 0 0;
				@include media(max-width $mobile-breakpoint-mini) {
					margin-top: 5px;
				}
			}

			a {
				padding-left: 59px;
				border-width: 0;
				line-height: normal;
				text-align: left;
				display: inline;
				padding-right: 9px;

				&:before,
				&:after {
					position: absolute;
					content: "";
					bottom: 13px;
					left: 0;
					height: 1px;
					border-bottom: 5px solid;
				}

				&:before {
					width: 50px;
					border-color: inherit;
					display: block;
				}

				&:after {
					z-index: -1;
					border-color: inherit;
					right: 0;
					width: 0;
				}

				&.nav-main__anchor--active {
					border-width: 0;
					background: #eaeaea;

					&:before {
						display: none;
					}

					&:after {
						width: 100%;
					}
				}
			}
		}
		@include media(max-width $mobile-breakpoint-max) {
			display: block;
		}
	}
}

.offcanvas-toggle-label {
	@include span-columns(3);
	display: none;
	cursor: pointer;
	transition: .5s transform ease-in-out;
	line-height: 1;
	position: relative;
	height: 40px;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	&.hidden {
		transform: translateY(-75px);
	}
	.offcanvas-toggle-icon,
	.offcanvas-toggle-icon:before,
	.offcanvas-toggle-icon:after {
		background: #242424;
		height: 2px;
		cursor: pointer;
		display: block;
		width: 20px;
	}
	.offcanvas-toggle-icon {
		@include center(v, a);
		left: 12px;
		transition: .5s background ease-in-out;
		&:before,
		&:after {
			content: "";
			position: absolute;
			transition: .5s transform ease-in-out;
		}
		&:before {
			transform-origin: 100% 0;
			transform: translateY(-6px);
		}
		&:after {
			transform-origin: 0 100%;
			transform: translateY(6px);
		}
	}
	@include media(max-width $mobile-breakpoint-max) {
		display: block;
	}
}

.offcanvas-overlay {
	position: fixed;
	top: 0;
	bottom: 0;
	right: 0;
	width: 0;
	height: 0;
	background: transparent;
	z-index: 10000;
	-webkit-tap-highlight-color: transparent;
}

.offcanvas-toggle ~ .page-wrapper .page.theme-default {
	transition: .5s transform ease-in-out;
	transform-origin: 0 0;

	@include media(max-width $mobile-breakpoint-max) {
		border: none;
	}
}

.offcanvas-toggle:checked {
	& ~ .page-wrapper {
		margin-top: 0;
	}
	& ~ .page-wrapper .page.theme-default {
		position: relative;
		height: 100%;
		overflow: hidden;
	}
	& ~ .page-wrapper .page.theme-default header,
	& ~ .page-wrapper .page.theme-default main,
	& ~ .page-wrapper .page.theme-default footer,
	& ~ .offcanvas-toggle-label {
		margin-top: 0;
		transform: translateX(210px);
	}

	& ~ .page-wrapper .page.theme-default header {
		top: 0;
	}

	& ~ .page-wrapper .page.theme-default {
		header .header-top.hidden,
		.trending-bar.hidden {
			transform: translateY(0);
		}
	}
	& ~ .offcanvas-toggle-label.hidden {
		top: 0;
	}

	& ~ .nav-bar-mobile {
		transform: translateX(0);
	}

	& ~ .offcanvas-overlay {
		width: 100%;
		height: 100%;
		opacity: 1;
		left: 210px;
	}

	& ~ .page-wrapper .offcanvas-toggle-label .offcanvas-toggle-icon {
		background: transparent;
		margin-left: 2px;
		&:before {
			transform: rotate(45deg) translateY(8px) translateX(2px);
		}
		&:after {
			transform: rotate(-45deg) translateY(6px) translateX(-3px);
		}
	}
}

.page-wrapper {
	position: relative;
	height: 100%;
	@include media(max-width $mobile-breakpoint-max) {
		overflow: hidden;
	}
}