// Longform Article Umfrage

.theme-longform {
	.article--type-longform {
		.custom-article__voting {
			background: #eaeaea;
			padding: 1em;
			margin: $element-space-large 0;

			.poll__content {
				width: $longform-split-column-smale;
				margin: auto;
				padding: 0;

				@media screen and (max-width: $longform-breakpoint-tablet) {
					width: 80%;
				}

				@media screen and (max-width: $longform-breakpoint-mobile) {
					width: 100%;
				}
			}

			.poll__submit .poll__answer__input {
				color: $longform-decoration-color;
				border: 1px solid $longform-decoration-color;
			}

			.poll__text {
				text-align: left;
				padding: 0;

				p {
					color: #666;
					font-family: $longform-font;
					font-style: italic;
					font-size: 30px;
					font-weight: normal;
					line-height: 1.4em;
					margin-top: 1.5rem;
					width: 100%;

					@media screen and (max-width: $longform-breakpoint-mobile) {
						font-size: 18px;
					}
				}
			}

			.poll__answer__label {
				color: #666;
				font-family: $longform-font;
			}

			.poll__answer__input {
				margin-top: 0.25em;

				&.poll__answer__input {
					font-family: $longform-font;
				}

			}
		}

		.poll--background {
			.poll__answer__label {
				position: relative;
				z-index: 1;
			}

			.poll__submit {
				margin-bottom: 1em;
			}
		}

		.poll__result {
			margin-bottom: 3em;

			.poll__row {
				border: none;
			}

			.poll__answer__share-bar {
				background-color: #fff;
				bottom: 0;
				border-radius: 8px 0 0 8px;
				height: 100%;
				left: 0;
			}

			.poll__answer__share-bar {
				background-color: #fff;
			}

			.poll__answer__share {
				color: #666;
				font-family: $longform-font;
				font-weight: bold;
				font-size: 14px;
			}
		}
	}
}