.teaser-carousel--tripple {
	@include clearfix;
	font-size: 18px;
	line-height: 1.2em;
	background-color: #222;
	min-height: 260px;
	font-family: $font-default;
	text-align: left;
	text-transform: uppercase;

	.container-leader-more-headline {
		background: $color-emph;
		color: #fff;
		font-size: .75em;
		padding-top: .5em;
		padding-bottom: .5em;
		text-align: center;
		top: 0;
		width: 100%;
	}

	.teaser__headline {
		color: #fff;
	}

	a {
		color: #fff;

		@include media(max-width $mobile-breakpoint-max) {
			font-size: 14px;
		}

		&.emph {
			color: $color-emph;
		}
	}

	.teaser .teaser__img img {
		width: 300px;
	}

	.swiper-container {
		padding: 16px;

		.swiper-pagecount,
		.swiper-pagination-bullets {
			display: none;
		}

		.swiper-slide.swiper-slide-duplicate.swiper-slide-prev,
		.swiper-slide.swiper-slide-duplicate.swiper-slide-next {
			visibility: visible;
		}

		figure {
			margin: 0;
		}
	}

	.swiper-button-next:before,
	.swiper-button-prev:before {
		color: #fff;
		font-size: 1em;
		padding: 0;
		margin: 0;
		padding-top: 0.85em;
	}

	.swiper-button-prev,
	.swiper-button-next {
		position: relative;
		background: transparent;
		height: 1em;
		width: 1em;
		margin-top: -30px;
	}

	.swiper-button-next {
		float: right;
		right: 0;
	}

	.swiper-button-prev {
		float: left;
	}
}
