$element-outer-space: 32px;
$element-space-large: 24px;
$element-space-smale: 16px;
$element-space-smaler: 8px;
$element-space-mobile: 10px;
$longform-split-column-large: 67%;
$longform-split-column-smale: 33%;
$longform-caption-space: .625rem;
$longform-decoration-color: $color-emph;
$longform-font: "Noto-Sans";
$longform-breakpoint-1080p: 1920px;
$longform-breakpoint-720p: 1280px;
$longform-breakpoint-tablet: 768px;
$longform-breakpoint-mobile: 640px;
$longform-breakpoint-mobile-min: 380px;

.theme-longform {
	.teaser-carousel--five {
		max-width: 90%;
		position: relative;
		margin: 0 auto;
		padding-top: 100px;

		@media screen and (max-width: $longform-breakpoint-mobile-min) {
			padding-top: 0;
		}

		.container-leader-more-headline {
			width: 37%;
			margin: 0em auto;
			font-family: "Noto-Sans";
			font-size: 30px;
			line-height: 1.5;
			color: #666;

			@media screen and (max-width: $longform-breakpoint-mobile-min) {
				width: auto;
				font-size: 22px;
				line-height: 1.25;
			}
		}

		.teaser__header {
			position: absolute;
			bottom: 0em;
			display: inline-block;
			width: 270px;
		}

		.teaser__link {
			font-family: $longform-font;
			font-size: $element-space-smale;
			font-weight: bold;
			color: #fff;
			padding-left: $element-space-mobile;
			padding-right: $element-space-mobile;
			display: inline-block;
			max-width: 97%;
			margin-bottom: $element-outer-space;
		}

		.emph {
			display: none;
		}

		.swiper-button-next {
			float: right;
			position: absolute;
			right: -1em;
			top: 60%;
			display: inline-block;
			font-size: 2em;

			@media screen and (max-width: $longform-breakpoint-mobile-min) {
				right: -.35em;
				top: 50%;
			}
		}

		.swiper-button-prev {
			float: right;
			position: absolute;
			left: -1em;
			top: 60%;
			display: inline-block;
			font-size: 2em;

			@media screen and (max-width: $longform-breakpoint-mobile-min) {
				left: -.35em;
				top: 50%;
			}
		}
	}
}