.teaser.teaser--breaking-news {
	background: $white;
	box-shadow: 3px 1px 3px 2px rgba(0, 0, 0, .5);
	display: none;
	margin-left: calc(#{$pagewidth} - #{$breaking-news_container-width} - #{$breaking-news_space-right});
	padding: 0;
	position: absolute;
	top: $breaking-news_space-top;
	height: 100px;
	width: $breaking-news_container-width;
	z-index: 99999999;

	@include media(max-width $mobile-breakpoint-max) {
		height: 80px;
		top: $breaking-news_mobile-space-top;
		transition: top 0.2s;
		position: fixed;
		margin-left: calc(100% - #{$breaking-news_container-width} - #{$breaking-news_space-right});
	}

	@include media(max-width $mobile-breakpoint-medium-max) {
		background: rgba(0, 0, 0, .8);
		margin: 0;
		width: 100%;
	}

	.breaking-news__article-link {
		display: flex;
		position: static;
		min-height: 120px;
		width: inherit;

		@include media(max-width $mobile-breakpoint-max) {
			min-height: 0;
		}

		&:hover {
			.teaser__headline {
				color: $color-emph;
				text-decoration: none;
				transition: color 0.2s;

				@include media(max-width $mobile-breakpoint-medium-max) {
					color: #fff;
				}
			}
		}

		.teaser__img {
			background: #f3f3f3;
			color: transparent;
			height: 100px;
			width: 100px;

			@include media(max-width $mobile-breakpoint-max) {
				height: 80px;
				width: 80px;
			}

			&:before {
				@extend .icon;
				@extend .icon--toast-alert;

				color: $color-emph;
				font-size: 70px;
				position: absolute;
				text-align: center;
				padding: 0.22em;
				z-index: 1;

				@include media(max-width $mobile-breakpoint-max) {
					font-size: 50px;
					padding: 0.3em;
				}
			}

			img {
				position: relative;
				z-index: 2;
			}

			img {
				@include media(max-width $mobile-breakpoint-max) {
					height: 80px;
					width: 80px;
				}
			}
		}
	}

	.teaser__header {
		padding: 5px 0 5px 20px;
		font-size: 14px;
		max-width: 340px;

		@include media(max-width $mobile-breakpoint-max) {
			margin-left: 10px;
		}

		@include media(max-width $mobile-breakpoint-medium-max) {
			padding: 5px 0 5px 10px;
			max-width: calc(100% - 120px);
		}

		.teaser__kicker {
			color: #2d2d2d;
			font-size: 12px;
			min-height: inherit;
			padding: 4px 0 5px;

			@include media(max-width $mobile-breakpoint-max) {
				font-size: 14px;
			}

			@include media(max-width $mobile-breakpoint-medium-max) {
				color: $white;
				font-size: 10px;
			}
		}

		.teaser__headline {
			font-size: $breaking-news_headline-fontsize;
			font-weight: bold;
			line-height: $breaking-news_lineHeight;
			display: -webkit-box;
			overflow: hidden;
			max-height: $breaking-news_headline-maxHeight;
			/* stylelint-disable */
			-webkit-line-clamp: 3;
			-webkit-box-orient: vertical;
			/* stylelint-enable */

			@include media(max-width $mobile-breakpoint-medium-max) {
				color: $white;
				font-size: $breaking-news_mobile-headline-fontsize;
			}
		}
	}

	.teaser--breaking-news__more-link {
		display: none;
	}

	.teaser--breaking-news__close-link {
		color: $color-emph;
		cursor: pointer;
		font-size: 16px;
		padding: 7px 18px 20px 20px;
		position: absolute;
		right: 0;
		top: 0;
		height: auto;
		width: auto;
		z-index: 1000;

		@include media(max-width $mobile-breakpoint-medium-max) {
			color: $white;
			font-size: 12px;
			padding: 6px 8px 20px 20px;
		}

		&:hover {
			color: $color-link-hover;
		}
	}

	&.breaking-news-animate-in {
		animation: BreakingNewsTeaserAnimateIn cubic-bezier(.565, -.365, .67, 1.39) .5s;
	}

	&.breaking-news-animate-out {
		animation: BreakingNewsTeaserAnimateOut cubic-bezier(.565, -.365, .67, 1.39) .5s;
	}
}

@keyframes BreakingNewsTeaserAnimateIn {
	from {
		opacity: 0;
		transform: translate(480px, 0px);
	}

	to {
		opacity: 1;
		transform: translate(0px, 0px);
	}
}

@keyframes BreakingNewsTeaserAnimateOut {
	from {
		opacity: 1;
		transform: translate(0px, 0px);
	}

	to {
		opacity: 0;
		transform: translate(480px, 0px);
	}
}

.header.fixed .teaser.teaser--breaking-news,
.header.header--sticky .teaser.teaser--breaking-news {
	top: $breaking-news_space-sticky-top;
	position: fixed;

	@include media(max-width $mobile-breakpoint-max) {
		top: $breaking-news_mobile-space-sticky-top;
	}
}

.nav-bar.sticky ~ .teaser.teaser--breaking-news {
	position: fixed;
	top: $breaking-news_space-sticky-top;
}

.nav-bar.hidden ~ .teaser.teaser--breaking-news {
	@include media(max-width $mobile-breakpoint-max) {
		top: $breaking-news_mobile-space-sticky-top;
	}
}

