// author-info-social

$socialPortalsColors: (
	facebook: #3b5998,
	twitter: #1da1f2,
	google: #dd4b39,
	linkedin: #0077b5,
	xing: #026466,
	other: #c7c8ca
);

.author-info__social {
	font-size: $author-info_social-fontSize;
	display: table;
	grid-column: author-left;
	grid-column-start: 1;
	grid-column-end: 3;
	margin-bottom: 0.5rem;
	min-height: min-content;

	@media (max-width: $mobile-breakpoint-max) {
		font-size: $author-info_social-fontSize-mobile;
	}

	.author-info__social-channels {
		position: static;
		width: 100%;

		a {
			&:before {
				display: none !important;
			}
		}

		.author-info__social-button {
			border-radius: 50%;
			float: left;
			line-height: 2em;
			text-align: center;
			margin-right: .5em;
			width: 2em;
			height: 2em;
		}

		.author-info__social-button {
			&:before {
				color: #fff;
				@include icon;
				line-height: 2em;
				vertical-align: initial;
			}
		}

		.author-info__social-facebook {
			&:before {
				@include icon--facebook;
			}
		}

		.author-info__social-twitter {
			&:before {
				@include icon--twitter;
				margin-top: 0.1em;
			}
		}

		.author-info__social-xing {
			&:before {
				@include icon--xing;
			}
		}

		.author-info__social-linkedin {
			&:before {
				@include icon--linkedin;
			}
		}

		.author-info__social-google {
			&:before {
				@include icon--googleplus;
				margin-top: 0.1em;
			}
		}

		.author-info__social-other {
			&:before {
				@include icon--plus;
			}
		}

		@each $socialPortalName, $socialPortalColor in $socialPortalsColors {
			.author-info__social-#{$socialPortalName} {
				background: $socialPortalColor;
			}
		}
	}
}

.author-info__image ~ .author-info__social {
	grid-column: author-right;
}