.teaser-stream-gallery {
	.carousel-ajax {
		&.swiper-container {
			margin-bottom: $vertical-space-widgets-half;
		}

		.swiper-buttons {
			font-size: 36px;
		}

		.swiper-pagecount {
			border: none;
			padding-top: 1em;
		}

		&.swiper-container .hero-block {
			margin-bottom: 0;
		}

		.carousel-ajax-wrapper {
			margin-bottom: 0.625em;
		}

		.hero-media__caption {
			font-size: 14px;
		}

		.hero-media__caption-text {
			color: $color-swiper-caption-text;
		}
	}
}