.container-leader.grosse-nachrichtenlage {
	max-width: 960px;

	@include media(max-width $mobile-breakpoint-max) {
		max-width: 100%;
		background-color: #fff;
	}

	&.count-3 {

		.teaser-leader.teaser-leader-m {
			&:nth-child(3) {
				margin-right: 0;
			}
		}
	}

	&.count-7 {
		.teaser-leader {
			&.teaser-leader-s {
				&:nth-child(4) {
					margin-right: 0;
				}
				&:nth-child(5) {
					padding-top: 20px;
				}

				&:nth-child(6) {
					padding-top: 20px;
				}

				&:nth-child(7) {
					padding-top: 20px;
				}
			}
		}
	}

	&.count-8 {
		.teaser-leader {
			&.teaser-leader-m {
				&:nth-child(5) {
					margin-right: 0;
				}
			}
		}
	}

	a {
		color: #fff;

		&.emph.headline_section {
			display: table;
			font-size: 16px;
			margin-bottom: 12px;
		}

		&:hover {
			color: $color-emph;
		}
	}

	.emph-hashtags a.emph {
		color: $color-emph;

		&:hover {
			color: #fff;
		}
	}

	.teaser__location {
		display: none;
	}

	.headline.headline-s a.emph {
		display: block;
	}

	.teaser.teaser-leader:not(.teaser-leader-w) a.emph {
		display: none;
	}

	.teaser-leader {

		&.teaser-leader-w {
			margin-top: 0;

			@include media(max-width $mobile-breakpoint-max) {
				height: 240px;
				font-size: 24px;
				background-color: #333;
				margin: 0;
			}

			.headline {
				font-size: 30px;
				bottom: 1em;

				@include media(max-width $mobile-breakpoint-max) {
					@include span-columns(12 of 12);
				}
			}

			.teaser-image-block {
				width: 100%;
				height: auto;
			}

			.teaser__link__more {
				display: block;
				visibility: hidden;
			}

			.teaser-image-block img {
				@include media(max-width $mobile-breakpoint-max) {
					width: 100%;
					margin: 0;
				}
			}
		}

		&.teaser-leader-m {
			width: 470px;
			margin-right: 20px;
			margin-bottom: 20px;
			height: 345px;
			padding: 0;
			font-size: 24px;
			line-height: 1.33em;

			@include media(max-width $mobile-breakpoint-max) {
				height: 100px;
				background-color: #333;
				margin: 1px 0;
				width: 100%;
			}

			.headline {
				bottom: 0;
				padding: 0 20px;
				margin: 0;
				font-weight: normal;
				line-height: 1.1;
				font-size: 26px;

				@include media(max-width $mobile-breakpoint-max) {
					bottom: 0;
					top: 10px;
					padding-left: 110px;
					font-size: 16px;
					line-height: 1.11em;
				}
			}

			.teaser__link__more {
				display: block;
				visibility: hidden;
			}

			.overlay {
				@include media(max-width $mobile-breakpoint-max) {
					display: none;
				}
			}

			.teaser-image-block {
				@include media(max-width $mobile-breakpoint-max) {
					float: left;
					width: 100px;
					margin: 0;
					height: 100px;
				}
				img {
					@include media(max-width $mobile-breakpoint-max) {
						width: 100px;
						margin: 0;
						height: 100px;
					}
				}
			}

			&:nth-child(5) {
				margin-right: 0;
			}
		}

		&.teaser-leader-s {
			width: 310px;
			height: 225px;
			padding: 0;
			margin-right: 20px;
			margin-bottom: 0px;
			font-size: 24px;
			line-height: 1.33em;

			@include media(max-width $mobile-breakpoint-max) {
				height: 100px;
				background-color: #333;
				margin: 1px 0;
				width: 100%;
			}

			.headline {
				position: absolute;

				@include media(max-width $mobile-breakpoint-max) {
					bottom: 0;
					top: 10px;
					padding-left: 110px;
					font-size: 16px;
					line-height: 1.3em;
				}
			}

			.overlay {
				@include media(max-width $mobile-breakpoint-max) {
					display: none;
				}
			}

			.teaser-image-block {
				@include media(max-width $mobile-breakpoint-max) {
					float: left;
					width: 100px;
					margin: 0;
					height: 100px;
				}
				img {
					@include media(max-width $mobile-breakpoint-max) {
						width: 100px;
						margin: 0;
						height: 100px;
					}
				}
			}
			&:last-child {
				margin-right: 0;
			}
		}
	}
}
