.topic {
	@media (max-width: $mobile-breakpoint-max) {
		float: left;
		display: block;
		margin-right: 0;
		width: 16.75%;
		text-align: left;
		padding: 0;
		padding-top: 1em;
		padding-left: 0.25em;
		padding-bottom: 0.5em;
	}

	.topic--filter__item {
		@media (max-width: $mobile-breakpoint-max) {
			line-height: 2em;
			display: block;
			text-align: center;
		}
	}

	.topic--filter {
		padding-bottom: 25px;
		@media (max-width: $mobile-breakpoint-max) {
			border: 0;
		}
	}

	.teaser .teaser__headline {
		color: $color-text-default;
		word-wrap: break-word;
	}

	.topic--filter__item__link {
		color: $color-text-default;
	}

	.topic--filter__item.topic--filter__item--active {
		border-bottom: 2px solid $color-emph;
	}
}

.topic.content--teaser--container {
	@media (max-width: $mobile-breakpoint-max) {
		@include span-columns(10);
		float: right;
		text-align: left;
		padding-top: 3.2em;
	}
}