h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

h1 {
	color: $color-text-logo;
	font-size: $font-size-headline-m;
	line-height: $font-size-headline-m;
}

a {
	color: $color-emph;
	cursor: pointer;
	text-decoration: none;
}

p {
	font-weight: 500;
	line-height: $default-lineheight;
}

.headline {
	color: $color-text-headline-default;
	font-family: $font-headline;
	font-weight: $font-weight-normal;
	line-height: $medium-lineheight;

	.teaser__link {
		color: $white;
	}

	a {
		transition: $transition-fast;
		border-bottom: 1px solid rgba(0, 0, 0, 0);
		display: inline;
	}

	a:hover {
		border-bottom: 1px solid rgba(0, 0, 0, .2);
		color: $color-bg-dark;
	}

	&.headline-l {
		font-size: $font-size-headline-l;

		@include media(max-width $mobile-breakpoint-max) {
			font-size: $font-size-headline-mobile-l;
		}
	}

	&.headline-m {
		font-size: $font-size-headline-m;

		@include media(max-width $mobile-breakpoint-max) {
			font-size: $font-size-headline-mobile-m;
		}
	}

	&.headline-s {
		font-size: $font-size-headline-s;

		@include media(max-width $mobile-breakpoint-max) {
			font-size: $font-size-headline-mobile-s;
		}
	}

	&.on-dark {
		clear: both;
		color: $color-text-ondark;
		padding-right: 2em;

		a {
			border-bottom: 1px solid rgba(255, 255, 255, 0);
		}

		a:hover {
			color: #fff;
			border-bottom: 1px solid rgba(255, 255, 255, .3);
		}
	}
}

.emph,
a.emph {
	border-bottom: none;
	color: $color-emph;

	&:hover {
		background: $color-emph;
		color: $white;
		border-bottom: none;
		border-radius: 3px;
	}
}

.headline a.emph,
.headline.on-dark a.emph {
	border-bottom: none;

	&:hover {
		color: $white;
		border-bottom: none;
	}
}
