.teaser-carousel--tripple {
	background: transparent;
	text-transform: none;
	position: relative;
	margin-bottom: 50px;

	a {
		color: $color-text-black;
		&.emph.headline_section {
			text-transform: uppercase;
			font-size: 11px;
			display: block;
			line-height: 1;
		}
	}
	.container-leader-more-headline {
		background: $buttons-more-background;
		font-size: 18px;
		padding: .9em;
		line-height: 1;
		text-align: left;
		text-transform: uppercase;
	}
	.teaser {
		background: $white;
		.teaser__img img {
			width: 100%;
			min-height: initial!important;
		}
	}
	.teaser__header {
		font-family: $font-default;
		position: relative;
		width: 85%;
		top: -35px;
		left: 20px;
		right: 35px;
		background: $white;
		padding: 10px 16px 0;
	}
	.teaser__link {
		font-size: 18px;
		font-weight: 500;
	}
	.swiper-container {
		padding: 14px 0 0;
		@media screen and (max-width: $mobile-breakpoint-max) {
			padding: 0 0 15px;
		}
		.swiper-pagecount {
			display: none;
		}
	}
	.swiper-pagination-bullets {
		display: block;
		text-align: center;
		margin-top: -5px;
		padding: 0 1em;
		position: relative;
		z-index: 2;
		@media screen and (max-width: $mobile-breakpoint-max) {
			width: calc(100% - 70px);
			position: absolute;
			bottom: 6px;
			left: 35px;
			z-index: 2;
		}
		.swiper-pagination-bullet {
			background: $color-cool-grey;
			&-active {
				background: $color-emph;
			}
			&+.swiper-pagination-bullet {
				margin-left: .2em;
			}
		}
	}
	.swiper-wrapper {
		li {
			background: $white;
			&:after {
				position: absolute;
				content: "";
				width: 100%;
				height: 15px;
				bottom: 0;
				left: 0;
				background: #eaeaea;
			}
		}
	}
	.swiper-buttons {
		font-size: 35px;
		height: 1em;
		position: absolute;
		top: 37px;
		right: .2em;
		> div {
			background: #eaeaea;
			border-radius: 4px;
			&:before {
				font-size: 13px;
				color: $color-text-black;
			}
		}
		.swiper-button-prev {
			margin-right: .28em;
		}
		@media screen and (max-width: $mobile-breakpoint-max) {
			top: initial;
			bottom: 0;
			height: 35px;
			width: 100%;
			right: 0;
			z-index: 2;
			background: $white;
			> div {
				float: none;
				display: block;
				margin-top: 0;
				position: absolute;
				background: $color-text-headline-default;
				&:before {
					color: $white;
				}
			}
		}
	}
	.teaser__intro {
		display: block;
		font-size: 16px;
		margin-top: 1em;
	}
	@media screen and (max-width: $mobile-breakpoint-max) {
		background: #fff;
	}
}

.article+.content-wide .teaser-carousel--tripple {
	.swiper-wrapper li:after {
		display: none;
	}
}