/*
 * @name: teaser
 *
 * @description: basic teaser
 *
 */

.teaser {

	&.advertisement .teaser__tag {
		background-color: $color-emph-special;
	}

	&.city-report {
		.teaser__tag {
			background-color: $color-emph-special;
		}

		.more {
			border-top: 3px solid $color-emph-special;
		}
	}
}

.teaser .teaser__img {
	display: block;
}

.teaser .teaser__headline {
	margin-bottom: .375em;
}

.teaser .teaser__kicker,
.teaser .teaser__date,
.teaser .teaser__time {
	font-size: .75em;
}

.content-double {
	margin-bottom: $vertical-space-section;

	.teaser__img--vertical ~ .teaser__header {
		@include span-columns(12 of 12);
		@include omega;
	}

	.content-double__column .teaser .teaser__header {
		@include omega;
	}

	.content-double__column .teaser {
		padding-top: 0;

		@include media(max-width $mobile-breakpoint-max) {
			border-top: 1px solid $color-teaser-border;
			padding: $vertical-space-widgets 0;
		}

		&:last-child {
			padding-bottom: 0;

			@include media(max-width $mobile-breakpoint-max) {
				padding-bottom: $vertical-space-widgets;
			}
		}
	}

	.content-double__column:first-child .teaser {
		@include media(max-width $mobile-breakpoint-max) {
			border: none;
			padding-top: 0;
		}
	}
}

.content-quad {
	margin-bottom: $vertical-space-section;
}

.teaser.advertisement {
	.teaser-stream-time {
		visibility: hidden;
	}
}
