.teaser-stream {
	@include outer-container;
	background: $color-bg-lighter;
	margin-bottom: $vertical-space-widgets;
	padding: $vertical-space-widgets;

	@media (max-width: $mobile-breakpoint-max) {
		margin-bottom: $vertical-space-widgets-mobile;
	}

	.advertisement {
		background: $color-emph;
		color: $white;
		padding: .25em .5em;
		font-size: $font-size-text-small;
		font-weight: normal;
		clear: both;
		font-family: $font-default;
		text-transform: uppercase;
		display: table-cell;
	}

	.teaser-stream-time {
		@include span-columns(1);
		margin-bottom: $vertical-space-widgets;
		margin-top: 5px;

		@media (max-width: $mobile-breakpoint-max) {
			@include span-columns(12);
			margin-bottom: $vertical-space-widgets-half;
		}

		p {
			color: $color-text-light;
			font-family: $font-extra;
			font-size: $font-size-extras-tiny;
			font-weight: $font-weight-regular;
			margin: 0;
		}
	}

	.teaser__link {
		transition: $transition-fast;
		color: $black;

		&:hover {
			color: $color-link-hover;
		}
	}

	.teaser__link-image {
		display: block;
		position: relative;
	}

	.teaser__link__text {
		transition: $transition-fast;
		color: $color-text-default;

		&:hover {
			color: $color-text-default;
		}
	}

	.teaser-stream-content {
		@include span-columns(11);
		margin-right: 0;

		.headline {
			margin-bottom: $vertical-space-widgets;
			margin-top: 0;
		}

		@media (max-width: $mobile-breakpoint-max) {
			@include span-columns(12);
		}

		&.with-small-img {
			@include span-columns(8);

			@media (max-width: $mobile-breakpoint-max) {
				@include span-columns(9);
			}
		}
	}

	.hashtag-bar {
		margin-bottom: $vertical-space-widgets;
		width: 100%;
	}

	.small-img-container {
		@include span-columns(3);
		padding-left: $vertical-space-widgets;
	}

	img {
		display: block;
		max-width: 100%;
		width: 100%;
	}

	.teaser-media .teaser-media__caption {
		display: inline-block;
		width: 100%;
	}

	.teaser-media .teaser-img__text,
	.teaser-img__credits {
		color: $color-text-light;
		font-family: Arial,sans-serif;
		font-size: 0.75em;
		font-weight: normal;
		margin: 0;
		padding: 0;
	}

	.teaser-media .teaser-img__text,
	.teaser-media .teaser-img__credits {
		margin-top: $vertical-space-widgets-half;
	}

	.teaser-media .teaser-img__credits {
		text-align: right;

		@media (max-width: $mobile-breakpoint-max) {
			text-align: left;
		}
	}

	.disqus-comment-count {
		display: none;
	}
}

.teaser-stream-gallery,
.teaser-stream-video {
	.headline .teaser__link {
		transition: $transition-fast;
		color: $white;

		&:hover {
			color: $color-link-hover-on-dark;
		}
	}

	.headline .emph {
		&:hover {
			color: $color-link-hover-on-dark;
		}
	}
}

.more-content {
	transition: opacity .2s linear;
	animation-delay: .1s;
	display: none;
}

.teaser-stream-time.mobile {
	display: none;
}
