@font-face {
	font-family: "icon-font-wetter";
	font-style: normal;
	font-weight: normal;
	src: url("../fonts/wetter-icons/wetter-icons.woff2") format("woff2"),
		url("../fonts/wetter-icons/wetter-icons.woff") format("woff");
}

/* Icon */

@mixin icon-wetter() {
	display: inline-block;
	font-family: "icon-font-wetter";
	font-style: normal;
	font-weight: normal;
	line-height: 1;
	speak: none;
	text-decoration: inherit;
	text-rendering: optimizeLegibility;
	text-transform: none;
	vertical-align: middle;
}

.icon-wetter {
	@include icon-wetter;
}


