.breadcrumb {
	background: $color-bg-lighter;
	font-size: 10px;
	margin: 0;
	padding-left: 1.67em;
	position: relative;

	@media (max-width: $mobile-breakpoint-max) {
		background-color: transparent;
	}

	&:before {
		@include icon;
		@include icon--home;
		font-size: 1.2em;
		margin: -.35em .2em 0;
	}

	.breadcrumb__item {
		margin-left: .6em;
		a {
			margin-left: .6em;
		}
	}

	.breadcrumb__item,
	.breadcrumb__link {
		color: $color-text-headline-default;
	}

	h1 {
		color: $color-emph;
	}

}