@media screen and (min-width: 768px) and (max-width: 1023px) {
	.sidebar__column,
	.content-wide,
	.content-triple__column {
		iframe {
			transform: scale(.76);
			transform-origin: 0 0;
		}
		img {
			max-width: 100%;
		}
	}
	.sidebar__column .ad--marker,
	.content-triple__column .ad--marker {
		height: 200px;
	}
	.content-wide .ad--marker {
		height: 56px;
	}
	.main > .ad--marker {
		.ad--marker-inner {
			transform: scale(.9);
			transform-origin: 0 0;
		}
	}
}

.ad {
	&.ad--marker {
		clear: both;
		position: relative;
		text-align: center;

		.ad--marker-inner {
			display: inline-block;
			text-align: left;

			&:before {
				color: $light-grey;
				content: "anzeige";
				display: block;
				font-family: $font-family-meta;
				font-size: 10px;
				text-transform: uppercase;
				margin-bottom: 2px;
			}
		}

		&.ad--empty {
			margin: 0;
			padding: 0;

			.ad--marker-inner:before {
				display: none;
			}
		}
	}
}

.page {
	.carousel-ajax {
		.ad {
			padding: 0;
			display: table-cell;
			vertical-align: middle;
			//height: 100%;

			.ad--marker-inner {
				&:before {
					transition: $transition-fast;
				}
			}

		}
	}
}