/* mobile navigation with off-canvas menu is defined in custom-classes/_custom__nav-bar--offcanvas.scss */

.nav-bar {
	box-shadow: none;
	font-family: $font-default;
	text-transform: uppercase;

	.nav-main {
		background: #fff;
		border-bottom: 1px solid #dddadc;
		border-top: 1px solid #dddadc;
		width: 100%;

		.nav-main__anchor--active {
			border-bottom: 5px solid #333;
		}
		ul {
			display: inline-block;
			margin-left: 20px;
			@include media (max-width $mobile-breakpoint-max) {
				margin-left: 0;
				display: block;
			} 
		}
	}

	nav {
		a {
			padding: 0;
		}
	}

	.nav-main {
		li {

			background: none;
			line-height: 2.1em;

			& + li {
				margin-left: 16px;
			}

			a {
				padding-top: 12px;
				border-bottom: 5px solid #ddd;
			}
		}

		.nav-main__anchor--active {
			font-weight: normal;
		}
	}

	button {
		display: none;
	}
}
