.inline-media {
	margin-bottom: $vertical-space-widgets;

	.inline-media__wrapper {
		line-height: 0;
	}

	.inline-media__lightbox-opener .inline-media__media {
		display: inline-block;
		position: relative;
		cursor: pointer;

		&:before {
			@include icon;
			@include icon--search;
			position: absolute;
			bottom: .625em;
			right: .625em;
			width: 1.5em;
			height: 1.5em;
			border-radius: 50%;
			box-shadow: none;
			line-height: 1.5em;
			color: $color-publication;
			background-color: $white;
			cursor: pointer;
			text-align: center;
			font-size: 1.125em;
			font-weight: bold;
		}

		@include media(max-width $mobile-breakpoint-max) {
			&:before {
				content: none;
			}
		}
	}

	.inline-media__caption {
		display: inline-block;
		margin-top: 0;
		padding: 10px $vertical-space-widgets;
		width: 100%;
	}

	.inline-media__text,
	.inline-media__credits {
		color: $color-text-light;
		font-family: $font-family-meta;
		font-size: .75em;
		font-weight: normal;
		margin: 0;
		padding: 0;
	}

	.inline-media__text {
		@include span-columns(12);
		font-weight: $font-weight-regular;

		@media (max-width: $mobile-breakpoint-max) {
			@include span-columns(12);
			@include omega;
		}
	}

	.inline-media__credits {
		@include span-columns(12);

		@media (max-width: $mobile-breakpoint-max) {
			@include span-columns(12);
			@include omega;
		}
		text-align: right;
	}
}

.swiper-container {
	.inline-media {
		@media (max-width: $mobile-breakpoint-max) {
			.inline-media__credits {
				padding-right: 2.5em;
			}
		}
	}
}

.video-inline {
	.inline-media {
		.inline-media__caption {
			margin-top: 0;
		}
	}
}

.brightcove_player div {
	max-width: 100%;
}
