//
// theme mixin
//
@mixin theme($name, $background-color, $border-color, $text-color, $background-element-color, $border-element-color) {
	.#{$name} {
		background-color: $background-color;
		color: $text-color;

		a {
			color: $text-color;
		}

		.block-header {
			border-color: $border-color;
		}

		.calendar .calendar__day--past {
			color: shade($text-color, 30%);
		}

		.calendar .calendar__day--today a {
			background-color: $text-color;
			color: $background-color;
		}

		.search .search__input,
		.search .search__select {
			background: $background-color;
			border-color: $text-color;
			color: $text-color;

			&::-webkit-input-placeholder {
				color: darken($text-color, 20%);
			}
			&:-moz-placeholder {
				color: darken($text-color, 20%);
			}
			&::-moz-placeholder {
				color: darken($text-color, 20%);
			}
			&:-ms-input-placeholder {
				color: darken($text-color, 20%);
			}
		}

		.search .search__button {
			background: $text-color;
			color: $background-color;
		}

		.search--header .search__button {
			background: $background-element-color;
		}

		.search--header .search__input,
		.search--header .search__select {
			border-color: $light-grey;
		}
	}
}
