.trending-bar {
	background: rgba(245, 245, 245, 0.8);
	backdrop-filter: blur(5px);
	height: 30px;
	position: relative;
	transition: .5s all ease-in-out;
	@include media(max-width $mobile-breakpoint-max) {
		overflow-x: scroll;
		-webkit-overflow-scrolling: touch;

	}

	&.hidden {
		transform: translateY(-90px);
	}
}

.trending-bar__list {
	max-width: 920px;
}

.trending-bar__list__item {
	a {
		color: $color-text-default;
	}
}