.sidebar-topic {
	background-color: $color-bg-black;
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	overflow: hidden;
	padding: 16px 16px 0;
	position: relative;
	width: 100%;

	> .headline {
		position: absolute;
		top: 16px;
		z-index: 10;
	}

	.hashtag {
		display: block;
		font-size: 1.5em;
		font-weight: normal;
		margin-bottom: 2em;
		position: relative;
		z-index: 1;
	}

	.emph {
		transition: $transition-fast;

		&:hover {
			color: $color-link-hover-on-dark;
		}
	}

	.sidebar-topic-headlines-container {
		bottom: 0;
		padding-right: .5em;
		position: relative;
		z-index: 10;

		.headline {
			margin-bottom: 24px;
		}
	}

	.overlay {
		background-color: rgba(0, 0, 0, .65);
		bottom: 0;
		left: 0;
		position: absolute;
		right: 0;
		top: 0;
	}
}
