@mixin center ($alignment: "both", $position: relative) {
	@if $alignment == "v" or $alignment == "vertical" {
		top: 50%;
		transform: translateY(-50%);
	}
	@if $alignment == "h" or $alignment == "horizontal" {
		left: 50%;
		transform: translateX(-50%);
	}
	@if $alignment == "b" or $alignment == "both" {
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
	@if $position == "a" {
		position: absolute;
	}
	@else {
		position: $position;
	}
}