.lightbox--media ~ .remodal-close {
	@include media(max-width $mobile-breakpoint-max) {
		z-index: 1;
	}
}

.carousel__caption__button {
	color: $white;
	cursor: pointer;
	display: none;
	left: 2.25em;
	position: absolute;
	text-decoration: none;
	top: 1.875em;
	z-index: 2;

	&:before {
		@include icon;
		@include icon--text;
	}
}

.lightbox--media ~ .remodal-close:before {
	@include media(max-width $mobile-breakpoint-max) {
		color: $white;
	}
}

.lightbox--media {
	padding: 2.5em 3.75em;
	width: auto;
	max-width: $pagewidth;

	@include media(max-width $mobile-breakpoint-max) {
		background: $dark-grey;
		height: 100%;
		padding: 2.5em 0 0;
		width: 100%;

		.article--media {
			background: $dark-grey;
		}

		.carousel__control i {
			color: $white;
		}
	}

	@include media(orientation "landscape") {
		@include media(max-width $mobile-breakpoint-max) {
			padding: 0;
		}

		.carousel .carousel__list {
			@include media(max-width $mobile-breakpoint-max) {
				margin: 0;
			}
		}

		.carousel__caption__button {
			display: block;
			left: 0;
			padding: .625em;
			top: 0;
		}

		.hero-media__media {
			@include media(max-width $mobile-breakpoint-max) {
				height: 100px;
				position: relative;
				width: 100%;
			}
		}

		.carousel__control i {
			@include media(max-width $mobile-breakpoint-max) {
				font-size: .9375em;
			}
		}

		.hero-media__media img {
			@include media(max-width $mobile-breakpoint-max) {
				height: 100%;
				margin: auto;
				width: auto;
			}
		}

		.hero-media__caption {
			@include media(max-width $mobile-breakpoint-max) {
				background: $black-transparent;
				bottom: 0;
				display: none;
				font-size: .625em;
				position: absolute;
			}
		}

		.hero-media__wrapper {
			@include media(max-width $mobile-breakpoint-max) {
				margin: 0;
			}
		}
	}

	.article {
		margin-top: 0;
		margin-bottom: 0;
		text-align: left;
	}

	.article__header {
		margin-bottom: 0;

		@include media(max-width $mobile-breakpoint-max) {
			margin: 0;
			padding: 0;
		}
	}

	.hero-carousel {
		margin-bottom: 0;
		margin-right: 0;
	}

	.minified .socialbar__channels {
		bottom: 4em;
		right: 0;
		top: auto;
	}

	.hero-media {
		.teaser__img_custom > img {
			display: block;
			width: auto;
			max-width: 100%;
			height: auto;
			max-height: calc(100vh - 280px);
			margin-left: auto;
			margin-right: auto;
		}

		.hero-media__caption-text {
			@include span-columns(9 of 12);

			@include media(max-width $mobile-breakpoint-max) {
				@include span-columns(12 of 12);
			}
		}

		.hero-media__caption-index {
			clear: both;
			font-weight: normal;
			text-align: center;
			width: 100%;
		}

		.hero-media__caption-credits,
		.hero-media__caption-datum,
		.hero-media__caption-text,
		.hero-media__caption-index {
			@include media(max-width $mobile-breakpoint-max) {
				color: $white;
			}
		}
		.hero-media__caption {
			display: block;
			max-width: $image-lightbox_caption-maxWidth;
			margin-left: auto;
			margin-right: auto;

			@include clearfix;
			@include media(max-width $mobile-breakpoint-max) {
				border-bottom: none;
				padding: $space-innerpage;
				background: $dark-grey;

				@include media(orientation "landscape") {
					padding-left: 3.75em;
					padding-right: 3.75em;
				}
			}
		}
	}

	.article__footer {
		margin-top: 1.5em;

		@include media(max-width $mobile-breakpoint-max) {
			display: none;
		}
	}

	.article__footer__more {
		color: $dark-grey;
		float: right;
		font-family: $font-family-meta;
		font-weight: $font-weight-fat;
		margin-bottom: $vertical-space-section;

		&:before {
			@include icon;
			@include icon--arrow-circle-right;
			border-radius: 50%;
			color: $dark-grey;
			font-size: 1.375em;
		}
	}

	.socialbar,
	.article__header__heading,
	.article__header__kicker {
		@include media(max-width $mobile-breakpoint-max) {
			display: none;
		}
	}

	.carousel .carousel__control--prev i,
	.carousel .carousel__control--next i {
		color: $white;
	}
}

