.article {
	.article__header {
		margin-bottom: 0;
		padding: 0;
	}

	.inline-block--wide .swiper-container .inline-media__caption-text+.inline-media__caption-credits {
		margin-top: 0.5rem;
	}

	.article__header__heading {
		margin: 0;
		padding: 0 $vertical-space-widgets;

		.article__header__headline__heading {
			font-weight: 500;

			@include media (max-width $mobile-breakpoint-max) {
				font-size: 30px;
				line-height: 1;
			}
		}
		@include media (max-width $mobile-breakpoint-max) {
			padding: 0 10px;

			.article__header__headline {
				margin-bottom: 20px;
			}
		}
	}

	.article__body {
		padding: 0 45px;

		@include media (max-width $mobile-breakpoint-max) {
			font-size: 16px;
			padding: 0 10px;

		}

		.socialbar {
			margin-bottom: 18px;
			@include media (max-width $mobile-breakpoint-max) {
				margin-bottom: 40px;
			}
		}

		h2 {
			font-size: 24px;

			@include media(max-width $mobile-breakpoint-max) {
				font-size: 16px;
				line-height: 1.5;
			}
		}
	}

	.hero-img__credits,
	.hero-video__credits,
	.hero-media__caption-credits {
		text-align: left;
	}

	.hero-img__credits {
		@include media(max-width $mobile-breakpoint-max) {
			color: $color-cool-grey;
		}
	}

	.hero-img__caption {
		position: relative;
	}
	.article__header__heading {
		padding-top: 0;
	}
	.article__header__headline {
		margin-bottom: $vertical-space-widgets-half;

		.headline_section {
			display: block;
			font-weight: bold;
			font-size: 10px;
			text-transform: uppercase;
		}
	}
	.hero-img__caption,
	.hero-media__caption,
	.inline-media__caption {
		border-left: 1px solid $color-cool-grey;
		font-size: 16px;
		padding: .25em 1.25em .5em;
		position: relative;
		margin: 1em 0 0 59px;
		width: calc(100% - 59px);

		.hero-media__caption-datum {
			float: none;
		}

		@include media (max-width $mobile-breakpoint-max) {
			border-color: $color-emph;
			font-size: 14px;
			margin-left: 10px;
			margin-top: 10px;
			padding: 0 10px;
			width: calc(100% - 10px);
		}

		div {
			font-size: 1em;
			line-height: 1.2;
			margin: 0;
			margin-top: 0.5rem;

			@include media (max-width $mobile-breakpoint-max) {
				line-height: 1.43;
			}
		}

		.inline-media__credits {
			text-align: left;
		}

		&:before {
			@include icon;
			@include icon--camera;
			@include center(v, a);
			color: $color-emph;
			font-size: 1.5rem;
			left: -1.85em;

			@include media (max-width $mobile-breakpoint-max) {
				display: none;
			}
		}
	}

	.video-inline {
		.hero-img__caption,
		.inline-media__caption {
			&:before {
				@include icon--video-2;
			}
		}
	}

	.sidebar {
		padding: 0 30px 0 20px;
	}

	.headline_section {
		display: table;
		font-size: 12px;
		font-weight: 500;
		text-transform: uppercase;
	}

	address {
		color: $color-cool-grey;
		display: inline-block;
		font-size: 14px;
		margin-right: 12px;
		float: left;

		@include media (max-width $mobile-breakpoint-max) {
			font-size: 13px;
			margin-right: 0;
			margin-bottom: 25px;

			span {
				margin-left: 5px;
			}
		}
	}

	.author-info_wrapper-date,
	.author-info_wrapper-image {
		padding-left: 0;
		margin-left: 1em;

		@include media(max-width $mobile-breakpoint-max) {
			margin-left: .5em;
		}
	}

	.author-info_wrapper-image {
		.author-info_wrapper-date {
			padding-left: 0;
		}
	}

	address {
		margin-bottom: 0;
		margin-right: 12px;
		margin-top: .2em;
	}

	.article__header__author__wrapper {
		padding: 0 $vertical-space-widgets;
		margin-bottom: 16px;
		@include media (max-width $mobile-breakpoint-max) {
			padding: 0 10px;
		}
	}

	.emph-hashtags {
		font-size: 14px;
		text-transform: uppercase;
		margin-bottom: $vertical-space-widgets-half;

		@include media (max-width $mobile-breakpoint-max) {
			margin-bottom: 0;
		}
	}

	.emph {
		padding: 2px;

		&:hover {
			color: #fff;
		}
	}
	.inline-block--wide,
	.video-inline {
		width: 100%;

		.inline-media__caption {
			margin-top: 1em;
		}

		.inline-media__caption,
		.hero-img__caption {
			border-bottom: none;
			border-color: $color-emph;

			&:before {
				color: $color-cool-grey;
			}

			.inline-media__caption-text {
				font-size: 16px;
				margin-top: 0;
			}
		}
		.swiper-pagecount {
			display: none;
		}
		@include media(max-width $mobile-breakpoint-max) {
			width: calc(100% + 20px);
			margin: 40px 0 30px -10px;
			.swiper-buttons {
				display: block;
				.swiper-button-next,
				.swiper-button-prev {
					display: block;
				}
			}
			.inline-media__caption-text {
				margin-top: 0;
			}
		}
	}


	.video-inline {
		width: inherit;

		@include media(max-width $mobile-breakpoint-max) {
			width: 100%;
		}
	}

	&.article--type-news:last-of-type {
		.emph-hashtags {
			display: none;
		}

		.teaser.teaser-article-summary {
			position: relative;

			.teaser-article-summary-headline {
				color: $color-text-default;
				font-size: 18px;
				font-weight: bold;
				text-transform: none;
			}
		}
	}

	.article__body ul:not([class]) {
		font-family: $font-default;
		font-size: 16px;
		font-weight: normal;
		list-style-type: none;
		line-height: 1.9em;
		padding: 0 16px;

		li {
			padding-left: 2.3em;

			&:before {
				@include icon;
				@include icon--bulletpoint;
				color: $color-emph;
				display: inline-block;
				font-size: .75em;
				margin-left: -3em;
				margin-right: 20px;
				margin-top: -5px;
				width: 1.3em;
			}
			&:last-of-type {
				margin-bottom: 2.5em;
			}
		}
		@include media(max-width $mobile-breakpoint-max) {
			padding: 0;
		}
	}

	.player_youtube .video__content {
		height: 0;
		position: relative;
		padding-bottom: 56.25%; /* 16:9 */

		iframe {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}

	&.article--type-gallery,
	&.article--type-video {
		.article__header {
			background: #fff;
		}
		.article__header__headline .article__header__headline__heading {
			color: #000;
		}
	}

	.hero-carousel .hero-media__caption-credits {
		text-align: left;
		margin-top: 0;
	}
}

.article.article--type-gallery .inline-block--wide,
.article.article--type-video .inline-block--wide {
	float: none;
	margin-left: 0;
	width: 100%;
}

// Temporr ausgeblendet wg. FDC-324, solange Header/Navigation nicht fertig gebaut ist
.trending--header,
.main-menu {
	@include media(max-width $mobile-breakpoint-max) {
		display: none;
	}
}

