// base config file

// neat config
$border-box-sizing: true;
$visual-grid: false;
$visual-grid-color: #ccc;
$visual-grid-index: front;
$gutter: .4em;

body {
	background: #fff;
	color: $color-text-default;
	font: 400 $font-size-text-default $font-family-meta;
	line-height: $default-lineheight;
}

.page-wrapper {
	margin: 0 auto;
	max-width: 960px;
	width: 100%;
	overflow: visible;
}

.page.theme-default {
	//border: 1px solid #dddadc;
	box-shadow: 0 0 0 1px rgba(221, 218, 220, 1);
	background: #eaeaea;
	margin-top: $vertical-space-widgets;

	@media (max-width: $mobile-breakpoint) {
		margin-top: 0;
	}
}

.main {

	@media (max-width: $mobile-breakpoint) {
		overflow: hidden;
		width: 100%;
		padding-top: 86px;
	}
}

