.hero-carousel {
	&.carousel-ajax {
		.swiper-button-prev,
		.swiper-button-next {
			&:before {
				@include media(max-width $mobile-breakpoint-max) {
					padding-top: 0.8em;
					vertical-align: top;
				}
			}
		}

		&.swiper-container {
			.hero-media__caption {
				@include media(max-width $mobile-breakpoint-max) {
					margin-left: 20px;
					padding: 0 10px;
					width: calc(100% - 20px);
				}
			}

			.swiper-buttons {
				@include media(max-width $mobile-breakpoint-max) {
					padding: 0 20px;
				}
			}
		}

		.hero-media {
			.hero-media__caption {
				width: calc(100% - 59px);
				.hero-media__caption-text,
				.hero-media__caption-credits {
					color: #666;
				}

				.hero-media__caption-credits {
					margin-top: 0.5rem;
				}
			}
		}
	}
}

