.trending-widget-wrapper {
	padding: 0;
	background-color: $grey;
	font-family: $font-default;
	margin-bottom: $vertical-space-widgets;
	padding-top: $vertical-space-widgets;

	@include media(max-width $mobile-breakpoint-max) {
		padding-bottom: $vertical-space-widgets;
	}

	.block-header {
		@include media(max-width $mobile-breakpoint-max) {
			font-size: 18px;
		}
	}

	.teaser-list {
		@include media(max-width $mobile-breakpoint-max) {
			padding: 0;
		}
	}

	.trending {
		margin: 0;
		padding: 0;
	}

	.teaser-list li + li .teaser {
		border: 0;
	}

	.teaser-list li {
		a:hover .teaser__headline {
			text-decoration: none;
		}
	}

	.teaser-list .teaser {
		padding-bottom: 0.5em;
	}

	.block-header.block-header--small {
		color: $black;
		font-size: 1.5em;
		font-weight: bold;
		margin-left: $vertical-space-widgets;
		margin-right: $vertical-space-widgets;
		padding-bottom: 0;

		@include media(max-width $mobile-breakpoint-max) {
			padding-left: 0;
		}

		&:after {
			border: none;
		}
	}

	.teaser__identifier {
		@include span-columns(2 of 12);
		color: $black;
		font-weight: bold;
	}

	.teaser .teaser__headline {
		color: $black;
		font-family: $font-default;
		font-size: 16px;
		font-weight: bold;
		margin-left: 0;
	}

	.teaser__img {
		margin-bottom: 1em;
	}
}

