.inline-carousel2 {
	.carousel__list {
		margin-top: 0;
	}

	.inline-media__wrapper {
		margin-bottom: $vertical-space-imageblock;
		position: relative;
		text-align: center;
	}

	.carousel__control,
	.carousel__control i {
		bottom: 0;
		color: $white;
		font-size: 1.875em;
		text-shadow: 0 1px $black;
		top: 0;

		@include media(max-width $mobile-breakpoint-max) {
			font-size: 1.5625em;
		}
	}

	.carousel__control--next {
		.carousel__control__icon {
			padding-right: .125em;
		}
	}

	.carousel__control--prev {
		.carousel__control__icon {
			padding-left: .125em;
		}
	}

	.inline-media__caption-credits,
	.inline-media__caption-index {
		display: block;
		margin-bottom: $vertical-space-imageblock;
	}

	.inline-media__caption-index {
		float: left;
		font-weight: $font-weight-bold;
		text-align: center;
		width: 100%;
	}

	.inline-media__caption-credits {
		float: right;
	}

	.inline-media__caption-text {
		@include clearfix;
		clear: both;
	}
}

.carousel__controls {
	display: none;
}

.carousel__item--center .carousel__controls {
	display: block;
}

.article .article__body {
	.inline-carousel--text {
		.inline-media__title p {
			font-size: 1.25em;
		}

		.inline-media__text p {
			font-size: 1.25em;
		}

		.inline-media__caption-index {
			font-size: .75em;
		}
	}
}