.prio-1 {
	.teaser-stream-text {
		display: none;
	}

	.teaser-stream-content.teaser-stream-content--noimg {
		.teaser-stream-text {
			display: block;
		}
	}
}

.prio-2 {
	.teaser-stream-text {
		display: none;
	}

	.teaser-stream-content.teaser-stream-content--noimg {
		.teaser-stream-text {
			display: block;
		}
	}
}

.prio-3,
.prio-5,
.prio-6 {
	.hashtag-bar {
		float: left;
	}

	.teaser-stream-text {
		@include span-columns(9);
		@include omega;
		margin-top: 0;
		display: none;
	}

	.teaser-media {
		@include span-columns(3);
		@include omega;
		float: right;
	}

	.teaser__link-image {
		float: right;
	}

	.headline {
		@include span-columns(9);
		@include omega;

		@media (max-width: $mobile-breakpoint-max) {
			@include span-columns(8);
			@include omega;
			font-size: 1.125em;
		}
	}

	.teaser-media__caption {
		display: none;
	}

	.teaser-stream-content.teaser-stream-content--noimg {
		.headline {
			width: 100%;
		}

		.teaser-stream-text {
			display: block;
			width: 100%;
		}
	}
}

.prio-4 {
	.teaser-stream-text {
		display: none;
	}

	.teaser-stream-content.teaser-stream-content--noimg {
		.teaser-stream-text {
			display: block;
		}
	}
}

.teaser-stream .teaser-media .teaser-media__caption {
	display: none;
}

.teaser-stream.prio-1,
.teaser-stream.prio-4,
.teaser-stream.prio-2 {
	.twitter-tweet {
		clear: both;
	}
}

