/* Scribble Live Constants */
$scribble-live-frame-color: #eee;
$scribble-live-headline-color: $color-text-headline-default;
$scribble-live-text-color: $color-article-body;
$scribble-live-text-color-credits: #999;
$scribble-live-decoration-color: $color-emph;

.teaser-stream {
	.scribble-live,
	.scribble-live.inline-block--wide {
		margin-left: 0;
		margin-right: 0;
		margin-top: 0;
		padding: 0 $vertical-space-widgets;
	}
}

.scribble-live {
	opacity: 1;
	* {
		box-sizing: border-box;

		&:before {
			box-sizing: border-box;
		}

		&:after {
			box-sizing: border-box;
		}
	}

	display: block;
	margin: 2em 0;
	position: relative;
	width: 100%;
	height: inherit !important;
	padding-right: $vertical-space-widgets;

	@include media(max-width $mobile-breakpoint-max) {
		padding-right: 0;
	}

	img {
		display: block;
	}

	audio,
	video {
		width: 100%;
		height: auto;
	}

	.marker {
		background: #fff999;
	}

	.pluginSkinLight a,
	.Embed a {
		color: $color-emph !important;
	}

	.scribble-live-title {
		background: $scribble-live-frame-color;
		color: $scribble-live-decoration-color;
		display: inline-block;
		font-family: $font-extra;
		font-size: 20px;
		font-weight: bold;
		text-transform: uppercase;
		line-height: 1.25em;
		padding: 0.5em;
		padding-bottom: 0.35em;
		width: 100%;
		position: relative;

		@include media(max-width $mobile-breakpoint-max) {
			font-size: 18px;
		}

		@include media(max-width $mobile-breakpoint-mini) {
			font-size: 16px;
		}
	}

	.scribble-live-description {
		border: 2px solid $scribble-live-frame-color;
		border-top: none;
		font-size: 14px;
		font-weight: normal;
		padding: 16px;
		color: #666;
		font-family: Arial;
	}

	.scribble-list-content-html {
		font-family: $font-default;
		font-weight: normal;
		line-height: 1.5;

		h3 {
			margin-left: 0;
		}

		a {
			color: $color-decoration-elements;
		}

		a.tweet-url.hashtag {
			font-size: inherit;
		}

		div {
			.twitter-tweet,
			.instagram-media,
			.fb-post {
				margin: 1.5em 0 !important;

				&:first-child {
					margin-top: 0 !important;
				}
			}

			&:last-of-type {
				.twitter-tweet,
				.instagram-media,
				.fb-post {
					margin-bottom: 0 !important;
				}
			}
		}

		img {
			width: 100%;
		}

		blockquote {
			margin: 0;
		}

		blockquote.UserCreated {
			padding-left: 2em;
		}

		.Meta {
			img {
				width: auto;
				display: inline-block;
			}
		}

		ul li,
		ol li {
			border-bottom: none;
			line-height: 1.5em;
			list-style: inherit;
			margin: 0;
			min-height: inherit;
			padding: 0;

			&:before {
				display: none;
			}
		}

		ul li {
			list-style-type: disc;
		}
	}

	.scribble-list-time {
		background: #fff;
		border: 2px dotted $scribble-live-frame-color;
		border-radius: 8px;
		color: $scribble-live-decoration-color;
		font-family: $font-extra;
		font-size: 12px;
		font-weight: bold;
		left: -40px;
		padding: 0.5em;
		position: absolute;
		text-align: center;

		.scribble-date,
		.scribble-time {
			display: block;
			line-height: 1.5em;
		}

		@include media(max-width $mobile-breakpoint-max) {
			border: none;
			font-size: 14px;
			font-weight: bold;
			left: inherit;
			padding: 0.5em;
			position: relative;
			text-align: left;

			.scribble-date {
				display: inline-block;
			}

			.scribble-time {
				display: inline-block;
				margin-left: 3px;
			}
		}
	}

	.scribble-list-content {
		max-width: 650px;
		opacity: 1;
		transition: opacity .5s;

		@include media(max-width $mobile-breakpoint-max) {
			margin-left: 0.5em;
			padding: 0;
		}
	}

	.scribble-load-more {
		background: $scribble-live-decoration-color;
		color: #fff;
		cursor: pointer;
		font-family: $font-extra;
		text-transform: uppercase;
		display: inline-block;
		padding: 0.5em 5em;
		margin-top: 1em;
		margin: auto 0;
		opacity: 1;
		transition: opacity .3s;

		@include media(max-width $mobile-breakpoint-max) {
			width: 100%;
		}

		&:hover {
			opacity: .8;
		}
	}

	.scribble-list-content-image {
		img {
			width: 100%;
		}
	}

	.scribble-list-content-video {
		background: #999;
		padding-bottom: 56.25%;
		padding-top: 0px;
		position: relative;
		overflow: hidden;
		height: 0;
		width: 100%;

		@include media(max-width $mobile-breakpoint-max) {
			width: inherit;
		}


		.embed-video,
		.youtubePlayer {
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			height: 100%;
			width: 100%;
		}
	}

	.scribble-load-more-outer {
		text-align: center;
		margin-top: 1em;
	}

	ul.scribble-live-list {
		border-left: 2px solid $scribble-live-frame-color;
		margin: 0;
		margin-left: 2em;
		min-height: 5em;
		padding-left: 0;

		@include media(max-width $mobile-breakpoint-max) {
			margin-left: 0;
		}

		li {
			border-bottom: 2px solid $scribble-live-frame-color;
			font-size: 16px;
			list-style: none;
			margin: 0;
			min-height: 5em;
			padding: 1em 0;
			position: relative;
			padding-left: 48px;

			@include media(max-width $mobile-breakpoint-max) {
				font-size: 15px;
				padding: 0.5em 0;
				padding-left: 0;
			}

			&:before {
				background: #fff;
				border: 2px solid $scribble-live-frame-color;
				border-radius: 50%;
				content: " ";
				height: 8px;
				width: 8px;
				position: absolute;
				bottom: -5px;
				left: -5px;
			}
		}

		.scribble-list-content-html ul li,
		.scribble-list-content-html ol li {
			list-style: initial;
			min-height: inherit;
			padding: inherit;
			border: none;
			list-style-type: inherit;
			padding-left: 0;
		}
	}

	.scribble-list-content-audio,
	.scribble-list-content-video,
	.scribble-list-content-image {
		margin-bottom: 1em;
	}

	.scribble-list-content-credits,
	.scribble-list-content p.Caption,
	.scribble-list-content-html figcaption {
		color: #666;
		font-style: italic;
		font-weight: normal;
		padding: 0.5em 0;
		margin: 0;
	}

	.scribble-list-content-credits {
		color: $scribble-live-text-color-credits;
		display: table;
		font-style: italic;
		font-size: 12px;
		padding-bottom: 0;

		.scribble-list-content-avatar {
			float: left;
			width: 35px;
			vertical-align: baseline;
			border-radius: 50%;
			margin-top: 0.5em;
			overflow: hidden;

			img {
				width: 35px;
				height: 35px;
			}
		}

		.scribble-list-content-author {
			float: left;
			line-height: 35px;
			margin-top: 0.5em;
			padding-left: 0.5em;
		}

		a {
			color: #666;
		}
	}

	.twitter-tweet {
		margin: 0 !important;
	}

	iframe {
		width: 100%;
	}

	.fb_iframe_widget {
		width: 100% !important;
	}

	.twitter-tweet {
		margin: 0;
		width: 100% !important;
	}

	.scribble-list-item.scribble-loader {
		background: url("../img/lazyloader.svg");
		background-repeat: no-repeat;
		background-position: 50% 50%;
		background-size: 30px 30px;
	}

	.scribble-live-wrapper .scribble-list-content,
	.new .scribble-list-content {
		opacity: 1;
		transition: opacity 1.5s;
	}

	.scribble-live-wrapper .scribble-list-content.scribble-animate,
	.new.scribble-animate .scribble-list-content {
		opacity: 0;
		max-height: 200px;
	}

	.hide-element {
		opacity: 0;
	}

	.dots-after {
		border-left: 2px solid $scribble-live-frame-color;
		display: inherit;
		margin-left: 38px;
		margin-top: 3px;
		padding-top: 1em;
		position: relative;
		z-index: 0;

		@include media(max-width $mobile-breakpoint-max) {
			margin-left: 0;
		}

		&:before {
			background: #fff;
			border: 2px solid $scribble-live-frame-color;
			border-radius: 50%;
			bottom: -5px;
			content: " ";
			height: 8px;
			left: -5px;
			position: absolute;
			width: 8px;
		}
	}

	.scribble-list-content-html {
		blockquote {
			background: #fff;
			color: #666;
			display: block;
			font-family: Georgia, serif;
			font-size: 16px;
			line-height: 1.2;
			margin: 0 0 20px;
			padding: 15px 20px 15px 45px;
			position: relative;
			text-align: justify;
			box-shadow: 2px 2px 10px #eee;
		}

		blockquote::before {
			content: "\201C"; /*Unicode for Left Double Quote*/
			color: $scribble-live-text-color-credits;
			font-family: Georgia, serif;
			font-size: 60px;
			font-weight: bold;
			left: 10px;
			position: absolute;
			top: 5px;
		}

		blockquote::after {
			content: "";
		}

		blockquote a {
			background: $scribble-live-frame-color;
			cursor: pointer;
			text-decoration: none;
			padding: 0 3px;
		}

		blockquote a:hover {
			color: #666;
		}

		blockquote em {
			font-style: italic;
		}

		blockquote.twitter-tweet:before,
		blockquote.instagram-media:before {
			content: "";
		}
	}

	figure {
		margin: 0;
	}

	.two-col-left,
	.two-col-right,
	.row.two-col,
	.row.three-col {
		@include span-columns(12);
		@include omega;
	}

	.two-col-right {
		.col-sidebar {
			@include span-columns(4);

			@include media(max-width $mobile-breakpoint-max) {
				@include span-columns(12);
			}
		}

		.col-main {
			@include span-columns(8);

			@include media(max-width $mobile-breakpoint-max) {
				@include span-columns(12);
			}
		}
	}

	.two-col-left {
		.col-main {
			@include span-columns(8);

			@include media(max-width $mobile-breakpoint-max) {
				@include span-columns(12);
			}
		}

		.col-sidebar {
			@include span-columns(4);

			@include media(max-width $mobile-breakpoint-max) {
				@include span-columns(12);
			}
		}
	}

	.row.two-col {
		.col-1 {
			@include span-columns(6);

			@include media(max-width $mobile-breakpoint-max) {
				@include span-columns(12);
			}
		}

		.col-2 {
			@include span-columns(6);

			@include media(max-width $mobile-breakpoint-max) {
				@include span-columns(12);
			}
		}
	}

	.row.three-col {
		.col-1 {
			@include span-columns(4);

			@include media(max-width $mobile-breakpoint-max) {
				@include span-columns(12);
			}
		}

		.col-2 {
			@include span-columns(4);

			@include media(max-width $mobile-breakpoint-max) {
				@include span-columns(12);
			}
		}

		.col-3 {
			@include span-columns(4);

			@include media(max-width $mobile-breakpoint-max) {
				@include span-columns(12);
			}
		}
	}

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		&:first-child {
			margin-top: 0;
		}
	}

	h1 {
		font-size: 24px;
		font-family: Arial;
		line-height: 1.3em;
		@include media(max-width $mobile-breakpoint-max) {
			font-size: 20px;
		}
	}

	h2 {
		font-size: 20px;
		@include media(max-width $mobile-breakpoint-max) {
			font-size: 18px;
		}
	}

	h3 {
		font-size: 16px;
		@include media(max-width $mobile-breakpoint-max) {
			font-size: 15px;
		}
	}

	.instagram-media {
		width: calc(90% - 5px);
	}

	.EmbeddedTweet {
		max-width: 100% !important;
	}

	.fb_iframe_widget span {
		width: 100% !important;
	}

	.fb_iframe_widget iframe {
		min-width: 0 !important;
	}
}

.article .article__body {
	.scribble-live {
		h1,
		h2,
		h3,
		h4,
		h5,
		h6 {
			color: $scribble-live-headline-color;
			line-height: inherit;
			margin: 0.5em 0;
			margin-left: 0;
			padding: 0;
		}

		p {
			padding: 0;
		}

		ul.scribble-live-list {
			margin: 0;
			margin-left: 38px;
			min-height: 5em;
			padding-left: 0;
			position: relative;
			z-index: 1;

			@include media(max-width $mobile-breakpoint-max) {
				margin-left: 0;
			}
		}

		iframe.youtubePlayer {
			margin-bottom: 0 !important;
			display: block;
		}

		.scribble-list-content-html {
			p {
				font-family: $font-default;
				font-weight: normal;
				font-size: 16px;
				margin: 0.5em 0;
				width: 100%;

				@include media(max-width $mobile-breakpoint-max) {
					font-size: 15px;
				}
			}
		}

		.fb-post iframe {
			transform-origin: 0 0;
			transform: initial !important;
			width: 100% !important;
		}
	}

	@keyframes blinker {
		50% { opacity: 0; }
	}

	.scribble-live-state {
		font-size: 12px;
		position: absolute;
		top: 0;
		right: 0;
		background: #fff;
		border: 2px solid $scribble-live-frame-color;
		display: inline-block;
		padding: 0 0.25em 0 0.5em;
		border-radius: 0 0 0 8px;
		text-transform: uppercase;

		&:after {
			color: $scribble-live-decoration-color;
			font-size: 2.5em;
			content: "";
			width: 10px;
			height: 10px;
			text-transform: uppercase;
			vertical-align: middle;
			margin-left: 2px;
			animation: blinker 1s linear infinite;
		}
	}

	.twitter-tweet::shadow .EmbeddedTweet,
	.twitter-tweet .EmbeddedTweet {
		max-width: 1000px;
	}

	@keyframes blinkerloadmore {
		50% { opacity: 50; }
	}

	.scribble-live-loading {
		animation: blinkerloadmore 0.5s linear infinite;
	}
}
