/*
 * @name: block-header
 *
 * @description: header for section blocks
 *
 */

// TODO: Constants
.block-header {
	border-bottom: 2px solid;
	font-size: 1.125em;
	margin-bottom: .5em;
	padding: .5em 0;

	@include media(max-width $mobile-breakpoint-max) {
		padding: .5em 1em;
	}
}

// TODO: Grid!

.collapsable .block-header__icon:before {
	@include icon;
	@include icon--arrow-right;
}

.block-header--small {
	border: none;
	position: relative;

	&::after {
		border-bottom: 2px solid;
		bottom: -4px;
		content: "";
		height: 0;
		left: 0;
		position: absolute;
		width: 13.33333%;

		@include media(max-width $mobile-breakpoint-max) {
			width: 100%;
		}
	}
}

.quiz .block-header--small {
	&::after {
		@include media(max-width $mobile-breakpoint-max) {
			width: 13.33333%;
		}
	}
}

.block-header__icon {
	position: relative;

	&::before {
		position: absolute;
		right: -15px;
	}
}
