// Teaser Carousel
.teaser-carousel {
	&.carousel-ajax {

		// Swiper berschreibungen fr Teaser Carousel
		&.swiper-container {
			padding-bottom: $vertical-space-widgets;
			margin-top: $vertical-space-widgets;

			@include media(max-width $mobile-breakpoint-max) {
				margin-left: $vertical-space-widgets-half;
				margin-right: $vertical-space-widgets-half;
			}
		}

		.swiper-buttons {
			font-size: 36px;
			color: $color-decoration-elements;
			padding: 0;
		}

		.swiper-pagination {
			display: block;
		}

		.swiper-pagecount {
			display: none;
			height: 0;
		}

		li.swiper-slide {
			padding: 0 2em;
		}

		// Spezielle berschreibungen fr Teaser Elemente im Carousel
		.teaser-media {
			.teaser-media__caption-text {
				a {
					color: #000;
					font-size: 16px;
				}
			}

			.teaser-media__caption {
				margin-bottom: 1em;
				width: 100%;
			}
		}
	}
}

// Artikelspezifisch
.article {
	.teaser-carousel {
		&.carousel-ajax {
			p {
				a {
					color: inherit;
					border-bottom: none;

					&:hover {
						text-decoration: underline;
					}
				}
			}
		}
	}
}
