.comments-area {
	clear: both;
	margin-bottom: 1em !important;
	margin-right: $vertical-space-widgets;

	@media (max-width: $mobile-breakpoint-max) {
		margin-right: 0;
	}

	.showDisqus {
		border-top: 1px solid;
		clear: both;
		cursor: pointer;
		color: $black;
		font-family: $font-extra;
		font-weight: bold;
		line-height: 3em;
		margin: 0.5em 0;
		text-align: left;
	}

	.disqus-comment-count {
		background: $color-emph;
		border-radius: 5px;
		color: $white;
		font-family: $font-extra;
		font-weight: bold;
		padding: 5px 15px;
		margin-right: 0.5em;
		position: relative;
		text-align: center;

		&:before {
			border-color: transparent $color-emph;
			border-style: solid;
			border-width: 0 0 10px 10px;
			bottom: -10px;
			content: "";
			position: absolute;
			right: 10px;
		}
	}
}

.article--type-video,
.article--type-gallery {
	.comments-area {
		.disqus-comment-button {
			color: #fff;
		}
	}
}