/*
 * @name: teaser
 *
 * @description: basic teaser
 *
 */
$angle-size: #{1em * $bigger-lineheight};
$angle-space: #{1em * 2};

.teaser {

	overflow: hidden;
	position: relative;

	@include media(max-width $mobile-breakpoint-max) {
		margin-left: $space-innerpage;
		margin-right: $space-innerpage;
	}

	a:hover .teaser__headline {
		text-decoration: underline;
	}

	.more {
		border-bottom: 1px solid $color-teaser-border;
		color: $color-teaser-link;
		display: inline;
		font-family: $font-family-meta;
		font-weight: $font-weight-bold;

		&:hover {
			border-bottom-color: $color-teaser-link;
			text-decoration: none;
		}
	}

	.teaser__header {
		font-size: 1em;
		margin: 0;
	}

	.teaser__tag {
		display: inline-block;
		float: left;
		font-family: $font-family-meta;
		font-size: .75em;
		font-weight: $font-weight-light;
		margin-right: .4166666667em;
		padding: $vertical-space;
		text-transform: uppercase;

		@include media(max-width $mobile-breakpoint-max) {
			font-size: .625em;
			margin-right: .3em;
		}
	}

	.teaser__body__text {
		display: inline;
		line-height: $default-lineheight;
	}

	&.updated .teaser__tag {
		background-color: $red;
		color: $white;
	}

	&.advertisement .teaser__tag {
		background-color: $light-grey;
		color: $white;
	}

	.teaser.advertisement {
		@include media(max-width $mobile-breakpoint-max) {
			margin: 0;
			padding: .625em;
		}
	}

	&.city-report {
		.teaser__tag {
			background-color: #90ee90;
			color: $dark-grey;
		}

		.teaser__body__text {
			display: none;
		}

		.more {
			@include span-columns(8 of 12);
			@include shift(4);
			@include omega;
			@include clearfix;
			border-bottom: none;
			border-top: 3px solid #90ee90;
			bottom: 2em;
			color: $dark-grey;
			display: block;
			height: 2em;
			left: 0;
			line-height: 1.875em;
			padding-top: .375em;
			position: absolute;
		}

		.more:before {
			@include icon;
			@include icon--arrow-circle-right;
			font-size: 1.875em;
		}
	}
}

.teaser .teaser__img {
	display: inline-block;
}

.teaser .teaser__img img,
.teaser .teaser__img.teaser__img--vertical {
	@include span-columns(12 of 12);
	@include clearfix;
	@include omega;
	display: block;
	height: auto;
}

.teaser .teaser__img--vertical + * {
	clear: both;
}

.teaser .teaser__img--left,
.teaser .teaser__img--right,
.teaser .teaser__img--right--round {
	@include span-columns(4 of 12);
	margin-bottom: .25em;

	+ .teaser__header {
		@include span-columns(8 of 12);
	}
}

.teaser .teaser__img.teaser__img--right {
	float: right;
	margin-right: 0;
}

.teaser .teaser__identifier {
	@include span-columns(1 of 12);

	+ .teaser__header {
		@include span-columns(9 of 12);
		@include omega;
	}

	.teaser__identifier__icon {
		&:before {
			@include icon;
			@include icon--arrow-circle-right;
		}
		font-size: .875em;
	}
}

.teaser .teaser__img--right {
	float: right;
}

.teaser .teaser__img--vertical {
	margin-bottom: $vertical-space-imageblock;
}

.teaser .teaser__img--left + .teaser__header,
.teaser .teaser__img--right {
	@include omega;
	@include clearfix;
}

.teaser .teaser__header {
	font-weight: normal;
	overflow: hidden;
	width: 100%;
}

.teaser .teaser__headline {
	clear: both;
	color: $color-teaser-headline;
	display: block;
	font-weight: normal;
	line-height: $default-lineheight;
	margin-bottom: $vertical-space;
}

.teaser .teaser__kicker {
	padding: $vertical-space 0;
}

.teaser .teaser__kicker,
.teaser .teaser__date,
.teaser .teaser__time {
	color: $color-teaser-kicker;
	display: inline-block;
	font-family: $font-family-meta;
	font-size: $article-heroimage-credits-fontsize;
	font-weight: $font-weight-light;
	text-transform: uppercase;
}

.teaser .teaser__kicker {
	float: left;
}

.content__column {
	margin-bottom: $vertical-space-section;
}

.collapsable__content {
	clear: both;
}
