/**
 * this scss partial contains various helpers that can help us avoid
 *
 * a) inline styles
 * b) repeating similar rules over and over again
 *
 * these classes cover rather basic rules only. the rules are important.
 * most of them follow the structure %property%-%value%{-%view%}
 *
 * this file has been merged with the rules from
 * * scss/_common/trumps/_trumps.scss
 * * scss/_common-nrw/trumps
 *
 * Documentation:
 * https://sconfluence.funkemedien.de/display/FUNDigital/CSS+Helper+Classes
 */

/////////////////////////////////////////////////////////////////////////////////
//                                   display                                   //
/////////////////////////////////////////////////////////////////////////////////
.display {
	@mixin display-helper($value) {
		&-#{$value} {
			display: $value !important;

			@include media(max-width $mobile-breakpoint-max) {
				&-mobile {
					display: $value !important;
				}
			}
			@include media(min-width $mobile-breakpoint-min) {
				&-desktop {
					display: $value !important;
				}
			}
		}
	}

	@include display-helper(block);
	@include display-helper(inline-block);
	@include display-helper(inline);
	@include display-helper(flex);
}

.mobile-hide {
	@include media(max-width $mobile-breakpoint-max) {
		display: none !important;
	}
}

.desktop-hide {
	@include media(min-width $mobile-breakpoint-min) {
		display: none !important;
	}
}

.mobile-only {
	display: none !important;

	@include media(max-width $mobile-breakpoint-max) {
		display: initial !important;
	}
}

.desktop-only {
	display: initial !important;

	@include media(max-width $mobile-breakpoint-max) {
		display: none !important;
	}
}

/////////////////////////////////////////////////////////////////////////////////
//                                   margin                                    //
/////////////////////////////////////////////////////////////////////////////////
.margin {
	@mixin margin-helper($amount) {
		&-#{$amount} {
			margin: $amount !important;

			@include media(max-width $mobile-breakpoint-max) {
				&-mobile {
					margin: $amount !important;
				}
			}
			@include media(min-width $mobile-breakpoint-min) {
				&-desktop {
					margin: $amount !important;
				}
			}
		}
	}
	@mixin margin-side-helper($side, $amount) {
		&-#{$side} {
			&-#{$amount} {
				margin-#{$side}: $amount !important;

				@include media(max-width $mobile-breakpoint-max) {
					&-mobile {
						margin-#{$side}: $amount !important;
					}
				}
				@include media(min-width $mobile-breakpoint-min) {
					&-desktop {
						margin-#{$side}: $amount !important;
					}
				}
			}
		}
	}

	@include margin-helper(0);
	@include margin-side-helper(top, 0);
	@include margin-side-helper(right, 0);
	@include margin-side-helper(bottom, 0);
	@include margin-side-helper(left, 0);

	&-centered {
		margin-left: auto !important;
		margin-right: auto !important;
	}
}

.vspace-desktop {
	@include media(min-width $mobile-breakpoint-min) {
		margin-bottom: 1.25em !important;
	}
}

.vspace-mobile {
	@include media(max-width $mobile-breakpoint-max) {
		margin-bottom: 1.25em !important;
	}
}

.no-vspace {
	margin-bottom: 0 !important;
	margin-top: 0 !important;
}

.no-vspace-desktop {
	@include media(min-width $mobile-breakpoint-min) {
		margin-bottom: 0 !important;
		margin-top: 0 !important;
	}
}

.no-vspace-mobile {
	@include media(max-width $mobile-breakpoint-max) {
		margin-bottom: 0 !important;
		margin-top: 0 !important;
	}
}

