/////////////////////////////////////////////////////////////////////////////////
//                                   padding                                   //
/////////////////////////////////////////////////////////////////////////////////
.padding {
	@mixin padding-helper($amount) {
		&-#{$amount} {
			padding: $amount !important;
		}
		@include media(max-width $mobile-breakpoint-max) {
			&-mobile {
				&-#{$amount} {
					padding: $amount !important;
				}
			}
		}
		@include media(min-width $mobile-breakpoint-min) {
			&-desktop {
				&-#{$amount} {
					padding: $amount !important;
				}
			}
		}
	}
	@mixin padding-side-helper($side, $amount) {
		&-#{$side} {
			&-#{$amount} {
				padding-#{$side}: $amount !important;
			}
			@include media(max-width $mobile-breakpoint-max) {
				&-mobile {
					&-#{$amount} {
						padding-#{$side}: $amount !important;
					}
				}
			}
			@include media(min-width $mobile-breakpoint-min) {
				&-desktop {
					&-#{$amount} {
						padding-#{$side}: $amount !important;
					}
				}
			}
		}
	}

	@include padding-helper(0);
	@include padding-side-helper(top, 0);
	@include padding-side-helper(right, 0);
	@include padding-side-helper(bottom, 0);
	@include padding-side-helper(left, 0);

	&-trbl {
