	}

	&-t {
		padding-top: $default-indent-mobile !important;
	}

	&-r {
		padding-right: $default-indent-mobile !important;
	}

	&-b {
		padding-bottom: $default-indent-mobile !important;
	}

	&-l {
		padding-left: $default-indent-mobile !important;
	}

	&-trbl-desktop {
		@include media(min-width $mobile-breakpoint-min) {
			padding: $default-indent-mobile !important;
		}
	}

	&-tb-desktop {
		@include media(min-width $mobile-breakpoint-min) {
			padding-top: $default-indent-mobile !important;
			padding-bottom: $default-indent-mobile !important;
		}
