}

/////////////////////////////////////////////////////////////////////////////////
//                                   floating                                  //
/////////////////////////////////////////////////////////////////////////////////
.float {
	@mixin float-helper($direction) {
		&-#{$direction} {
			float: $direction !important;

			@include media(max-width $mobile-breakpoint-max) {
				&-mobile {
					float: $direction !important;
				}
			}
			@include media(min-width $mobile-breakpoint-min) {
				&-desktop {
					float: $direction !important;
				}
			}
		}
	}

	@include float-helper(left);
	@include float-helper(right);
	@include float-helper(none);
}
.clearfix {
	@include clearfix;

	@include media(max-width $mobile-breakpoint-max) {
		&-mobile {
			@include clearfix;
		}
	}
	@include media(min-width $mobile-breakpoint-min) {
		&-desktop {
			@include clearfix;
		}
	}
}

