// ToDo place this in Custoum Sticky Class
@keyframes videoStickyCloseButton {
	0% {
		transform: scale(1);
		opacity: 0;
	}
	50% {
		opacity: 1;
		color: $color-emph;
	}
	100% {
		transform: scale(2);
		opacity: 0;
	}
}

.video-placeholder {
	clear: both;
	height: 0;
	position: relative;

	&.video-placeholder--styled {
		background: #f3f3f3;
		text-align: center;

		&:before {
			@include icon;
			@include icon--video;
			animation: videoStickyCloseButton 1s infinite;
			color: #f3f3f3;
			font-size: 3rem;
			margin-top: -2rem;
			position: absolute;
			text-shadow: 0 0 5px #fff;
			top: 50%;
		}
	}
}

.video-transition {
	&.video-opener,
	&.video-inline {
		display: block;
		transform-origin: center center;
		width: calc(100% - 0px);
		will-change: width;
		transition: all 0.5s;
	}
}

.player-fixed {
	background: transparent;
	max-width: 940px;
	position: fixed;
	z-index: 100000;

	.video-autoplay {
		display: none;
	}

	.hero-media__caption-datum {
		display: none;
	}

	&.player-fixed--desktop {
		border-bottom: none;
		overflow: visible;
		top: 0;
	}

	&.player-fixed--mobile {
		background: $color-emph;
		color: #fff;
		padding: 0;
		padding-bottom: 0;
		top: $vertical-space-widgets-half;

		@media (max-width: $mobile-breakpoint-max) {
			top: 55px;
		}

		.video-brightcove,
		.player_brightcove {
			@include span-columns(3 of 12);

			@media (max-width: $mobile-breakpoint-max) {
				@include span-columns(6 of 12);
			}
		}

		figcaption {
			@include span-columns(9 of 12);
			padding: 1rem;
			padding-left: 0;

			@media (max-width: $mobile-breakpoint-max) {
				@include span-columns(6 of 12);
			}
		}

		.player_brightcove {
			.video-brightcove {
				width: 100%;
			}
		}
	}

	figcaption {
		.hero-media__caption-text,
		.inline-media__caption-text {
			color: #fff;
			font-weight: bold;
			font-size: 1.4rem;
			margin-bottom: 0;
			padding-right: 1em;
		}
	}

	.player_brightcove_rendered {
		margin-bottom: 0 !important;

		@media (max-width: $mobile-breakpoint-max) {
			margin-bottom: 0 !important;
		}
	}
}

.player-fixed--close {
	display: inline-block;
	right: 1rem;
	top: 1rem;
	position: absolute;
	width: 20px;
	height: 20px;
	z-index: 1;

	@media (max-width: $mobile-breakpoint-max) {
		right: 0.5rem;
		top: 0.5rem;
	}

	&:before {
		@include icon;
		@include icon--close;
		color: #fff;
		cursor: pointer;
		font-size: 18px;
		transition: all 0.4s ease-out;
		will-change: auto;
		z-index: 999;

		@include media(max-width $mobile-breakpoint-max) {
			font-size: 14px;
		}
	}

	&:hover {
		background: $color-emph !important;

		&:before {
			animation: text-shadow-animation 0.4s; /* IE 10+, Fx 29+ */
			color: #fff;
			transform: rotate(360deg);
		}
	}
}

.article {
	.hero-img {
		&.player-fixed--desktop {
			padding-bottom: 0;
			min-width: 200px;
			margin-bottom: 0;

			&.player-fixed-hover {
				&:hover {
					min-width: 700px;
				}
			}

			figcaption {
				display: none;
			}

			@media (max-width: $mobile-breakpoint-max) {
				padding-bottom: 0;
			}
		}

		&.player-fixed--mobile {
			padding-bottom: 0;

			@media (max-width: $mobile-breakpoint-max) {
				left: 0;
				transform: translate3d(0px, 0px, 0px) !important;
			}

			&.video-opener {
				.hero-media__caption-text {
					overflow: hidden;
					display: -webkit-box;
					/* stylelint-disable */
					-webkit-box-orient: vertical;
					-webkit-line-clamp: 3;
					/* stylelint-enable */

					@include media(max-width $mobile-breakpoint-max) {
						font-size: 1rem;
						/* stylelint-disable */
						-webkit-line-clamp: 5;
						/* stylelint-enable */
					}

					@include media(max-width $mobile-breakpoint-medium-max) {
						font-size: 0.85rem;
						/* stylelint-disable */
						-webkit-line-clamp: 4;
						/* stylelint-enable */
					}

					@include media(max-width $mobile-breakpoint-mini) {
						font-size: 0.75rem;
						/* stylelint-disable */
						-webkit-line-clamp: 3;
						/* stylelint-enable */
					}
				}
			}
		}
	}

	.video-inline {
		&.player-fixed--desktop {
			padding: 0;
			padding-bottom: 0;
			min-width: 200px;
			margin-bottom: 0;

			&.player-fixed-hover {
				&:hover {
					min-width: 700px;
				}
			}

			figcaption {
				display: none;
			}

			@media (max-width: $mobile-breakpoint-max) {
				padding-bottom: 0;
			}
		}

		&.player-fixed--mobile {
			@include span-columns(12 of 12);
			margin-right: 0;

			@media (max-width: $mobile-breakpoint-max) {
				width: 100%;
				left: 0;
				transform: translate3d(0px, 0px, 0px) !important;
			}

			.inline-media__caption-text {
				overflow: hidden;
				display: -webkit-box;
				/* stylelint-disable */
				-webkit-box-orient: vertical;
				-webkit-line-clamp: 3;
				/* stylelint-enable */

				@include media(max-width $mobile-breakpoint-max) {
					font-size: 1rem;
					/* stylelint-disable */
					-webkit-line-clamp: 5;
					/* stylelint-enable */
				}

				@include media(max-width $mobile-breakpoint-medium-max) {
					font-size: 0.85rem;
					/* stylelint-disable */
					-webkit-line-clamp: 4;
					/* stylelint-enable */
				}

				@include media(max-width $mobile-breakpoint-mini) {
					font-size: 0.75rem;
					/* stylelint-disable */
					-webkit-line-clamp: 3;
					/* stylelint-enable */
				}
			}

			.inline-media .inline-media__caption {
				line-height: inherit;
			}
		}
	}
}
