$font-size-headline-l: 54px;
$font-size-headline-m: 32px;
$font-size-headline-s: 24px;
$font-size-headline-mobile-l: 36px;
$font-size-headline-mobile-m: 30px;
$font-size-headline-mobile-s: 24px;
$font-size-text-default: 18px;
$font-size-text-small: 14px;
$font-size-extras-big: 36px;
$font-size-extras-medium-big: 21px;
$font-size-extras-medium-bigger: 23px;
$font-size-extras-medium-biggest: 46px;
$font-size-extras-medium: 16px;
$font-size-extras-small: 13px;
$font-size-extras-tiny: 12px;
$font-default: Arial, Helvetica, Verdana, sans-serif;
$font-headline: "Lato", Arial, sans-serif;
$font-family-meta: Arial, sans-serif;

// Still needed? (mkle)
$font-extra: Arial, Helvetica, Verdana, sans-serif;



/* Colours */
$color-publication: #fff !default;
$black: #000;
$black-transparent: rgba(0, 0, 0, .5);
$dark-grey: #222;
$lighter-grey: #666;
$light-grey: #888;
$grey: #bfbfbf;
$nearly-lightest-grey: #d6d6d6;
$lightest-grey: #f0f0f0;
$teaser-border: #ebebeb;
$white: #fff;
$green: #4a7e3e;
$red: #8a1213;
$light-red: #c00000;
$facebook-dark: #3b5998;
$facebook-light: #9daccb;
$twitter-dark: #00aced;
$twitter-light: #7fd5f6;
$google-plus-dark: #d14836;
$google-plus-light: #e8a39a;
$whatsapp-dark: #64d448;

/* responsive breakpoints */

// Permanent! Use Always!
// also change for jquery-pin minWidth!
$mobile-breakpoint: 768px;
$mobile-breakpoint-max: 767px;
$mobile-breakpoint-min: 768px;
$mobile-breakpoint-medium-max: 549px;
$mobile-breakpoint-mini: 400px;

/* General widths */
$pagewidth: 960px;

/* fontsize */
$article-heading-fontsize: 46px;
$mobile-article-heading-fontsize: 26px;
$article-header-fontsize: 2em;
$article-header-fontsize-mobile: 1.4375em;
$article-author-fontsize: 1em;
$article-intro-fontsize: 1.375em;
$article-text-fontsize: $article-author-fontsize;
$article-text-headline-fontsize: 1.25em;
$article-heroimage-credits-fontsize: .75em;

/* line-heights */
$default-lineheight: 1.4;
$medium-lineheight: 1.25;
$bigger-lineheight: 1.65;
$smaller-lineheight: 1;

/* font-weight */
$font-weight-regular: 300;
$font-weight-bold: 600;
$font-weight-normal: 700;
$font-weight-light: 100;
$font-weight-fat: 900;

/* spaces */
$vertical-space: 4px;
$vertical-space-swiper: .625em;
$vertical-space-swiper-big: 2em;
$vertical-space-swiper-biggest: 3em;
$vertical-space-imageblock: .625em;
$vertical-space-widgets: 16px;
$vertical-space-widgets-half: 8px;
$vertical-space-widgets-mobile-double: 20px;
$vertical-space-widgets-mobile: 10px;
$vertical-space-widgets-mobile-half: 5px;
$vertical-space-section: 2.5em;
$space-innerpage: 0;

// Transition
$transition-fast: all .2s cubic-bezier(.25, .1, .25, 1);
$transition-medium: all .4s cubic-bezier(.25, .1, .25, 1);

/* Buttons load-more, read-more */
$buttons-more-background: #f60940;
$buttons-more-color: #fff;
$buttons-more-font: $font-headline;
$buttons-more-font-size: 1.5em;
$buttons-more-font-weight: bold;
$buttons-more-vertical-space: 40px;
$buttons-more-vertical-space-mobile: 20px;

// Swiper Colors
$color-swiper-buttons: #f60940;
$color-swiper-bullets: #bdd8ba;
$color-swiper-bullets-active: #4a7e3e;
$color-swiper-caption-text: #666;
$color-swiper-caption-credits: #666;
$color-swiper-pagecount: #444;
$color-swiper-pagecount-border: #666;