// Longform Article Infobox

.theme-longform {
	.article--type-longform {

		&.article {
			.article__field__multi-infobox {
				.article__header__heading {
					font-size: 16px;
					padding: 0;
				}
			}
		}

		.article__body {
			.article__field__multi-infobox {
				display: table;
				margin: auto;
				width: $longform-split-column-smale;


				@include media(max-width $mobile-breakpoint-max) {
					float: none;
					margin: $element-space-large auto;
					width: 100%;
					padding: 0 $element-space-mobile;
				}

				h1,
				h2,
				h3,
				p {
					font-size: 16px;
					width: 100%;

					@include media(max-width $mobile-breakpoint-max) {
						font-size: 14px;
					}
				}
				.article__header__intro {
					.article__header__intro__text {
						@include media(max-width $mobile-breakpoint-max) {
							font-size: 14px;
						}
					}
				}

				p {
					margin: 0;
					padding: 0.25em 0;
				}

				.article__header__inner {
					margin-left: 0;
					width: 100%;
				}

				.article__header__headline,
				.article__header__headline__heading {
					color: #666;
					font-style: italic;
					font-weight: bold;
					border-bottom: 1px solid $longform-decoration-color;
					padding: 0.25em 0;
					margin: 0;
				}

				.article__header__intro {
					.article__header__intro__text {
						color: #999;
						font-weight: normal;
						font-style: italic;
					}
				}

				.teaser-article-summary {
					padding: 0;
					.teaser-article-summary-headline {
						color: #999;
						font-weight: normal;
						font-style: italic;
					}
				}

				&.article__field__multi-infobox_left {
					float: left;
					margin-right: 1.5em;
					margin-left: 4%;
					width: 25%;

					@include media(max-width $mobile-breakpoint-max) {
						float: none;
						margin: $element-space-large auto;
						width: 100%;
						padding: 0 $element-space-mobile;
					}
				}

				&.article__field__multi-infobox_right {
					float: right;
					margin-left: 1.5em;
					margin-right: 4%;
					width: 25%;


					@include media(max-width $mobile-breakpoint-max) {
						float: none;
						margin: $element-space-large auto;
						width: 100%;
						padding: 0 $element-space-mobile;
					}
				}
			}
		}
	}
}