/*
 * grid
 */

// TODO: Is the Background-color specified ?

.page-wrapper {
	@include media(min-width $mobile-breakpoint-min) {
		margin: 0 auto;
		width: $pagewidth;
	}
}

.page {
	@include outer-container;

	@include media(min-width $mobile-breakpoint-min) {
		width: $pagewidth;
	}

	@include media(max-width $mobile-breakpoint-max) {
		padding: 0;
	}
}

.main {
	@include outer-container;
	clear: both;
}

.content-wide {
	@include span-columns(12);
	@include omega();

	.content-wide__column {
		@include span-columns(12 of 12);
	}

	.content-wide__column--half {
		@include span-columns(6 of 12);

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
			@include clearfix;
			@include omega;
		}
	}
}

.content-quad {
	@include span-columns(12);
	@include omega();

	.content-quad__column {
		@include span-columns(3 of 12);

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
			@include omega();
		}
	}

	.content-quad__column-two {
		@include span-columns(2 of 12);

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
			@include omega();
		}
	}

	.content-quad__column-four {
		@include span-columns(4 of 12);

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
			@include omega();
		}
	}

	.content-quad__column-six {
		@include span-columns(6 of 12);
		@include omega();

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
			@include omega();
		}
	}
}

.content-triple {
	@include span-columns(12);
	@include omega();

	.content-triple__column {
		@include span-columns(4 of 12);

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
			@include omega();
		}
	}
}

.content-double {
	@include span-columns(12);
	@include omega();

	.content-double__column {
		@include span-columns(6 of 12);

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
			@include omega();
		}
	}
}

.content-double-table {
	@include span-columns(12);
	@include row();
	display: table;
	@include omega();
	margin: -1.375em 0 1.25em;

	.content-double-table__column {
		@include span-columns(6 of 12);
		display: block;

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
		}
	}
}

.content {
	@include span-columns(8);

	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(12);
		@include omega();
	}

	.content__column {
		@include span-columns(8 of 8);
	}

	.content__column-double {
		@include span-columns(4 of 8);

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(8 of 8);
			@include omega();
		}
	}
}

.sidebar {
	@include span-columns(4);
	@include omega();

	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(12);
		@include omega();

		.sidebar__column {
			@include span-columns(12 of 12);
			@include pad(0);
		}
	}

	.sidebar__column {
		@include span-columns(4 of 4);
	}
}

.footer {
	@include outer-container;
}

.footer .footer__column {
	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(12 of 12);
		@include clearfix;
		@include omega;
	}
}

.footer .footer__column-wide {
	@include span-columns(12);
}
