.buttons-more {
	transition: all .2s $ease-out-quad;
	background-color: $buttons-more-background;
	color: $buttons-more-color;
	cursor: pointer;
	clear: both;
	display: block;
	font-family: $buttons-more-font;
	font-size: $buttons-more-font-size;
	font-weight: $buttons-more-font-weight;
	font-style: normal;
	text-align: center;

	&.load-more {
		margin: $buttons-more-vertical-space-mobile auto $buttons-more-vertical-space;

		&:hover {
			background-color: $dark-grey;
		}

		&.loading {
			background-color: $buttons-more-background;
			animation: flash 1s infinite;
			/* stylelint-disable */
			flash-timing-function: ease-in-out;
			/* stylelint-enable */
		}
	}

	&.read-more {
		&:hover {
			background-color: $dark-grey;
		}
	}
}

@-webkit-keyframes flash {
	0% {
		background-color: $buttons-more-background;
	}

	50.0% {
		background-color: $dark-grey;
	}

	100.0% {
		background-color: $buttons-more-background;
	}
}

@keyframes flash {
	0% {
		background-color: $buttons-more-background;
	}

	50.0% {
		background-color: $dark-grey;
	}

	100.0% {
		background-color: $buttons-more-background;
	}
}
