/*
 * @name: search
 *
 * @description: default search module
 *
 */

.search--header {
	@include span-columns(12 of 12);
	display: none;
	font-family: $font-family-meta;
	font-size: 1.2291666667em;
	margin-bottom: $space-innerpage;
	margin-top: $space-innerpage;

	@include media(max-width $mobile-breakpoint-max) {
		@include pad(0 $space-innerpage);

		&.visible {
			display: block;
		}
	}

	legend {
		@include clearfix;
	}

	.search__input {
		@include span-columns(20 of 32, block-collapse);
		appearance: none;
		border: 1px solid $dark-grey;
		border-radius: 0;
		border-right: none;
		color: $black;
		font-size: 1em;
		height: 2.375em;
		vertical-align: middle;

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(9 of 12, block-collapse);
			appearance: none;
			border-radius: 0;
		}

		&::-webkit-input-placeholder {
			color: $lightest-grey;
			font-weight: normal;
		}
		&:-moz-placeholder {
			color: $lightest-grey;
			font-weight: normal;
		}
		&::-moz-placeholder {
			color: $lightest-grey;
			font-weight: normal;
		}
		&:-ms-input-placeholder {
			color: $lightest-grey;
			font-weight: normal;
		}

	}

	.search__button {
		@include span-columns(3 of 12, block-collapse);
		@include omega;
		box-sizing: border-box;
		background-color: $green;
		border: 0;
		color: $white;
		font-size: 1em;
		height: 2.375em;
		line-height: 2.375em;
		padding: 0;
		position: relative;

		@include media(max-width $mobile-breakpoint-max) {
			color: transparent;
			text-indent: -99px;

			&:before {
				color: $white;
				left: 0;
				right: 0;
				text-align: center;
				text-indent: 0;
			}
		}

		&:before {
			@include icon;
			@include icon--search;
			bottom: 0;
			font-size: 1.5em;
			left: .9375em;
			line-height: 1.5833333333em;
			margin-right: .3125em;

			@include media(max-width $mobile-breakpoint-max) {
				left: 0;
				margin: 0;
				position: absolute;
			}
		}
	}

	.search__input {
		@include omega;
		box-sizing: border-box;
	}

	.search--header__hint {
		color: $lighter-grey;
		font-family: $font-default;
		font-size: $article-author-fontsize;
	}
}
