$aside-inside-vertical-space: .5em;

.search--asides {
	@include span-columns(8 of 12);
	@include shift(2);
	@include clearfix;
	@include omega;

	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(12);
		@include shift(0);
		@include pad(0 $space-innerpage);
	}

	.search--asides__archive,
	.search--asides__events,
	.search--asides__recents {
		@include span-columns(6 of 12);
		margin-bottom: 4.5em;

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12);
			margin-bottom: 1em;
		}
	}

	.search--asides__archive__icon,
	.search--asides__events__icon {
		@include span-columns(3);
		color: $lighter-grey;
		font-size: 3em;
		text-align: center;
	}

	.search--asides__events__icon:before {
		@include icon;
		@include icon--suche-veranstaltungen;
	}

	.search--asides__archive__icon:before {
		@include icon;
		@include icon--suche-zeitungsarchiv;
	}

	.search--asides__archive__text__headline,
	.search--asides__events__text__headline {
		color: $lighter-grey;
		font-size: $article-text-headline-fontsize;
		font-weight: $font-weight-light;
		margin-bottom: $aside-inside-vertical-space;
		text-transform: uppercase;
	}

	.search--asides__events__text,
	.search--asides__archive__text {
		@include span-columns(9);
		line-height: $bigger-lineheight;

		a {
			color: $color-emph;
			display: block;
			font-family: $font-family-meta;
			font-weight: $font-weight-regular;
			margin-top: $aside-inside-vertical-space;
			width: auto;
		}

		a:hover {
			text-decoration: underline;
		}
	}

	.search--asides__recents__headline,
	.search--asides__more__title {
		@extend .block-header;

		@include media(max-width $mobile-breakpoint-max) {
			margin-left: 0;
			margin-right: 0;
		}
	}

	.search--asides__events {
		@include omega;
	}

	.search--asides__recents {
		clear: both;

		.search--asides__recents__list {
			@include margin(0);
			@include pad(0);
			color: $dark-grey;
			list-style-type: none;
		}

		.search--asides__recents__list li {
			font-size: .875em;
			font-weight: normal;
			line-height: 1.75em;
		}

		.search--asides__recents__list li:before {
			color: $color-emph;
			content: "\2022";
			font-size: 1em;
			margin-left: .4166666667em;
			margin-right: .75em;
		}
	}
}

.search--results {
	.search--asides {
		display: flex;
		flex-direction: column;

		.search--asides__others {
			order: 1;
		}

		.search--asides__more {
			order: 2;
		}
	}

	.search--asides__archive,
	.search--asides__events,
	.search--asides__obituaries,
	.search--asides__experts {
		margin-bottom: 1.5625em;
		padding-bottom: 1.5625em;
	}

	.search--asides__archive,
	.search--asides__events,
	.search--asides__obituaries {
		border-bottom: 1px solid $nearly-lightest-grey;
	}
}

@include media(max-width $mobile-breakpoint-max) {
	.search--asides .search--asides__archive,
	.search--asides .search--asides__events,
	.search--asides .search--asides__obituaries,
	.search--asides .search--asides__experts {
		margin-bottom: 1.5625em;
		padding-bottom: 1.5625em;
	}

	.search--asides .search--asides__archive,
	.search--asides .search--asides__events,
	.search--asides .search--asides__obituaries {
		border-bottom: 1px solid $nearly-lightest-grey;
	}

	.search--page {
		.search--asides {
			display: flex;
			flex-direction: column;

			.search--asides__others {
				order: 2;
			}

			.search--asides__more {
				order: 1;
			}
		}
	}
}
