.teaser-stream {
	.external-box {
		@include outer-container;
		transition: all .4s;
		border: 1px solid $color-border;
		display: block;
		margin-top: $vertical-space-widgets;
		padding: $vertical-space-widgets;

		&:hover {
			background-color: $color-bg-light;
		}

		.external-box-text-container {
			@include span-columns(9);
		}

		.external-box-text-container p {
			color: $color-text-default;
			font-size: $font-size-text-small;

			// &.external-box-source {
			// font-weight: $font-weight-bold;
			// }
		}

		.external-box-img-container {
			@include span-columns(3);
		}
	}
}
