// Longform Article Carousel

.theme-longform {
	.article--type-longform {
		.article__body {

			.carousel-ajax {

				width: initial;

				@media screen and (max-width: $longform-breakpoint-mobile){
					width: 100%;
					margin: 0;
				}

				.swiper-button-next,
				.swiper-button-prev {
					background: #333;
					border-radius: 4px;
					color: #fff;
					padding: 1em;
					font-size: 1em;
					text-shadow: none;
					width: initial;
					height: initial;

					&:before {
						font-weight: bold;
						font-size: 1em;
						padding: 0;
						background: transparent;
					}
				}

				.swiper-buttons {
					font-size: 12px;
					padding: 0 1em;
				}

				.swiper-pagecount {
					color: $longform-decoration-color;
					border-top: none;
				}

				.inline-block {
					margin-bottom: 0;
				}

				&.swiper-container {
					.inline-media__caption-text {
						@media screen and (max-width: $longform-breakpoint-mobile) {
							font-size: 14px;
						}
					}
				}

				.inline-media__media {
					img {
						min-height: 41vw !important;
						max-height: 41vw;
					}

					&.teaser__img--articleportrait img {
						width: auto;
					}
				}

				.swiper-pagination-bullet {
					background: rgba(0, 0, 0, 0.4);
					border: 1px solid #fff;
					height: 8px;
					width: 8px;
					margin-bottom: 0;
				}

				.swiper-pagination-bullet-active {
					background: #fff;
					border: 1px solid #fff;
					height: 12px;
					vertical-align: middle;
					width: 12px;
				}

			}

			.inline-block--wide,
			.inline-block--left-25,
			.inline-block--right-25,
			.inline-block--left,
			.inline-block--right,
			.inline-block--full {
				.carousel-ajax {
					.inline-media__caption {
						padding-bottom: 0;
						.inline-media__caption-credits {
							margin-bottom: 0;
						}
						@media screen and (max-width: $longform-breakpoint-mobile) {
							margin: auto;
							padding: 0 $element-space-smaler;
							width: calc(100% - #{$element-space-smale});
						}
					}
				}
			}

			.inline-block--wide {
				@media screen and (max-width: $longform-breakpoint-mobile) {
					width: 100%;
				}
				.carousel-ajax {
					width: $longform-split-column-smale;
					margin: auto;
					float: none;

					@media screen and (max-width: $longform-breakpoint-tablet) {
						width: 80%;
					}

					@media screen and (max-width: $longform-breakpoint-mobile) {
						width: 100%;
					}

					.inline-media {
						width: 100%;
					}

					.inline-media__media {
						img {
							min-height: 22vw !important;
							max-height: 22vw;

							@media screen and (max-width: $longform-breakpoint-tablet) {
								min-height: 53vw !important;
								max-height: 53vw;
							}

							@media screen and (max-width: $longform-breakpoint-mobile) {
								min-height: 72vw !important;
								max-height: 72vw;
							}
						}
					}

					.swiper-buttons {
						top: 9vw !important;

						@media screen and (max-width: $longform-breakpoint-tablet) {
							top: 22vw !important;
						}

						@media screen and (max-width: $longform-breakpoint-mobile) {
							top: 32vw !important;
						}

						@media screen and (max-width: $longform-breakpoint-mobile-min) {
							top: 30vw !important;
						}
					}

					.swiper-pagecount {
						border-top: none;
					}

					.inline-media__caption-credits {
						margin-top: 1em;
					}
				}
			}

			.inline-block--full {
				.carousel-ajax {
					&.swiper-container {
						margin-bottom: 0;
					}

					.inline-media__media {
						img {
							min-height: 66vw !important;
							max-height: 66vw;

							//@media screen and (max-width: $longform-breakpoint-tablet) {
							//	min-height: 75vw !important;
							//	max-height: 75vw;
							//}
						}

						&.teaser__img--articleportrait img {
							width: auto;
						}
					}

					.inline-media__caption {
						@media screen and (max-width: $longform-breakpoint-mobile) {
							width: calc(100% - #{$element-outer-space});
						}
					}

					.swiper-pagination {
						display: block;
						top: 63vw;
						position: absolute;
						width: 100%;
						z-index: 1000000;

						@media screen and (max-width: $longform-breakpoint-tablet) {
							//top: 70vw;
							display: none;
						}

						@media screen and (max-width: $longform-breakpoint-mobile) {
							display: none;
						}
					}

					.swiper-pagecount {
						display: none;

						@media screen and (max-width: $longform-breakpoint-mobile) {
							display: block;
						}
					}

					.swiper-buttons {
						@media screen and (max-width: $longform-breakpoint-tablet) {
							top: 32vw !important;
						}

						@media screen and (max-width: $longform-breakpoint-mobile) {
							top: 32vw !important;
						}
					}
				}
			}

			.inline-block--left,
			.inline-block--right {
				.carousel-ajax {
					.inline-media__wrapper {
						width: 100%;
						padding: 0;
					}

					.inline-media__caption {
						margin-left: 0;
						margin-right: 0;
					}

					.inline-media__media {
						img {
							min-height: 41vw !important;
							max-height: 41vw;

							@media screen and (max-width: $longform-breakpoint-tablet) {
								min-height: 49vw !important;
								max-height: 49vw;
							}

							@media screen and (max-width: $longform-breakpoint-mobile) {
								min-height: 72vw !important;
								max-height: 72vw;
							}
						}

						&.teaser__img--articleportrait img {
							width: auto;
						}
					}

					.swiper-pagination {
						@media screen and (max-width: $longform-breakpoint-mobile) {
							display: none;
						}
					}

					.swiper-pagecount {
						@media screen and (max-width: $longform-breakpoint-mobile) {
							border-top: none;
							display: block;
						}
					}

					.swiper-buttons {
						top: 19vw !important;
						width: $longform-split-column-large;

						@media screen and (max-width: $longform-breakpoint-tablet) {
							top: 21vw !important;
						}

						@media screen and (max-width: $longform-breakpoint-mobile) {
							top: 32vw !important;
							width: 100%;
						}

						@media screen and (max-width: $longform-breakpoint-mobile-min) {
							top: 30vw !important;
						}
					}
				}

				.swiper-pagination {
					bottom: $element-space-large;
					display: block;
					position: absolute;
					width: $longform-split-column-large;
					z-index: 1000000;
					transform: none;
					left: initial;
				}

				.swiper-pagecount {
					display: none;
				}
			}

			.inline-block--left {
				.swiper-lazy-preloader {
					left: 34%;

					@media screen and (max-width: $longform-breakpoint-mobile) {
						left: 50%;
					}
				}
			}

			.inline-block--left-25,
			.inline-block--right-25 {
				.carousel-ajax {
					.inline-media__media {
						img {
							min-height: 14vw !important;
							max-height: 14vw;

							@media screen and (max-width: $longform-breakpoint-tablet) {
								min-height: 34vw !important;
								max-height: 34vw;
							}

							@media screen and (max-width: $longform-breakpoint-mobile) {
								min-height: 72vw !important;
								max-height: 72vw;
							}
						}
					}

					.swiper-buttons {
						@media screen and (max-width: $longform-breakpoint-tablet) {
							top: 14vw !important;
						}

						@media screen and (max-width: $longform-breakpoint-mobile) {
							top: 32vw !important;
						}

						@media screen and (max-width: $longform-breakpoint-mobile-min) {
							top: 30vw !important;
						}
					}

					.swiper-pagination {
						display: none;
					}
				}
			}
		}
	}
}