.teaser-stream-divider {
	//background: $color-emph;
	color: $color-text-light;
	font-size: $font-size-extras-medium-biggest;
	margin-bottom: $vertical-space-widgets;
	padding-left: $vertical-space-widgets;

	@media (max-width: $mobile-breakpoint-max) {
		font-size: $font-size-extras-big;
		margin: $vertical-space-widgets 0;
	}

	p {
		font-family: $font-extra;
		font-weight: $font-weight-bold;
		margin: 0;

		@include media(max-width $mobile-breakpoint-max) {
			font-size: 18px;
			line-height: 1;
		}
	}
}
