.flex-box.inline-block--wide {
	padding-right: 0;
}

.inline-carousel {
	.swiper-button-prev,
	.swiper-button-next {
		background: #454345;
		border-radius: 5px;
		width: 35px;
		height: 35px;

		&:before {
			color: $white;
			font-size: 14px;

			@include media (max-width $mobile-breakpoint-max) {
				padding-top: 0.8em;
				vertical-align: top;
			}
		}
	}

	&.carousel-ajax {
		.carousel-ajax-wrapper {
			margin-bottom: 0;
		}
	}

	.swiper-button-prev {
		&:before {
			@include icon--arrow-2-left;
		}
	}

	.swiper-button-next {
		&:before {
			@include icon--arrow-2-right;
		}
	}

	.inline-media {
		.inline-media__caption {
			display: inline-block;
			margin-top: 10px;
			width: calc(100% - 59px);
			margin: 1em 0 0 59px;
			border-left: 1px solid #939599;
			font-size: 16px;
			padding: .25em 1.25em .5em;
			position: relative;

			@include media (max-width $mobile-breakpoint-max) {
				margin-left: 10px;
				margin-top: 10px;
				padding: 0 10px;
				width: calc(100% - 10px);
			}
		}
	}

	.swiper-pagination {
		@include center(h, a);
		bottom: inherit;
		display: block;

		.swiper-pagination-bullet {
			background: transparent;
			border: 1px solid $white;
			width: 8px;
			height: 8px;
			opacity: 1;
			margin-bottom: 10px;

			&.swiper-pagination-bullet-active {
				background: $white;
				width: 10px;
				height: 10px;
				position: relative;
				top: 1px;
			}
		}
	}
}

@include media(max-width $mobile-breakpoint-max) {
	.carousel-ajax.inline-carousel .teaser__img--articleportrait img {
		width: inherit;
	}
}