/*
 * grid all
*/

// Longform Article

body {
	&.body--type-longform {
		.page-wrapper {
			width: 100%;
			max-width: 100%;
		}

		.main {
			max-width: inherit;
		}

		.page {
			width: 100%;
			max-width: 100%;
			padding: 0;

			@include media(min-width $mobile-breakpoint-min) {
				width: 100%;
			}
		}
	}
}

.img-autosize,
.img-autosize img {
	height: auto;
	width: 100%;
}
