.author-info__detail {
	display: flex;

	ul.author-info__detail-list {
		list-style: none;

		li {
			&:before {
				@include icon;
				@include icon--bulletpoint;

				color: $color-emph;
				font-size: 0.85em;
				margin-right: 0.5em;
			}
		}

		.author-info__detail-list--marker {
			font-weight: bold;
		}
	}
}

.article__body {
	.author-info__detail {
		ul.author-info__detail-list {
			padding-left: 0;
			li {
				margin-bottom: 0.5em;
			}
		}
	}
}