/*
 * @name: search
 *
 * @description: search module for nav services
 *
 */

.search--services {
	@extend .search--header;
	@include span-columns(26 of 32);
	@include shift(4);
	display: block;
	font-size: 1em;
	font-weight: bold;
	margin-right: 0;
	margin-top: 0;
	min-width: 18.75em;
}

.search--services .search__input,
.search--services .search__select,
.search--services .search__button {
	font-weight: normal;
	height: 2em;
}

.search--services .search__select {
	appearance: window;
	border: none;
	font-family: $font-family-meta;
	font-size: 1em;
	padding-left: .625em;
	width: 100%;
}

.search--services .select-style {
	@include span-columns(20 of 32, block-collapse);
	@include omega;
	appearance: none;
	float: left;
	overflow: hidden;
	position: relative;

	&:before {
		@include icon;
		@include icon--dropdown;
		background: $white;
		color: $green;
		display: inline-block;
		padding: .5em;
		pointer-events: none;
		position: absolute;
		right: 0;
	}

	select::-ms-expand {
		display: none;
	}
}

.search--services .search__button {
	height: 2em;
	line-height: 2em;

	&::before {
		display: none;
	}
}

.nav-main__list__recommendation,
.nav-main__list__employer,
.nav-main__list__course {
	.search--services {
		margin: 0;
	}

	.search__input {
		@include span-columns(24 of 32, block-collapse);
	}
}

.sidebar .search--services .select-style {
	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(9 of 12, block-collapse);
	}
}

.sidebar .search--services .search__button {
	@include media(max-width $mobile-breakpoint-max) {
		text-indent: -12.5em;
	}

	&:before {
		@include icon;
		@include icon--search;
		bottom: 0;
		color: $white;
		font-size: 1.5em;
		left: .9375em;
		line-height: 1.3;
		margin-right: .3125em;

		@include media(max-width $mobile-breakpoint-max) {
			left: 0;
			margin: 0;
			position: absolute;
		}
	}
}

.sidebar .search--services {
	@include span-columns(28 of 32, block-collapse);
	min-width: initial;
	padding-left: 0;

	.search__input,
	.select-style {
		@include span-columns(9 of 12, block-collapse);
	}
}
