.kachel-teaser {
	&.teaser-stream {
		.teaser-stream-content {
			.updated {
				font-size: 12px;
				text-transform: uppercase;
				background: $black;
				color: $white;
				font-weight: bold;
				padding: .5em .5em .1em;
				position: absolute;
				top: 0;
				left: 14px;
				z-index: 1;
				@media screen and (max-width: $mobile-breakpoint-max) {
					left: initial;
					right: 50px;
				}
			}
		}
	}
}