.container-leader-opener.with-sidebar {
	background-color: $white;
	margin-bottom: 20px;

	.teaser-leader img {
		width: 100%;
	}

	.teaser-image-block {
		height: 230px;
		display: inline-block;
		overflow: hidden;

		@include media(max-width $mobile-breakpoint-max) {
			height: auto;
			display: block;
			max-width: 100%;
			width: 100%;
		}

		.updated {
			position: absolute;
			font-size: 10px;
			color: $white;
			text-transform: uppercase;
			top: 20px;
			left: 20px;

			@include media(max-width $mobile-breakpoint-max) {
				top: 10px;
				left: 10px;
			}

			&:before {
				@include icon;
				@include icon--bulletpoint;
				color: #f06808;
				margin-right: 6px;
				margin-top: -2px;
			}
		}
	}

	.teaser-opener-subblock {
		padding-left: 20px;
		padding-right: 20px;

		.headline {
			position: relative;
			padding: 0;

			&.headline-l {
				font-size: 36px;
				line-height: 1.1em;
				margin-bottom: 24px;
				bottom: 0;
				width: 100%;

				@include media(max-width $mobile-breakpoint-max) {
					font-size: 18px;
					margin-bottom: 16px;
				}
			}

			.teaser__link {
				color: #333;
			}
		}

		a.emph.headline_section {
			font-size: 10px;
			text-transform: uppercase;
		}

		.emph-hashtags {
			font-size: 14px;
			display: inline-block;
			position: relative;
			float: left;
			text-transform: uppercase;

			@include media(max-width $mobile-breakpoint-max) {
				margin-bottom: 8px;
			}
		}
	}

	.teaser-leader-l {
		padding-bottom: 34px;
		overflow: visible;

		.ribbon-share {
			cursor: pointer;
			float: right;
			margin-right: 20px;
			left: 0;
			margin-bottom: 0;

			@include media(max-width $mobile-breakpoint-max) {
				margin-top: 10px;
				margin-left: 20px;
				float: left;
				display: table;
				width: 100%;
			}

			&.social-open {
				left: 4px;
				.ribbon-fold.ribbon-fold-left-bottom {
					&:before {
						float: right;
					}
				}
				@include media(max-width $mobile-breakpoint-max) {
					margin-left: 0;
				}
			}
		}
	}
}