.trending-bar {
	background: $white;
	font-size: 11px;
	display: table;
	width: 100%;
	padding: .65em 1.8em;
	@include media(max-width $mobile-breakpoint-max) {
		display: block;
		overflow: hidden;
		padding: .65em 1em;
	}
}

.trending-bar__list {
	display: inline-flex;
	float: left;
	margin: 0;
	padding-left: 0;
	white-space: nowrap;
	overflow: hidden;
}

.trending-bar__list__item {
	list-style: none;
	margin-right: 1.8em;
	a {
		color: $black;
	}
	&:last-of-type {
		margin-right: 0;
	}
	&.trending-bar__headline {
		float: left;
		font-weight: bold;
		margin-right: 3.18em;
		@include media(max-width $mobile-breakpoint-max) {
			margin-right: 1.2em;
		}
	}
	@include media(max-width $mobile-breakpoint-max) {
		margin-right: 1.2em;
	}
}