// Longform Article Carousel

.theme-longform {
	.article--type-longform {
		.custom-article__author {
			float: left;
			margin-right: 1.5em;
			margin-left: 4%;
			padding: 0;
			width: 25%;

			@media screen and (max-width: $longform-breakpoint-mobile) {
				margin: 0;
				margin-bottom: 1em;
				width: 100%;
				padding: 0 10px;
			}

			.author-info__name address {
				width: 100%;
				float: left;
				text-align: left;
			}
		}
	}
}

