section.container-leader {
	&.flex-3,
	&.flex-4,
	&.flex-5 {
		.teaser-leader {
			.headline {

				a.emph {
					display: none;
				}
			}

			.teaser-image-block img {
				@media (max-width: $mobile-breakpoint-max) {
					width: 100%;
					height: 210px;
				}
			}

			&.teaser-leader-l,
			&.teaser-leader-m,
			&.teaser-leader-s {
				@media (max-width: $mobile-breakpoint-max) {
					margin-bottom: 10px !important;
				}

				&:last-child {
					@media (max-width: $mobile-breakpoint-max) {
						margin-bottom: 0 !important;
					}
				}
			}
		}

		&.content--teaser--container {
			@media (max-width: $mobile-breakpoint-max) {
				background-color: #322d27;
				height: auto;
			}
		}
	}

	&.flex-3 {
		height: 440px;
		.teaser-leader {
			&.teaser-leader-w {
				width: 630px;
				height: 440px;

				@media (max-width: $mobile-breakpoint-max) {
					width: 100%;
					height: 210px;
					margin: 0;
					margin-bottom: 10px;
				}
			}

			&.teaser-leader-s {
				width: 320px;
				height: 214px;
				margin-left: 10px;
				margin-right: 0;
				margin-bottom: 10px;

				@media (max-width: $mobile-breakpoint-max) {
					width: 100%;
					height: 210px;
					margin: 0;
					margin-bottom: 10px;
				}

				img {
					width: auto;
					height: 214px;

					@media (max-width: $mobile-breakpoint-max) {
						width: 100%;
						height: auto;
					}
				}
			}

			.headline {
				&.headline-w {
					&.on-dark {
						width: 100%;

						@media (max-width: $mobile-breakpoint-max) {
							font-size: 18px;
						}
					}
				}

				&.headline-s {
					&.on-dark {
						width: 100%;

						@media (max-width: $mobile-breakpoint-max) {

						}
					}
				}
			}

			.teaser-leader.teaser-leader-s:last-child {
				width: 320px;
			}
		}
	}

	&.flex-4 {
		.teaser-leader {
			@media (max-width: $mobile-breakpoint-max) {
				height: 210px;
				margin: 0;
				margin-bottom: 10px;
				width: 100%;
			}

			&.teaser-leader-m {
				float: left;
				margin-right: 10px;
				margin-bottom: 10px;
				width: 475px;

				@media (max-width: $mobile-breakpoint-max) {
					width: 100%;
					height: 210px;
					margin: 0;
					margin-bottom: 10px;
				}

				&:nth-child(even) {
					float: right;
					margin-right: 0;
					margin-left: 0;
				}

				&:nth-child(3) {
					margin-bottom: 0;
				}

				&:nth-child(4) {
					margin-bottom: 0;
				}

				img {
					@media (max-width: $mobile-breakpoint-max) {
						width: 100%;
						height: auto;
					}
				}
			}
			.headline-m {
				font-size: 18px;
				display: inline-block;
				width: 100%;

				@media (max-width: $mobile-breakpoint-max) {

				}
			}
			&.teaser-leader-s:last-child {
				width: 320px;
				margin-bottom: 0;
			}

			&.content--teaser--container {
				@media (max-width: $mobile-breakpoint-max) {
					background-color: #322d27;
					height: auto;
				}
			}
		}
	}

	&.flex-5 {
		height: 430px;

		.teaser-leader {
			@media (max-width: $mobile-breakpoint-max) {
				width: 100%;
				height: 210px;
				margin-bottom: 10px;
			}

			.headline.headline-l {
				font-size: 18px;
				margin: 0;
				width: 100%;

				@media (max-width: $mobile-breakpoint-max) {

				}
			}

			.headline.headline-s {
				@media (max-width: $mobile-breakpoint-max) {

				}
			}

			&.teaser-leader-l {
				margin-right: 10px;
				width: 320px;

				@media (max-width: $mobile-breakpoint-max) {
					width: 100%;
					height: 310px;
				}

				.teaser-image-block {
					width: auto;
					height: 430px;

					img {
						@media (max-width: $mobile-breakpoint-max) {
							width: auto;
							height: 430px;
						}
					}
				}
			}

			&.teaser-leader-s {
				height: 210px;
				width: 310px;
				margin-right: 10px;
				margin-bottom: 10px;

				@media (max-width: $mobile-breakpoint-max) {
					margin-right: 0;
					width: 100%;
				}

				.teaser-image-block img {
					width: 100%;
					height: 210px;

					@media (max-width: $mobile-breakpoint-max) {
						width: 100%;
					}
				}

				&:nth-child(3) {
					margin-right: 0;
				}

				&:nth-child(5) {
					margin-right: 0;
				}
			}

			&.teaser-leader-s:last-child {
				width: 310px;

				@media (max-width: $mobile-breakpoint-max) {
					width: 100%;
				}
			}
		}

		&.content--teaser--container {
			@media (max-width: $mobile-breakpoint-max) {
				background-color: #322d27;
				height: auto;
			}
		}
	}
}