.hero-carousel {
	&.carousel-ajax {
		@include media(max-width $mobile-breakpoint-max) {
			margin-bottom: 1em;
		}

		.swiper-pagination {
			display: none;
		}

		img {
			max-height: 600px;
		}

		.hero-media__media.media.teaser__img--articleportrait img {
			width: auto;

			@media (max-width: $mobile-breakpoint-max) {
				max-width: 420px;
				width: 100%;
				height: auto;
			}
		}

		.hero-media {
			.hero-media__caption {
				display: inline-block;
				width: 100%;

				.hero-media__caption-text {
					color: $color-swiper-caption-text;
				}

				.hero-media__caption-credits {
					color: $color-swiper-caption-credits;
					margin-bottom: 0;
				}
			}
		}
	}
}

