.inline-block--wide {
	display: block;
	clear: both;
	float: none;

	.twitter-video,
	.twitter-tweet {
		width: 100% !important;
	}
}

.inline-block--left,
.inline-block--left-25 {
	float: left;
	margin-right: 1.25em;
	margin-left: $vertical-space-widgets;

	@include media(max-width $mobile-breakpoint-max) {
		margin-right: 0;
		margin-left: 0;
	}
}

.inline-block--right,
.inline-block--right-25 {
	float: right;
	margin-left: 1.25em;
	margin-right: $vertical-space-widgets;

	@include media(max-width $mobile-breakpoint-max) {
		margin-left: 0;
		margin-right: 0;
	}
}

.inline-block--left-25,
.inline-block--right-25 {
	width: 25%;

	@include media(max-width $mobile-breakpoint-max) {
		display: none;
	}
}

.inline-block--left,
.inline-block--right {
	width: 50%;

	@include media(max-width $mobile-breakpoint-max) {
		width: 100%;
	}
}
