.container-leader {
	.teaser-leader {
		background-position: center center;
		background-size: cover;
		cursor: pointer;
		float: left;
		margin-bottom: $vertical-space-widgets-half;
		vertical-align: top;

		.teaser-image-block {
			img {
				display: block;
			}
		}

		&.adcontainer {
			background: #fff;

			p {
				color: $color-text-ondark-light;
				font-size: $font-size-extras-tiny;
				font-weight: $font-weight-regular;
				margin: 0 0 5px;
			}

			.overlay {
				display: none;
			}

			.ad {
				height: 100%;
				width: 100%;
			}

			.headline {
				display: none;
			}
		}

		.headline {
			bottom: 0;
			left: 0;
			padding: 0 $vertical-space-widgets $vertical-space-widgets;
			position: absolute;

			&.headline-l {
				@include span-columns(10);
				bottom: $vertical-space;

				@include media(max-width $mobile-breakpoint-max) {
					@include span-columns(12);
					font-size: $font-size-headline-mobile-m;
				}
			}

			&.headline-l a {
				display: inline;
			}

			&.headline-m {
				padding-top: .5em;

				@include media(max-width $mobile-breakpoint-max) {
					font-size: $font-size-headline-mobile-s;
				}
			}

			&.headline-s {
				padding-top: .5em;
				width: 100%;

				@include media(max-width $mobile-breakpoint-max) {
					font-size: $font-size-headline-mobile-s;
				}
			}

			&.headline-s a {
				display: inline;
			}
		}

		&.teaser-leader-l {
			@include fill-parent;
			margin-bottom: .968%;

			&:last-child {
				margin-bottom: 0;
			}

			@media (max-width: $mobile-breakpoint-max) {
				@include span-columns(12);

				img {
					width: 100%;
				}
			}
		}

		&.teaser-leader-m {
			@include span-columns(6 of 9);
			height: 313px;

			@media (max-width: $mobile-breakpoint-max) {
				@include span-columns(12);

				img {
					width: 100%;
				}
			}
		}

		&.teaser-leader-s {
			@include span-columns(3 of 9);
			height: 313px;

			@include media(min-width $mobile-breakpoint-min) {
				img {
					width: 100%;
				}
			}

			@media (max-width: $mobile-breakpoint-max) {

				img {
					width: 100%;
				}
			}
		}

		.teaser-leader__caption {
			bottom: $vertical-space;
			color: $color-text-ondark-light;
			font-size: .625em;
			position: absolute;
			right: 10px;
		}
	}

	.teaser-leader {
		&.teaser-leader-s {
			&:last-child {
				width: 32.69%;

				@media (max-width: $mobile-breakpoint-max) {
					@include span-columns(12);
				}
			}
		}
	}
}

.teaser-leader {
	.overlay {
		background-image: linear-gradient(180deg, rgba(20, 20, 20, 0) 0%, rgba(20, 20, 20, 0) 50%, rgba(20, 20, 20, 0.9) 100%);
		height: 100%;
		position: absolute;
		width: 100%;
	}
	.updated {
		font-size: 13px;
		text-transform: uppercase;
		background: $black;
		color: $white;
		font-weight: bold;
		padding: .75em 1em .2em;
		position: absolute;
		top: 0;
		left: 16px;
		z-index: 1;
		@media screen and (max-width: $mobile-breakpoint-max) {
			font-size: 12px;
			padding: .5em .5em .1em;
		}
	}
}

