.socialbar {
	border-top: 1px solid #eaeaea;
	border-bottom: 1px solid #eaeaea;
	clear: both;
	margin: 0 32px 18px 16px;
	padding-top: 20px;
	padding-bottom: 16px;
	text-align: center;

	@include media (max-width $mobile-breakpoint-max) {
		margin: 0 0 40px;
	}

	.share-button {
		margin-right: 0.5em;

		&.email {
			margin-right: 0;
		}

		&:last-child {
			display: none;
		}

		.share-button__icon {
			border-radius: 50%;
			line-height: 2em;
			text-align: center;
			width: 2em;
			height: 2em;
			position: relative;

			&:before {
				@include center(b,a);
				font-size: .75em;
			}
		}
	}

	.socialbar__channels {
		display: inline-block;
	}

	.share-button,
	.share-button-static {
		&.facebook .share-button__icon,
		&.twitter .share-button__icon,
		&.google-plus .share-button__icon,
		&.whatsapp .share-button__icon,
		&.email .share-button__icon,
		&.print .share-button__icon {
			@include media(max-width $mobile-breakpoint-max) {
				font-size: 1.45em;
				margin: 0;
			}

			@include media(max-width $mobile-breakpoint-mini) {
				font-size: 1.25em;
				margin: 0;
			}
		}
	}

	.share-button.share-button.email .share-button__icon {
		background-color: $orange;

		&:before {
			@include icon;
			@include icon--mail-square;
		}
	}
}

.container-leader-opener.with-sidebar,
.teaser.fullwidth {
	.socialbar {
		transition: all 0.4s cubic-bezier(0.25, 0.1, 0.25, 1);
		background: transparent;
		visibility: hidden;
		height: 100%;
		position: absolute;
		top: 0;
		width: 100%;
		z-index: 100;
		border: 0;
		margin: 0;
		padding: 0;

		.socialbar__channels {
			transition: all 0.4s cubic-bezier(0.25, 0.1, 0.25, 1);
			top: 25%;
			opacity: 0;
			position: relative;
			margin: auto;
			display: table;
			float: none;
			width: auto;
		}

		&.animate {
			background: rgba(0, 0, 0, 0.8);
			visibility: visible;

			.socialbar__channels {
				top: 50%;
				opacity: 1;
			}
		}
	}
}

.article.article--type-gallery .socialbar__channels,
.article.article--type-video .socialbar__channels {
	float: none;
}