.main .search--results {
	background: transparent;
	padding-top: .5em;


	.search--results__results {
		@extend .container-stream;
	}

	.search--results__asides {
		@extend .container-sidebar;
		@include media(max-width $mobile-breakpoint-max) {
			display: none;
		}
	}

	.search--results__options__filters {
		margin-top: 1.8em;
		@include media(max-width $mobile-breakpoint-max) {
			margin-top: 0;
		}
	}

	.paging {
		padding-left: 1em;
		&:last-of-type {
			display: none;
		}
		@include media(max-width $mobile-breakpoint-max) {
			padding-left: 10px;
		}
	}

	.teaser__kicker {
		display: none;
	}

	.sort__list__title,
	.search--results__options__filters__title,
	.paging__description {
		text-transform: none;
		font-weight: normal;
		font-size: 14px;
		color: $color-cool-grey;
		min-width: 90px;
		float: left;
		text-align: left;
		@include media(max-width $mobile-breakpoint-max) {
			min-width: 65px;
		}
	}

	.sort__list__title,
	.paging__description {
		line-height: 1;
		position: relative;
		top: 2px;
	}

	.paging__description {
		top: 6px;
	}

	.sort__list__title,
	.search--results__options__filters__title {
		margin-bottom: 10px;
	}
	.search--results__options__filters__title {
		margin-top: 0;
		line-height: 1;
		padding-top: 12px;
		@include media(max-width $mobile-breakpoint-max) {
			padding-top: 0;
		}
	}

	legend {
		font-size: 36px;
		font-weight: 500;
	}

	.paging a {
		font-size: 16px;
		line-height: 1;
		color: $color-text-headline-default;
		&.active {
			color: $black;
			font-weight: 500;
		}
	}

	.search--result__list {
		@include media(max-width $mobile-breakpoint-max) {
			margin: 0 10px;
		}
	}

	.search--result__list__entries {
		padding-left: 0;
		.teaser-list {

			@include media(max-width $mobile-breakpoint-max) {
				margin: 20px 10px 0;
			}

			li {
				background: #fff;
				margin-bottom: 16px;
				border: none;
				article {
					padding: 20px;
					@include media(max-width $mobile-breakpoint-max) {
						padding: 10px;
						margin: 0;
						border: none;
					}
				}
			}

			.teaser__date {
				display: none;
			}

			.teaser__body {
				font-size: 16px;
			}

			.teaser__img {
				height: 145px;
				width: 145px;
				@include center(v, a);
				right: 0;
				overflow: hidden;
				img {
					@include center(h);
					height: 145px;
					width: auto;
				}
				@include media(max-width $mobile-breakpoint-max) {
					width: 80px;
					height: 80px;
					right: 10px;
					transform: none;
					margin-top: 0;
					top: 20px;
					img {
						height: 80px;
					}
				}
			}

			.teaser__headline {
				max-width: 75%;
				font-size: 1px;
				a {
					font-weight: 500;
					&:first-of-type {
						font-size: 10px;
						text-transform: uppercase;
					}
					&:not(.emph) {
						display: block;
						font-size: 18px;
						&:not(:last-child) {
							margin-bottom: .5em;
						}
						//padding: 20px 20px 0;
					}
					&.emph:not(:first-of-type) {
						font-size: 14px;
						text-transform: uppercase;
						margin-right: .75em;
					}

				}
				@include media(max-width $mobile-breakpoint-max) {
					max-width: inherit;
					padding-top: 10px;

					a:nth-child(2) {
						min-height: 65px;
						word-wrap: break-word;
						max-width: 65%;
						line-height: 1.1;
						padding-top: 10px;
						margin-bottom: 7px;
					}
				}
			}
			.teaser__body {
				width: 75%;
				margin-top: .75em;
				@include media(max-width $mobile-breakpoint-max) {
					width: 100%;
				}
			}
		}
	}

	.search--results__options__sort__hitcount {
		display: none;
	}

	.sort__list__items {
		float: left;
		padding-top: 1em;
		li {
			padding: 0 .75em;
			&:after {
				display: none;
			}
			@include media(max-width $mobile-breakpoint-max) {
				font-size: 16px;
				padding: 0;
				label,
				input {
					float: left;
				}
				label {
					max-width: 65px;
					position: relative;
					top: -2px;
					line-height: 1.25;
				}
				&+li {
					margin-top: 20px;
				}
			}
		}
	}

	.search--results__options {

		@include media(max-width $mobile-breakpoint-max) {
			border-top: none;
		}

		&__sort {
			border-top: none;

		}
		.filters__list__items {
			margin-top: 0;
			float: left;

			@include media(max-width $mobile-breakpoint-max) {
				border-radius: 4px;
				border-color: $color-text-headline-default;
			}

			li:first-child {
				border-top-left-radius: 4px;
				border-bottom-left-radius: 4px;
			}

			.item-tag {
				background: $white;
				border-color: $color-text-headline-default;
				&--active {
					background: $color-text-headline-default;
					.item-tag__link,
					.item-tag__ri {
						color: $white;
					}
				}
				&:last-child {
					border-color: $color-text-headline-default;
				}
				&__link {
					color: $color-text-headline-default;
				}
				&__ri {
					color: $color-text-headline-default;
				}
			}
			@include media(max-width $mobile-breakpoint-max) {
				li {
					border-color: $color-text-headline-default;
					&:first-child {
						border-top-left-radius: 4px;
						border-top-right-radius: 4px;
						border-bottom-right-radius: 0;
					}
					&:last-child {
						border-bottom-left-radius: 4px;
						border-bottom-right-radius: 4px;
					}
				}

			}
		}
		&__sort__list {
			width: 100%;
			@include media(max-width $mobile-breakpoint-max) {
				padding-left: 20px;
			}
		}
	}

	.search--results__results .search--fullsize {

		@include media(max-width $mobile-breakpoint-max) {
			padding: 0 10px;
		}

		fieldset {
			@include media(max-width $mobile-breakpoint-max) {
				padding: 0;
			}
		}

		.search__input {
			font-family: $font-default;
			font-weight: 500;
			font-size: 24px;
			padding: .25em 0;
			background: transparent;
			border: none;
			height: auto;
			border-bottom: 2px solid #d8d8d8;
			width: 50%;
			&::-webkit-input-placeholder {
				color: $color-text-headline-default;
			}
			&:-moz-placeholder {
				color: $color-text-headline-default;
			}
			&::-moz-placeholder {
				color: $color-text-headline-default;
			}
			&:-ms-input-placeholder {
				color: $color-text-headline-default;
			}

			@include media(max-width $mobile-breakpoint-max) {
				float: none;
				width: 100%;
				margin-bottom: 20px;
				padding-left: 10px;
				padding-right: 10px;
			}
		}
		.search__button:last-child {
			background: $color-text-headline-default;
			border-radius: 4px;
			font-size: 14px;
			text-transform: uppercase;
			margin-left: 20px;
			width: calc(50% - 20px);
			&:before {
				display: none;
			}
			@include media(max-width $mobile-breakpoint-max) {
				float: none;
				width: 100%;
				margin-left: 0;
				color: $white;
				text-align: center;
				text-indent: 0;
			}
		}
	}
}