.container-sidebar {
	@include span-columns(3 of 9);
	@include omega;
	display: inline-block;

	@media (max-width: $mobile-breakpoint-max) {
		@include span-columns(12);
		margin: $vertical-space-widgets-mobile 0;
	}

	.container-sidebar-widget-wrapper {
		@media (max-width: $mobile-breakpoint-max) {
			padding-left: 0;
			position: relative;
		}
	}

	.sidebar-item,
	.inline-block--wide {
		margin-bottom: $vertical-space-widgets;

		@media (max-width: $mobile-breakpoint-max) {
			margin-bottom: $vertical-space-widgets-mobile;
		}

		&:last-child {
			margin-bottom: 0;

			@media (max-width: $mobile-breakpoint-max) {
				margin-bottom: $vertical-space-widgets-mobile;
			}
		}
	}

	.teaser-section-stream {
		&:last-child {
			margin-bottom: 0;
		}
	}
}
