.custom-author--detailpage {
	.author-info__social {
		a {
			&:before {
				display: none !important;
			}
		}
	}

	&.article__header__intro {
		margin-left: 0;
		width: 100%;
	}

	.author-info__social-channels {
		.author-info__social-button {
			font-size: 1.5em;
		}
	}

	.author-info__email {
		margin-bottom: $vertical-space-widgets;
	}
}

.article {
	.custom-author--detailpage {
		&.article__header__intro {
			margin-left: 0;
			width: 100%;

			.article__header__intro__text {
				color: $color-article-body;
			}
		}
	}
}