.themen-teaser-opener {
	.headline {
		position: absolute;
		bottom: $vertical-space-widgets;
		color: $color-emph;
		left: $vertical-space-widgets;
	}

	.teaser-img__credits {
		bottom: $vertical-space-widgets-half;
		color: rgba(255, 255, 255, 0.6);
		font-size: 12px;
		position: absolute;
		right: $vertical-space-widgets-half;

		@include media(max-width $mobile-breakpoint-max) {
			font-size: 10px;
		}
	}

	img {
		display: block;
		width: 100%;
	}

	.teaser-image-block {
		margin: 0;
	}

	&.teaser-leader {
		&.teaser-leader-l {
			height: auto;
		}
	}
}
