// teaser-list
.teaser-list {
	list-style: none;
	padding: 0;

	@include media(max-width $mobile-breakpoint-max) {
		@include pad(0 $space-innerpage);
	}
}

.teaser-list--autotage .teaser {
	padding: .3125em 0;
}

.teaser-list li {
	&:before {
		float: left;
		margin-right: .3125em;
	}

	&:first-child .teaser {
		padding-top: 0;
	}

	&:first-child .teaser--highlight {
		padding-top: 0;

		.teaser__identifier {
			padding-top: .5em;
		}

		.teaser__header {
			padding-left: .5em;
			padding-right: .5em;
			padding-top: .75em;
		}

		.teaser__identfier + .teaser__header {
			padding-left: 0;
			padding-top: 0;
		}

		.teaser__img ~ .teaser__header,
		.teaser__img ~ .teaser__identifier {
			padding-top: 0;
		}
	}

	a:hover .teaser__headline {
		text-decoration: underline;
	}
}

.teaser-list .teaser__identifier {
	@include span-columns(2 of 12);
	color: $lighter-grey;
	font-size: 1.5em;
	text-align: center;
}

.teaser-list .teaser--small {
	@include media(max-width $mobile-breakpoint-max) {
		font-family: $font-default;
		margin-left: 0;
		margin-right: 0;
	}
}

.teaser-list .teaser__header {
	@include span-columns(10 of 12);
	@include omega;
}

.teaser-list li + li .teaser {
	border-top: 1px solid $nearly-lightest-grey;
}

.collapsable .block-header__icon:before {
	@include icon;
	@include icon--arrow-right;
	display: none;
}

@include media(max-width $mobile-breakpoint-max) {
	.block-header {
		padding-bottom: $vertical-space-imageblock;
	}

	.collapsable {
		.block-header {
			.block-header__icon {
				display: block;
				position: static;
			}

			.block-header__icon:before {
				bottom: 0;
				font-size: 2.5em;
				position: absolute;
				right: 10px;
			}
		}

		.block-header .block-header__icon:before {
			transform: rotate(90deg);
		}

		&.open .block-header .block-header__icon:before {
			transform: rotate(-90deg);
		}

		.collapsable__content {
			display: none;
		}

		&.open .collapsable__content {
			display: block;
		}
	}
}

.teaser-list-content .collapsable--desktop .block-header {
	border-bottom: none;
	margin-bottom: .5em;
}

.collapsable--desktop {
	.block-header {
		cursor: pointer;
	}

	.collapsable__content {
		display: none;
	}

	&.open .collapsable__content {
		display: block;
	}
}

@include media(min-width $mobile-breakpoint-min) {
	.block-header .block-header__icon:before {
		content: none;
	}
}

.teaser-list-text,
.teaser-list-text-image {
	@include media(max-width $mobile-breakpoint-max) {
		margin-left: $space-innerpage;
		margin-right: $space-innerpage;
	}
}

.teaser-list-text-image img {
	height: auto;
	margin-bottom: .5em;
	margin-top: .5em;
	width: 100%;
}

.block-header ~ .collapsable__content {
	@include media(min-width $mobile-breakpoint-min) {
		> .teaser:first-child {
			padding-top: 0;
		}
	}
}

.teaser-list--autotage,
.teaser-list--with-arrow {
	list-style: none;
	margin: 1.5625em 0 0 1.5625em;
	padding: 0;

	li {
		@include clearfix;
	}

	.teaser__identifier,
	a {
		line-height: 35px;
	}

	.teaser__identifier {
		@include span-columns(1 of 12);
		font-size: 1.125em;
	}

	a {
		@include span-columns(11 of 12);
		@include omega;
	}

	.teaser__identifier:before {
		@include icon;
		@include icon--arrow-circle-right;
	}
}

.teaser-list.two-columns {
	display: flex;
	flex-wrap: wrap;

	.teaser-list__item {
		width: calc(50% - 5px);
		margin-bottom: 5px;

		&:nth-child(2n) {
			margin-left: 10px;
		}

		.teaser {
			border-top: 0;
			padding-top: 0;

			.teaser__img {
				width: 100%;
			}

			.teaser__header,
			.teaser__body {
				width: 100%;
			}
		}

		.teaser__img--vertical {
			margin-bottom: 0;
		}
	}
}