.teaser-stream-gallery {
	background: $color-bg-dark;

	.hero-img__text {
		color: $color-text-light;
		font-size: .75em;
		font-weight: normal;
		text-align: left;
	}

	.carousel-ajax {
		.swiper-pagination {
			display: none;
		}


	}
}

.carousel {
	overflow: hidden;
	position: relative;
}

.carousel .carousel__list {
	display: block;
	margin: 10px;
	overflow: hidden;
	position: relative;
	width: 100%;
}

.carousel .carousel__item {
	width: 100%;

	.no-js & {
		display: none;

		&:first-child {
			display: block;
		}
	}

	.js & {
		display: none;
		position: absolute;
		top: 0;
	}
}

.carousel__controls {
	bottom: 0;
	left: 0;
	position: absolute;
	right: 0;
	top: 0;
}

.teaser-media {
	margin: 0;

	.teaser-media__caption {
		.teaser-media__caption-text,
		.teaser-media__caption-text a {
			color: $color-text-light;
			font-size: $font-size-text-small;
			font-weight: normal;
			margin: 0;
			text-decoration: none;
		}
	}
}

.carousel .carousel__item--center {
	.js & {
		display: block;
		left: 0;
	}
}

.carousel .carousel__item--prev,
.carousel .carousel__item--next {
	.js & {
		display: block;
	}
}

.carousel .carousel__item {
	transition: left .5s ease-in-out;
}

.carousel .carousel__item--prev {
	.js & {
		left: -100%;
	}
}

.carousel .carousel__item--next {
	.js & {
		left: +100%;
	}
}

.carousel__control {
	width: 30%;

	&.carousel__control--next,
	&.carousel__control--prev {
		display: table;
		text-decoration: none;

		i {
			display: table-cell;
			vertical-align: middle;
		}
	}

	&.carousel__control--prev {
		text-align: left;
	}

	&.carousel__control--next {
		text-align: right;
	}

	&.carousel__control--next .carousel__control__icon:before {
		@include icon;
		@include icon--arrow-right;
	}

	&.carousel__control--prev .carousel__control__icon:before {
		@include icon;
		@include icon--arrow-left;
	}
}

.carousel .carousel__control {
	bottom: 0;
	cursor: pointer;
	font-size: 36px;
	height: 100%;
	margin: auto;
	position: absolute;
	top: 0;
	z-index: 1;

	.no-js & {
		display: none;
	}
}

.carousel .carousel__controls .carousel__control__icon {
	color: $color-emph;

	&:before {
		font-size: 24px;
		font-weight: 800;
		padding: 0 10px;
	}
}

.carousel .carousel__control--prev {
	left: 0;
	margin-left: 0;
	text-align: left;
}

.carousel .carousel__control--next {
	margin-right: 0;
	right: 10px;
	text-align: right;
}

.carousel .carousel__positions {
	display: none;
	margin: 0 auto 16px;
	overflow: hidden;
	text-align: center;
}

.carousel .carousel__index {
	background: $color-bg-light;
	border-radius: 999px;
	display: inline-block;
	height: 5px;
	margin: 0 5px;
	width: 5px;
}

.carousel .carousel__index--active {
	background: $color-emph;
}

.inline-carousel--text {
	.inline-media__wrapper {
		overflow: hidden;
	}

	.carousel__item {
		min-height: 266px;
	}

	.inline-media__text {
		@include span-columns(12);
		float: none;
	}

	.carousel__item--center {
		z-index: 2;
	}

	.carousel__item--next,
	.carousel__item--prev {
		height: 100px;
	}

	.carousel__item {
	}
}
