/*
 * @name: search-bar
 *
 * @description: search module for Navigation
 *
 */

.search-bar {
	@include outer-container;

	background-color: $color-bg-lighter;
	clear: both;
	display: none;
	font-family: $font-extra;
	position: relative;
	width: 960px;
	z-index: 200;

	@media (max-width: $mobile-breakpoint-max){
		@include span-columns(12);
		border-bottom: 1px solid $color-text-black;
		display: none;
		position: fixed;
		z-index: 200;
	}

	.toggle-search {
		color: $color-emph;
		cursor: pointer;
		float: right;
		font-size: $font-size-extras-small;
		font-weight: normal;
		margin: 1.25em;
		margin-bottom: 0;

		@media (max-width: $mobile-breakpoint-max){
			display: none;
		}
	}

	.search-bar-input-wrapper {
		@include span-columns(12);
		padding: $vertical-space-widgets;

		.form-fields {
			@include span-columns(13 of 18);

			@media (max-width: $mobile-breakpoint-max){
				@include span-columns(13 of 18);
			}
		}

		.form-fields .form-fields-search {
			@include span-columns(12);
			@include omega;

			appearance: none;
			border: 0;
			border-bottom: 1px solid;
			border-radius: 0;
			font-size: $font-size-extras-big;
			outline: none;
			padding: 0 0 .25em;

			@media (max-width: $mobile-breakpoint-max) {
				font-size: $font-size-extras-medium-big;
			}
		}

		.form-actions {
			@include span-columns(4 of 18);
			@include shift(1);

			background-color: $color-emph;
			border: 0;
			border-radius: 8px;
			color: $color-text-white;
			cursor: pointer;
			font-size: 1.5em;
			padding: $vertical-space-widgets-half;
			text-align: center;
			transition: $transition-fast;

			@media (max-width: $mobile-breakpoint-max) {
				@include span-columns(4 of 18);
				font-size: 1em;
				padding: .25em;
			}
		}

		.form-actions:hover {
			background-color: $dark-grey;
		}
	}

	.errorSearchMessage {
		color: $color-emph;
		display: none;
		left: $vertical-space-widgets;
		position: absolute;
		top: $vertical-space-widgets;

		@media (max-width: $mobile-breakpoint-max) {
			font-size: .5em;
			top: .375em;
		}
	}
}

.header .search-bar {
	@media (max-width: $mobile-breakpoint-max) {
		top: 58px;
	}
}

.header.sticky .search-bar {
	@media (max-width: $mobile-breakpoint-max) {
		top: 47px;
	}
}
