.teaser--hero-img-wrapper {
	display: inline-block;
	position: relative;
	z-index: 0;

	.socialbar {
		&.teaser__icons {
			bottom: 1rem;
			right: 1rem;
		}
	}
}

.socialbar {
	&.teaser__icons {
		display: table;
		margin: 0;
		padding: 0;
		position: absolute;
		text-align: center;
		z-index: 1;

		li.socialbar__item {
			border-bottom: none;
			display: inline-block;
			list-style: none;
			margin-right: 0.25rem;

			&:last-child {
				margin-right: 0;
			}
		}

		.share-button {
			.share-button__icon {
				background: #fff;
				border-radius: 100%;
				padding: 0;
				height: 2em;
				width: 2em;

				&:before {
					color: $color-emph;
					vertical-align: inherit;
					line-height: 2em;
					padding: 0;
				}
			}

			&.video {
				.share-button__icon {
					&:before {
						@include icon--video;
						padding-left: 0.2em;
					}
				}
			}

			&.gallery {
				.share-button__icon {
					&:before {
						@include icon--galery;
						margin-left: -0.1em;
					}
				}
			}

			&.paid {
				.share-button__icon {
					&:before {
						@include icon--paid;
						font-size: 1.5em;
						line-height: 1.3em;
					}
				}
			}
		}
	}
}

.teaser--large {
	.socialbar {
		&.teaser__icons {
			.share-button {
				.share-button__icon {
					font-size: 11px;
				}
			}
		}
	}
}

.teaser__img--left ~ .socialbar,
.teaser__img--right ~ .socialbar {
	&.teaser__icons {
		z-index: 0;
	}
}

.teaser__img--left ~ .socialbar {
	&.teaser__icons {
		left: 0.5rem;
		top: 1.5rem;

		@include media(max-width $mobile-breakpoint-max) {
			top: 1.75rem;
		}
	}
}

.teaser__img--right ~ .socialbar {
	&.teaser__icons {
		right: 0.5rem;
		top: 1.5rem;

		@include media(max-width $mobile-breakpoint-max) {
			top: 1.75rem;
		}
	}
}

// BMO spezifisch weil mobil das Teaser mit Bild left per css umgedreht wurde.
.pub-bmo {
	.teaser__img--left ~ .socialbar {
		&.teaser__icons {
			left: 0.5rem;
			top: 1.5rem;

			@include media(max-width $mobile-breakpoint-max) {
				right: 0.5rem;
				left: inherit;
				top: 1.75rem;
			}
		}
	}
}

.teaser-stream,
.teaser-leader,
.container-leader-opener {
	.socialbar {
		&.teaser__icons {
			position: absolute;

			li.socialbar__item {
				display: inline;
			}

			.share-button {
				.share-button__icon {
					background: $color-emph;
					border-radius: 0;
					font-size: 2em;
					padding: 0;
					height: 1em;
					width: 1em;

					&:before {
						color: #fff;
						vertical-align: top;
						line-height: 1em;
						padding: 0;
					}
				}
				&.video {
					.share-button__icon {
						&:before {
							@include icon--video-3;
							padding-left: 0;
						}
					}
				}
			}
		}
	}

	&.prio-1,
	&.prio-2,
	&.prio-4 {
		.socialbar.teaser__icons {
			left: 0.5em;
			top: 0.5em;
		}
	}

	&.prio-3,
	&.prio-5,
	&.prio-6 {
		.socialbar.teaser__icons {
			left: 0;
			top: 0;
		}
	}
}

.kachel-teaser {
	&.teaser-stream {
		.teaser-stream-content {
			.socialbar {
				&.teaser__icons {
					left: 0;
					padding: 0.5rem;
					visibility: visible;
				}
			}
		}
	}
}

.teaser-leader {
	.socialbar {
		&.teaser__icons {
			.share-button {
				.share-button__icon {
					margin: .5em;

					@include media(max-width $mobile-breakpoint-max) {
						margin: .25em;
						margin-left: 20px;
					}
				}
			}
		}
	}
}
