.compare-slider {
	margin-bottom: 2em;
	overflow: hidden;
	position: relative;
	width: 100%;
	font-family: $font-family-meta;

	* {
		user-select: none;
		display: block;
	}

	.copyright {
		text-align: right;
	}

	.txt + .copyright {
		margin-top: 0.5em;
	}

	.compare-slider--wrapper {
		position: relative;
	}

	.animate {
		transition: all 0.2s;
	}

	.compare-slider--year-left,
	.compare-slider--year-right {
		position: absolute;
		top: 5px;
		z-index: 4;
		background: rgba(0, 0, 0, 0.5);
		padding: 0.2em 0.5em;
		color: #fff;
	}

	.compare-slider--year-left {
		left: 5px;
	}

	.compare-slider--year-right {
		right: 5px;
	}

	.compare-slider--button-line {
		background: #fff;
		height: 100%;
		margin: auto;
		width: 2px;
		position: relative;
	}

	.compare-slider--button {
		cursor: col-resize;
		height: 100%;
		position: absolute;
		left: 40%;
		width: 20%;
		z-index: 3;
	}

	.compare-slider--img-left img,
	.compare-slider--img-right img {
		width: 100%;
		height: auto;
	}

	.compare-slider--img-left {
		overflow: hidden;
		position: absolute;
		width: 50%;
		z-index: 1;
	}

	.compare-slider--img-right {
		position: relative;
		width: 100%;
	}

	.compare-slider--button-line-bubble {
		height: 80px;
		width: 6px;
		top: calc(50% - 40px);
		position: relative;
		left: -2px;
		border-radius: 20px;
		background: #fff;
	}

	.compare-slider--button:hover {
		.compare-slider--button-line-arrowleft,
		.compare-slider--button-line-arrowright {
			color: rgba(255, 255, 255, 1);
			transition: all 0.2s;
		}

		.compare-slider--button-line-arrowleft {
			left: -1em;
		}

		.compare-slider--button-line-arrowright {
			right: -1em;
		}
	}

	.compare-slider--button-line-bubble {

		.compare-slider--button-line-arrowleft,
		.compare-slider--button-line-arrowright {
			color: rgba(255, 255, 255, 0.5);
			position: absolute;
			top: calc(50% - 0.5em);
			font-size: 1.5em;
		}

		.compare-slider--button-line-arrowleft {
			&:before {
				@include icon;
				@include icon--arrow-left-fat;
			}

			left: -0.75em;
		}

		.compare-slider--button-line-arrowright {
			&:before {
				@include icon;
				@include icon--arrow-right-fat;
			}

			right: -0.75em;

		}
	}

	.inline-media__caption,
	.compare-slider--intro {
		background: #f3f3f3;
		padding: 1em;
	}

	.inline-media__caption {
		font-size: 0.85em;
	}

	.compare-slider--intro {
		font-size: 1em;
		font-weight: bold;
	}

	.hidden {
		display: none;
	}
}