.new-article-popup {
	background-color: #fff;
	box-shadow: 0px 0px 10px 4px rgba(0, 0, 0, .7);
	display: none;
	font-family: $font-headline;
	font-weight: bold;
	position: fixed;
	top: 50%;
	left: 30%;
	padding: 1em;
	transition: all 0.5s linear;
	text-align: center;
	width: 600px;
	z-index: 13;

	@include media(max-width $mobile-breakpoint-max) {
		left: 0;
		top: 30%;
		width: 100%;
	}

	.new-article-popup h2 {
		font-family: $font-headline;
		font-size: 30px;
	}

	p.message {
		width: 100%;
	}

	.popup-content {
		padding: 0.5em;
	}

	.remodal-confirm:hover,
	.remodal-confirm:active {
		background: #fff;
		color: $color-emph;
	}

	.remodal-confirm {
		background: $color-emph;
		border: 1px solid $color-emph;
		font-size: 14px;
		letter-spacing: 1.5px;
		padding: 1.5em 0.5em;
		color: #fff;
		width: auto;
		text-transform: uppercase;

		@include media(max-width $mobile-breakpoint-max) {
			margin-top: 1em;
			width: 256px;
		}
	}

	.remodal-cancel:hover,
	.remodal-cancel:active {
		background: $color-emph;
		color: #fff;
	}

	.remodal-cancel {
		border: 1px solid rgba(173, 173, 173, 1);
		background: #fff;
		color: rgba(173, 173, 173, 1);
		font-size: 14px;
		letter-spacing: 1.5px;
		margin-right: 1em;
		padding: 1.5em 0.5em;
		text-transform: uppercase;
		width: auto;

		@include media(max-width $mobile-breakpoint-max) {
			margin: 0;
			width: 256px;
		}
	}

	.news-alert-close {
		float: right;
		color: $color-emph;

		&:before {
			@include icon;
			@include icon--close;
		}
	}
}

div#newsalert-overlay {
	background-color: rgba(0, 0, 0, 0.4);
	display: none;
	height: 100%;
	position: fixed;
	top: 0px;
	left: 0px;
	text-align: center;
	transition: all 0.5s linear;
	width: 100%;
	z-index: 12;
}