.teaser-leader-more {
	img {
		width: 100%;
	}

	&.adcontainer {
		padding-left: .9%;

		@media (max-width: $mobile-breakpoint-max) {
			display: none;
		}
	}

	.headline {
		padding: 0 0 $vertical-space-widgets;
		position: relative;

		&.headline-l {
			@include span-columns(9);
		}
	}

	.teaser-leader-more-image {
		position: relative;
	}

	.teaser-leader-more-icon {
		background-color: $white;
		border-radius: 50%;
		bottom: 15px;
		color: $color-emph;
		height: 50px;
		left: 15px;
		padding-top: 12px;
		position: absolute;
		text-align: center;
		width: 50px;
	}
}

.container-leader-more {
	.teaser-leader-more-icon {
		display: none;
	}
}

.container-leader-more-gallery {
	.teaser-leader-more-icon {
		display: block;
	}

	.teaser-leader-more-icon:before {
		@include icon;
		@include icon--galery;
		font-size: 1.25em;
	}
}

.container-leader-more-video {
	.teaser-leader-more-icon {
		display: block;
	}

	.teaser-leader-more-icon:before {
		@include icon;
		@include icon--video;
		font-size: 1.5em;
		margin-left: 5px;
	}
}

.teaser-leader.teaser-leader-more.teaser-leader-s img {
	width: 100%;
	height: 100%;
}

