/* ==========================================================================
   Remodal necessary styles
   ========================================================================== */

/* Hide scroll bar */
.remodal_lock {
	overflow: hidden;
}

/* Anti FOUC */

.abo-login {
	display: none;
}

.remodal-is-opened {
	.abo-login {
		display: block;
	}
}

/* ==========================================================================
   Remodal default theme
   ========================================================================== */

/* Overlay default theme styles */

.remodal-wrapper {
	z-index: 99999;

	&:after {
		margin-left: 0;
	}
}

.remodal-overlay {
	background-color: transparent;
	opacity: 0;
	z-index: 99999;

	&:after {
		content: "";
		display: inline-block;
		height: 100%;
		margin-left: -.05em;
	}
}

/* Modal dialog default theme styles */

.remodal {
	display: inline-block;
	max-width: inherit;
	margin: inherit;
	padding: 0;
	position: relative;
	width: inherit;

	@include media(min-width $mobile-breakpoint-min) {
		box-shadow: 0 0 1.25em $dark-grey;
		min-height: 0;
	}

	@include media(max-width $mobile-breakpoint-max) {
		-webkit-overflow-scrolling: touch;
	}

	/* Remodal anti-zoom. Font-size should be >= 16px. */
	select,
	textarea,
	input,
	select:focus,
	textarea:focus,
	input:focus {
		font-size: 16px;
	}
}

.remodal_active {
	.remodal-overlay {
		opacity: 1;
	}

	.remodal {
		transform: scale(1);
	}
}

/* Modal dialog vertical align  */

.remodal,
.remodal-overlay:after {
	vertical-align: middle;
}

/* Close button */

.remodal-close {
	border-radius: 50%;
	transition: background .2s linear;
	right: .875em;
	left: initial;
	top: .875em;
	will-change: auto;
	z-index: 1;

	@include media(max-width $mobile-breakpoint-max) {
		right: .625em;
		top: .625em;
	}

	&:before {
		@include icon;
		@include icon--close-circle;
		color: $lighter-grey;
		cursor: pointer;
		display: block;
		font-family: "icon-font" !important;
		font-size: 2em;
		margin-right: 0 !important;
		transition: all .2s linear;
		will-change: color;
	}

	&:hover,
	&:active {
		text-decoration: none;

		&.remodal-close {
			&:before {
				color: $black;
			}
		}
	}
}

/* Dialog buttons */

.remodal-confirm,
.remodal-cancel {
	background-clip: padding-box;
	border-radius: 50px;
	cursor: pointer;
	display: inline-block;
	font-size: 10pt;
	margin: 0 0 5px;
	padding: 9px 0;
	text-align: center;
	text-decoration: none;
	transition: all .2s linear;
	width: 120px;
}

.remodal-confirm {
	background: #f4f4f4;
	border: 2px solid #16a085;
	color: #16a085;

	&:hover,
	&:active {
		background: #16a085;
		color: #f4f4f4;
	}
}

.remodal-cancel {
	background: #f4f4f4;
	border: 2px solid #c0392b;
	color: #c0392b;

	&:hover,
	&:active {
		background: #c0392b;
		color: #f4f4f4;
	}
}

.remodal .article {
	@include span-columns(12 of 12);
	@include shift(0);
	@include omega;
	@include clearfix;
}
