$element-outer-space: 32px;
$element-space-large: 24px;
$element-space-smale: 16px;
$element-space-smaler: 8px;
$element-space-mobile: 10px;
$longform-split-column-large: 67%;
$longform-split-column-smale: 33%;
$longform-caption-space: .625rem;
$longform-decoration-color: $color-emph;
$longform-font: "Noto-Sans";
$longform-breakpoint-1080p: 1920px;
$longform-breakpoint-720p: 1280px;
$longform-breakpoint-tablet: 768px;
$longform-breakpoint-mobile: 640px;
$longform-breakpoint-mobile-min: 380px;

.theme-longform {
	font-family: $longform-font;
	font-weight: normal;
	font-style: normal;
	overflow-x: hidden;

	.theme-longform__logo {
		padding: 1em;
		position: absolute;
		z-index: 100;

		@media screen and (max-width: $longform-breakpoint-mobile) {
			padding-top: 0.75em;
		}

		a {
			display: inline-block;
			transition: all 0.2s;

			&:hover {
				opacity: 0.5;
			}
		}

		img {
			cursor: pointer;
			width: 200px;
		}
	}

	.header {
		border-top: none;
		margin: 0;
		&.header--sticky {
			display: none;
		}
	}

	.article__header {
		.socialbar__channels {
			&.socialbar__channels--round {
				.share-button__icon {
					line-height: 1.25em;
				}
			}
		}

		.socialbar {
			@media screen and (max-width: $longform-breakpoint-mobile) {
				padding-top: 0;
			}

			&.socialbar--ishidden {
				position: absolute;
				text-shadow: none;
				top: 1.25rem;
				z-index: 100;

				.socialbar__channels {
					&.socialbar__channels--round {
						.share-button__icon {
							@media screen and (max-width: $longform-breakpoint-mobile) {
								height: 2em;
								line-height: 0.85em;
								width: 2em;
							}

							&:before {
								@media screen and (max-width: $longform-breakpoint-mobile) {
									font-size: 0.75em;
								}
							}
						}
					}
				}

				.socialbar__share[type=checkbox]:checked ~ .socialbar__share--button {
					color: #fff;
				}

				.socialbar__share--button {
					color: #fff;
					text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.5);
					margin-top: 0.25em;

					&:before {
						font-size: 2em;

						@media screen and (max-width: $longform-breakpoint-mobile) {
							font-size: 1.25em;
						}
					}

					span {
						display: none;
					}
				}

				@media screen and (max-width: $longform-breakpoint-mobile) {
					top: 1rem;
				}

				&.socialbar--ishidden--left {
					left: 1.5rem;

					@media screen and (max-width: $longform-breakpoint-mobile) {
						left: 1rem;
					}
				}

				&.socialbar--ishidden--right {
					right: 1.5rem;

					@media screen and (max-width: $longform-breakpoint-mobile) {
						right: 1rem;
					}
				}
			}
		}
	}

	.main {
		@media screen and (max-width: $longform-breakpoint-tablet) {
			padding-top: 0;
		}
	}

	.article .headline_section.emph {
		display: none;
	}

	.article--type-longform {
		margin: 0;
		overflow: hidden;
		width: 100%;

		@media screen and (max-width: $longform-breakpoint-tablet) {
			padding-top: 0;
		}

		&.article {

			h1,
			h2,
			h3,
			h4,
			h5,
			h6,
			ol,
			ul {
				clear: both;
				color: #333;
				float: none;
				font-family: $longform-font;
				font-weight: normal;
				font-style: normal;
				margin: auto;
				width: $longform-split-column-smale;

				@media screen and (max-width: $longform-breakpoint-tablet) {
					width: 80%;
				}

				@media screen and (max-width: $longform-breakpoint-mobile) {
					width: 100%;
				}
			}

			ol,
			ul {
				margin-bottom: 1.125rem;

				@media screen and (max-width: $longform-breakpoint-mobile) {
					padding-left: 2.2rem;
				}
			}

			li {
				font-family: $longform-font;
			}

			.swiper-wrapper {
				margin: 0;
			}

			// Header Vertical align construction. Use Custom Classes in Pagebuilding Groups. Hierarchie beachten. Custom Gruppen mssen ineinander verschachtelt sein.

			.custom-article__header-wrapper {
				height: 100%;
				position: absolute;
				top: 0;
				width: 100%;

				.custom-article__header-wrapper-inner {
					display: table;
					height: 100%;
					width: 100%;

					.custom-article__header-wrapper-inner-cell {
						display: table-cell;
						height: 100%;
						padding-left: 10em;
						text-align: left;
						vertical-align: middle;

						@media screen and (max-width: $longform-breakpoint-tablet) {
							padding-left: 5em;
						}

						@media screen and (max-width: $longform-breakpoint-mobile) {
							padding-left: 1em;
						}
					}
				}
			}

			// ENDE Header Vertical align construction

			.hero-img,
			video-inline {
				margin-bottom: 0;
				padding-bottom: 0;

				.hero-img__text {
					display: none;
				}

				.hero-img__caption {
					background-color: transparent;
					bottom: 2em;
					display: inline-block;
					letter-spacing: normal;
					line-height: 1em;
					padding: 0;
					position: absolute;
					right: 3.375em;
					width: auto;

					&:before {
						display: none;
					}

					@media screen and (max-width: $longform-breakpoint-mobile) {
						position: relative;
						bottom: 0;
						right: 0;

						.hero-img__text {
							display: block;
							color: $lighter-grey;
							text-shadow: none;
							padding-left: 2.35765%;
						}
					}

					.hero-img__credits {
						font-style: italic;
						font-weight: normal;
						font-size: 12px;
					}
				}
			}

			// Article Header

			.article__header {
				text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
				padding: 0;
				position: relative;
				margin-bottom: 6em;

				@media screen and (max-width: $longform-breakpoint-tablet) {
					margin-bottom: 1.5em;
				}

				@media screen and (max-width: $longform-breakpoint-tablet) {
					padding: 0;
				}

				h1,
				h2,
				h3,
				h4,
				h5,
				h6 {
					color: #fff;
					width: 100%;
				}

				.article__header__kicker,
				.article__header__headline,
				.teaser-stream-time {
					color: #fff;
					font-weight: normal;
					text-transform: none;
				}

				.article__header__heading {
					padding: 0;
					width: 50%;
					font-weight: normal;

					@media screen and (max-width: $longform-breakpoint-tablet) {
						width: 60%;
					}

					@media screen and (max-width: $longform-breakpoint-mobile) {
						width: 100%;
					}
				}

				.article__header__kicker {
					font-size: 16px;
					padding: 0;
					display: block;

					&:after {
						width: 3em;
						border-top: 2px solid $longform-decoration-color;
					}
				}

				.teaser-stream-time {
					font-size: 14px;
				}

				.hero-img .hero-img__credits {
					font-family: $longform-font;
					color: #fff;
					display: table-cell;
					float: none;

					@media screen and (max-width: $longform-breakpoint-mobile) {
						display: block;
						padding-left: 2.35765%;
						color: $lighter-grey;
						text-shadow: none;
					}
				}

				.article__header__headline,
				.article__header__headline__heading {
					font-family: $longform-font;
					font-size: 40px;
					font-weight: normal;
					font-style: normal;
					text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);

					@media screen and (max-width: $longform-breakpoint-tablet) {
						font-size: 24px;
					}
				}

				.article__header__author__wrapper {
					clear: both;
					width: 100%;
				}

				.article__header__intro__text {
					border-bottom: 1px solid rgba(255, 255, 255, 0.5);
					border-top: 1px solid rgba(255, 255, 255, 0.5);
					color: #fff;
					font-size: 16px;
					margin: auto;
					max-width: 80%;
					padding: $vertical-space-widgets;
				}

				.article__header__inner {
					margin: 0;
					max-width: 100%;
					padding: 0;
					width: 100%;
					z-index: 1;

					* {
						margin: 0;
					}

					.article__header__tag {
						margin-bottom: 0.5rem;
						margin-right: 0.25rem;
						float: none;
					}
				}

				address,
				time {
					font-style: normal;
				}
			}

			// Article Body

			.article__body {
				margin: 0;
				padding: 0;
				width: 100%;

				@media screen and (max-width: $longform-breakpoint-tablet) {
					padding: 0;
				}

				figure {
					margin: 0;
				}

				iframe {
					margin-bottom: 0;
				}

				p {
					font-weight: normal;
					font-family: $longform-font;
					font-style: normal;
					font-size: 18px;
					line-height: 26px;
					margin: 1em auto;
					margin-top: 0;
					padding: 0;
					width: $longform-split-column-smale;

					@media screen and (max-width: $longform-breakpoint-tablet) {
						width: 80%;
					}

					@media screen and (max-width: $longform-breakpoint-mobile) {
						clear: both;
						font-size: 14px;
						line-height: 23px;
						padding: 0 $element-space-mobile;
						width: 100%;
					}
				}

				ol p,
				ul p {
					width: auto;
				}

				h2,
				h3,
				h4,
				h5,
				h6 {
					font-style: italic;
					font-weight: normal;
					padding-left: 0;
					margin-bottom: 1rem;

					@media screen and (max-width: $longform-breakpoint-mobile) {
						padding: 0 $element-space-mobile;
					}
				}

				h2 {
					font-size: 30px;

					@media screen and (max-width: $longform-breakpoint-mobile) {
						font-size: 22px;
					}
				}

				h3 {
					font-size: 26px;

					@media screen and (max-width: $longform-breakpoint-mobile) {
						font-size: 18px;
					}
				}

				.inline-media__media:before {
					display: none;
				}

				.inline-block {
					&.inline-block--full {
						margin-bottom: 0;
						@media screen and (max-width: $longform-breakpoint-tablet) {
							margin-bottom: 1.5em;
						}
					}
				}

				.player_brightcove_rendered {
					margin-bottom: 0 !important;
				}

				.teaser-container--inline-left,
				.teaser-container--inline-right {
					.hero-img__text {
						p {
							font-family: $longform-font;
							font-style: italic;
							font-size: 16px;
						}
					}
				}

				.article__header__intro,
				.teaser-article-summary {
					background: transparent;
					border: none;
					font-weight: bold;
					font-family: $longform-font;
					font-style: normal;
					margin: 0;
					margin-bottom: 1em;
					width: 100%;

					p {
						text-transform: none;
					}

					.article__header__intro__text,
					.teaser-article-summary-headline {
						color: #000;
						font-weight: bold;
						font-family: $longform-font;
						font-style: normal;

						@media screen and (max-width: $longform-breakpoint-mobile) {
							font-size: 16px;
							line-height: 23px;
						}
					}
				}

				.custom-article__intro {
					&.article__header__intro {
						clear: none;
						float: none;
						margin: 0 auto;
						width: 33%;

						@media screen and (max-width: $longform-breakpoint-mobile) {
							width: 100%;
						}

						p {
							display: inline-block;
							width: 100%;
						}
					}
				}

				.teaser-article-summary {
					clear: none;
					float: none;
					margin: 0 auto;
					width: 33%;
					overflow: visible;
					padding: 0;

					@media screen and (max-width: $longform-breakpoint-mobile) {
						width: 100%;
					}

					p {
						display: inline-block;
						width: 100%;
					}
				}

				.inline-media__caption {
					margin: 0;
				}

				.inline-media__caption,
				.inline-carousel.carousel-ajax.swiper-container .inline-media__caption,
				.hero-img__caption--video {
					color: #000;
					font-size: 16px;
					font-style: italic;
					margin-top: 0;
					padding-top: 0;
					background: transparent;
					border-bottom: none;
					padding-bottom: 0;
					&:before {
						content: "";
					}

					@media screen and (max-width: $longform-breakpoint-mobile) {
						font-size: 14px;
						margin-left: $element-space-smaler;
						> * {
							float: none;
						}
					}
				}

				.inline-media__caption-text,
				.inline-media__caption-credits {
					font-family: $longform-font;
					font-size: 16px;

					@media screen and (max-width: $longform-breakpoint-mobile) {
						font-size: 12px;
					}
				}

				.inline-media__caption-text,
				.inline-carousel.carousel-ajax.swiper-container .inline-media__caption-text {
					color: #000;
				}

				.inline-media__caption-credits,
				.inline-media__caption .rights,
				.inline-carousel.carousel-ajax.swiper-container .inline-media__caption-credits {
					color: #999;
					text-align: left;
					font-style: italic;
					margin-top: 1em;
					font-size: 1em;
					line-height: 1.4em;
				}

				.inline-media__caption .rights strong {
					font-weight: normal;
				}

				.inline-media {
					background: transparent;
					@media screen and (max-width: $longform-breakpoint-mobile) {
						width: 100%;
					}
					.inline-media__text,
					.inline-media__credits {
						color: #000;
						font-family: $longform-font;
						font-size: 16px;
						text-align: left;
						line-height: 1.5em;

						@media screen and (max-width: $longform-breakpoint-mobile) {
							font-size: 14px;
						}
					}
					.inline-media__text {
						margin-top: 0;
					}
				}

				.inline-media .inline-media__credits {
					color: #999;
				}

				.inline-media__wrapper {
					display: table;
					margin-bottom: 0.625em;
					width: 100%;
					padding: 0;
					line-height: 0;
				}

				// inline-block--wide

				.inline-block--wide {
					margin: $element-space-large 0;

					@media screen and (max-width: $longform-breakpoint-mobile) {
						margin: $element-space-large 0;
						padding: 0 $element-space-mobile;
					}

					.inline-media {
						margin: auto;
						width: $longform-split-column-smale;

						@media screen and (max-width: $longform-breakpoint-tablet) {
							width: 80%;
						}

						@media screen and (max-width: $longform-breakpoint-mobile) {
							width: 100%;
						}
					}

					.inline-media__caption,
					.hero-img__caption--video {
						border-left: 1px solid $longform-decoration-color;
						display: inline-block;
						padding-left: $element-space-smaler;
						line-height: 1.5em;
						margin: auto;
						margin-left: $element-space-smaler;
						width: calc(100% - #{$element-space-smale});

						@media screen and (max-width: $longform-breakpoint-tablet) {
							margin-left: $element-space-smaler;
						}
					}
				}

				// inline-block--full

				.inline-block--full {
					padding: $element-outer-space 0;
					overflow: auto;

					@media screen and (max-width: $longform-breakpoint-tablet) {
						margin: $element-space-large 0;
						padding: 0;
					}

					@media screen and (max-width: $longform-breakpoint-mobile) {
						margin: $element-space-large 0;
					}

					.inline-media__caption {
						border-left: 1px solid $longform-decoration-color;
						display: table;
						font-family: $longform-font;
						line-height: 1.5em;
						margin: auto;
						padding-left: $element-space-smaler;
						width: $longform-split-column-smale;

						@media screen and (max-width: $longform-breakpoint-tablet) {
							padding: 0 $element-space-smaler;
							width: 80%;
						}

						@media screen and (max-width: $longform-breakpoint-mobile) {
							margin: auto;
							padding: 0 $element-space-smaler;
							width: calc(100% - 16px);
						}

						&.hidden {
							display: none;
						}
					}
				}

				// inline-block--left, inline-block--right

				.inline-block--left,
				.inline-block--right {
					display: block;
					float: none;
					margin: 0;
					padding: $element-outer-space;
					position: relative;
					width: 100%;
					overflow: auto;

					@media screen and (max-width: $longform-breakpoint-tablet) {
						margin: 0;
						padding: $element-space-smale;
					}

					@media screen and (max-width: $longform-breakpoint-mobile) {
						margin: $element-space-large 0;
						padding: 0 $element-space-mobile;
					}

					.inline-media {
						position: relative;
					}

					.inline-media__wrapper {
						position: relative;
						margin-bottom: 0;

						@media screen and (max-width: $longform-breakpoint-mobile) {
							margin-bottom: $longform-caption-space;
						}
					}

					ul li {
						margin-bottom: 0;
					}

					.inline-media__caption {
						@media screen and (max-width: $longform-breakpoint-mobile) {
							margin-left: $element-space-smaler;
						}
					}

					.inline-carousel {
						.inline-media__wrapper {
							margin-bottom: 0;

							@media screen and (max-width: $longform-breakpoint-mobile) {
								margin-bottom: $longform-caption-space;
							}
						}
					}

					.inline-media__caption,
					.hero-img__caption--video {
						position: absolute;
						top: 0;
						width: calc(#{$longform-split-column-smale} - #{$element-space-smale});

						@media screen and (max-width: $longform-breakpoint-mobile) {
							position: relative;
						}

						@media screen and (max-width: $longform-breakpoint-mobile) {
							width: calc(100% - #{$element-space-smale});
						}
					}
				}

				.inline-block--left {
					.inline-media__caption,
					.hero-img__caption--video {
						border-left: 1px solid $longform-decoration-color;
						padding-left: $element-space-smaler;
						right: 0;
						margin-left: 0;
						margin-right: 0;
						line-height: 1.5;

						@media screen and (max-width: $longform-breakpoint-mobile) {
							margin-left: $element-space-smaler;
						}
					}

					.inline-media__media {
						display: block;
						padding-right: $longform-split-column-smale;
						margin: 0;

						@media screen and (max-width: $longform-breakpoint-mobile) {
							padding-right: 0;
						}
					}
				}

				.inline-block--right {
					.inline-media__caption,
					.hero-img__caption--video {
						border-left: none;
						border-right: 1px solid $longform-decoration-color;
						left: 0;
						padding-right: $element-space-smaler;
						line-height: 1.5;

						@media screen and (max-width: $longform-breakpoint-mobile) {
							border-right: none;
							border-left: 1px solid $longform-decoration-color;
							padding-right: 0;
							padding-left: $element-space-smaler;
						}
					}

					.inline-media__media {
						display: block;
						padding-left: $longform-split-column-smale;

						@media screen and (max-width: $longform-breakpoint-mobile) {
							padding-left: 0;
						}
					}

					.swiper-pagination {
						right: 0;
					}

					.swiper-container .swiper-buttons {
						right: 0;
					}

					.swiper-lazy-preloader {
						left: $longform-split-column-large;
					}
				}

				// inline-block--left-25, inline-block--right-25

				.inline-block--left-25,
				.inline-block--right-25 {
					display: block;
					padding: 0 $element-space-large;

					@media screen and (max-width: $longform-breakpoint-tablet) {
						padding: 0 $element-space-smale;
						width: 50%;
					}

					@media screen and (max-width: $longform-breakpoint-mobile) {
						width: 100%;
					}

					.carousel-ajax {
						.inline-media__media {
							img {
								min-height: 14vw !important;
								max-height: 14vw;

								@media screen and (max-width: $longform-breakpoint-tablet) {
									min-height: 34vw !important;
									max-height: 34vw;
								}

								@media screen and (max-width: $longform-breakpoint-mobile) {
									min-height: 14vw !important;
									max-height: 72vw;
								}
							}
						}

						.swiper-pagecount {
							border-top: none;
						}

						.swiper-buttons {
							@media screen and (max-width: $longform-breakpoint-tablet) {
								top: 14vw !important;
							}

							@media screen and (max-width: $longform-breakpoint-mobile) {
								top: 32vw !important;
							}

							@media screen and (max-width: $longform-breakpoint-mobile-min) {
								top: 30vw !important;
							}
						}
					}

					.inline-media__caption {
						border-left: 1px solid $longform-decoration-color;
						display: inline-block;
						font-family: $longform-font;
						line-height: 1.5em;
						margin-left: $element-space-smaler;
						padding-left: $element-space-smaler;
						width: 100%;

						@media screen and (max-width: $longform-breakpoint-mobile) {
							width: calc(100% - 16px);
						}

						&.hidden {
							display: none;
						}
					}
				}

				.inline-block--left-25 {
					margin-left: 4%;

					@media screen and (max-width: $longform-breakpoint-tablet) {
						margin-left: 8%;
						float: left;
					}

					@media screen and (max-width: $longform-breakpoint-mobile) {
						float: none;
						margin: $element-space-large 0;
						padding: 0 $element-space-mobile;
					}
				}

				.inline-block--right-25 {
					margin-right: 4%;

					@media screen and (max-width: $longform-breakpoint-tablet) {
						margin-right: 8%;
					}

					@media screen and (max-width: $longform-breakpoint-mobile) {
						float: none;
						margin: $element-space-large 0;
						padding: 0 $element-space-mobile;
					}
				}

				// Video

				.teaser-container--inline-left,
				.teaser-container--inline-right {
					display: inline-block;
					margin: 0;
					margin-left: $element-outer-space;
					margin-top: $element-space-large;
					margin-bottom: $element-space-large;
					width: calc(#{$longform-split-column-large} - 40px);

					@media screen and (max-width: $longform-breakpoint-tablet) {
						margin-left: #{$element-space-smale};
						width: calc(#{$longform-split-column-large} - #{$element-outer-space});
					}

					@media screen and (max-width: $longform-breakpoint-mobile) {
						margin: $element-space-large auto;
						width: calc(100% - #{$element-space-smale});
					}

					p {
						font-size: 1em;
						line-height: 1.5em;
						margin: 0;
						width: 100%;

						@media screen and (max-width: $longform-breakpoint-mobile) {
							padding: 0;
						}
					}

					.hero-img__caption--video {
						color: #000;
						font-size: 16px;
						font-style: italic;
						position: absolute;
						width: calc(#{$longform-split-column-smale} - #{$element-space-smaler});

						@media screen and (max-width: $longform-breakpoint-mobile) {
							clear: both;
							margin: auto;
							padding: 0 $element-space-smaler;
							position: relative;
							width: calc(100% - #{$element-space-smale});
						}
					}

					.player_container {
						display: block;
						float: left;
						width: 100%;

						@media screen and (max-width: $longform-breakpoint-mobile) {
							float: none;
							margin-bottom: $longform-caption-space;
						}
					}

					.hero-img__caption--video {
						@media screen and (max-width: $longform-breakpoint-mobile) {
							margin: auto;
							width: calc(100% - #{$element-space-smale});
						}
					}
				}

				.teaser-container--inline-left {
					@media screen and (max-width: $longform-breakpoint-tablet) {
						width: calc(#{$longform-split-column-large} - #{$element-space-large});
					}

					@media screen and (max-width: $longform-breakpoint-mobile) {
						display: block;
						padding: 0;
						width: calc(100% - #{$element-space-large});
					}

					.hero-img__caption--video {
						border-left: 1px solid $longform-decoration-color;
						right: 0;
						padding-left: $element-space-smaler;
					}
				}

				.teaser-container--inline-right {
					padding-left: calc(#{$longform-split-column-smale} - #{$element-space-large});
					width: calc(100% - 64px);

					@media screen and (max-width: $longform-breakpoint-tablet) {
						margin-left: 16px;
						width: calc(100% - #{$element-outer-space});
					}

					@media screen and (max-width: $longform-breakpoint-mobile) {
						display: block;
						margin-left: auto;
						padding: 0;
						width: calc(100% - 16px);
					}

					.hero-img__caption--video {
						border-right: 1px solid $longform-decoration-color;
						left: $element-outer-space;
						width: calc(#{$longform-split-column-smale} - 40px);

						@media screen and (max-width: $longform-breakpoint-tablet) {
							left: $element-space-smale;
							padding-right: $element-space-smaler;
						}

						@media screen and (max-width: $longform-breakpoint-mobile) {
							border-left: 1px solid $longform-decoration-color;
							border-right: none;
							left: 0;
							width: calc(100% - #{$element-space-smale});
						}

						.hero-img__text {
							span {
								display: inline-block;
								width: 100%;
							}
						}
					}
				}

				.teaser-container--inline-,
				.video-inline.inline-block-- {
					width: $longform-split-column-smale;
					margin: $element-space-large auto;

					@media screen and (max-width: $longform-breakpoint-tablet) {
						width: 80%;
						.hero-img__text {
							width: 100%;
						}
					}

					@media screen and (max-width: $longform-breakpoint-mobile) {
						width: 100%;
						padding: 0 10px;
					}

					.player_container {
						margin-bottom: $longform-caption-space;
					}

					.hero-img__caption--video {
						display: table;
						border-left: 1px solid $longform-decoration-color;
						color: #000;
						font-size: 16px;
						font-style: italic;
						padding-left: $element-space-smaler;
						width: 100%;
					}

					p {
						font-size: 16px;
						font-style: italic;
						margin: 0;
						width: 100%;
					}
				}

				.teaser-container--inline-left,
				.teaser-container--inline-right,
				.teaser-container--inline-wide {
					.hero-img__caption--video {
						span {
							color: #999;
							display: inline-block;
							margin-top: 1em;
							width: 100%;

							&:before {
								content: "Video: ";
							}
						}
						.hero-img__text {
							width: 100%;
						}
					}
				}

				.teaser-container--inline-wide {
					margin: $element-space-large 0;
					width: 100%;

					.video {
						margin-bottom: $longform-caption-space;
					}

					.hero-img__caption--video {
						border-left: 1px solid $longform-decoration-color;
						color: #000;
						display: table;
						font-size: 16px;
						font-style: italic;
						margin: auto;
						padding-left: $element-space-smaler;
						width: $longform-split-column-smale;

						@media screen and (max-width: $longform-breakpoint-tablet) {
							width: 80%;
						}

						@media screen and (max-width: $longform-breakpoint-mobile) {
							padding: 0 $element-space-smaler;
							width: calc(100% - #{$element-outer-space});
						}

						.hero-img__text {
							width: 100%;

							p {
								font-family: $longform-font;
								font-style: italic;
								font-size: 16px;
								line-height: 1.5em;
								margin: 0;
								width: 100%;

								@media screen and (max-width: $longform-breakpoint-mobile) {
									padding: 0;
								}
							}
						}
					}
				}
			}
		}
	}
}