@font-face {
	font-family: "icon-font";
	font-style: normal;
	font-weight: normal;
	src: url("../fonts/icon-font/fuzo/icon-font.woff2") format("woff"),
		url("../fonts/icon-font/fuzo/icon-font.woff") format("woff");
}

/* Icon */

@mixin icon() {
	display: inline-block;
	font-family: "icon-font";
	font-style: normal;
	font-weight: normal;
	line-height: 1;
	speak: none;
	text-decoration: inherit;
	text-rendering: optimizeLegibility;
	text-transform: none;
	vertical-align: middle;
	/* stylelint-disable */
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	/* stylelint-enable */
}

.icon {
	@include icon;
}


