.article {
	.article__body {
		.article__field__multi-quote {
			.article__header__intro,
			.teaser-article-summary {

				background: transparent;

				p {
					color: $color-text-default;

				}
			}

			.teaser-article-summary-headline {
				font-size: 1.5em;
			}
		}
	}
}

.article__field__multi-quote {
	margin-top: $vertical-space-widgets;
	padding: 0 $vertical-space-widgets;

	@include media(max-width $mobile-breakpoint-max) {
		padding: 0;
	}
}

.article__field__multi-quote_left {
	padding: 0;
	padding-left: $vertical-space-widgets;

	@include media(max-width $mobile-breakpoint-max) {
		padding-left: 0;
	}
}

.article__field__multi-quote_right {
	padding: 0;
	padding-right: $vertical-space-widgets;

	@include media(max-width $mobile-breakpoint-max) {
		padding-right: 0;
	}
}

.article__field__multi-quote {
	&.article__field__multi-quote_right,
	&.article__field__multi-quote_left {
		.teaser-article-summary {
			.teaser-article-summary-headline {
				font-size: 1em;
			}
		}
	}
}

.article__field__multi-quote {
	.teaser-article-summary {
		.teaser-article-summary-headline {
			font-weight: 100;
			display: block;
		}
	}
}
