/* ==========================================================================
   Remodal necessary styles
   ========================================================================== */

/* Hide scroll bar */
.remodal_lock {
	overflow: hidden;
}

/* Overlay necessary styles */

.remodal-overlay {
	bottom: 0;
	display: none;
	left: 0;
	overflow: auto;
	position: fixed;
	right: 0;
	text-align: center;
	top: 0;
	z-index: 9999;
}

.remodal-overlay:after {
	content: "";
	display: inline-block;
	height: 100%;
	margin-left: -.05em;
}

/* Modal dialog necessary styles */

.remodal {
	display: inline-block;
	position: relative;

	@include media(max-width $mobile-breakpoint-max) {
		/* stylelint-disable */
		overflow-scrolling: touch;
		left: 0;
		overflow-y: scroll;
		position: fixed;
		top: 0;
	}
}

/* Remodal anti-zoom. Font-size should be >= 16px. */

.remodal select,
.remodal textarea,
.remodal input,
.remodal select:focus,
.remodal textarea:focus,
.remodal input:focus {
	font-size: 16px;
}

/* ==========================================================================
   Remodal default theme
   ========================================================================== */


/* Overlay default theme styles */

.remodal-overlay {
	background-color: transparent;
	opacity: 0;
}

.remodal_active .remodal-overlay {
	opacity: 1;
}

/* Modal dialog default theme styles */

.remodal {
	@include media(min-width $mobile-breakpoint-min) {
		max-width: 100%;
	}

	@include media(max-width $mobile-breakpoint-max) {
		max-height: 100%;
		max-width: 100%;
		padding: 0;
	}
}

/* Modal dialog vertical align  */

.remodal,
.remodal-overlay:after {
	vertical-align: middle;
}

.remodal-close:before {
	color: $lighter-grey;
}

.remodal-close:hover,
.remodal-close:active {
	text-decoration: none;
}

.remodal-close:hover.remodal-close:before,
.remodal-close:active.remodal-close:before {
	color: $black;
}

/* Dialog buttons */

.remodal-confirm,
.remodal-cancel {
	background-clip: padding-box;
	border-radius: 50px;
	cursor: pointer;
	display: inline-block;
	font-size: 10pt;
	margin: 0 0 5px;
	padding: 9px 0;
	text-align: center;
	text-decoration: none;
	transition: all .2s linear;
	width: 120px;
}

.remodal-confirm {
	background: $color-bg-light;
	border: 2px solid $color-bg-overlay-form;
	color: $black;
}

.remodal-confirm:hover,
.remodal-confirm:active {
	background: $color-bg-overlay-form;
	color: $color-bg-light;
}

.remodal-cancel {
	background: $color-bg-light;
	border: 2px solid $black;
	color: $black;
}

.remodal-cancel:hover,
.remodal-cancel:active {
	background: $black;
	color: $color-bg-light;
}

.remodal .article {
	@include span-columns(12 of 12);
	@include shift(0);
	@include omega;
	@include clearfix;
}

