.theme-longform {
	.article {
		.article__header {
			.hero-img__caption {
				border: none;
			}
		}
	}

	.socialbar {
		border: none;
	}

	.theme-longform__logo {
		img {
			@media screen and (max-width: $longform-breakpoint-mobile) {
				width: 130px;
			}
		}
	}

	.footer {
		margin-bottom: 0;
	}
}