// author-info-name

.author-info__name {
	font-style: $author-info_name-fontStyle;
	font-weight: $author-info_name-fontWeight;
	grid-column: author-left;
	margin-bottom: 1rem;
	line-height: 1.25em;
}

.author-info__image ~ .author-info__name {
	grid-column: author-right;
}

.author-info__name-text {
	color: $author-info_name-color;
	font-family: $author-info_name-font;
	font-size: $author-info_name-fontSize;
	font-style: $author-info_name-fontStyle;
	font-weight: $author-info_name-fontWeight;

	@media (max-width: $mobile-breakpoint-max) {
		font-size: $author-info_name-fontSize-mobile;
	}
}

.author-info__name-prefix {
	color: $author-info_name-prefix-color;
	font-family: $author-info_name-prefix-font;
	font-size: $author-info_name-prefix-fontSize;
	font-style: $author-info_name-prefix-fontStyle;
	font-weight: $author-info_name-prefix-fontWeight;

	@media (max-width: $mobile-breakpoint-max) {
		font-size: $author-info_name-prefix-fontSize-mobile;
	}
}

.author-info__name-suffix {
	color: $author-info_name-suffix-color;
	font-family: $author-info_name-suffix-font;
	font-size: $author-info_name-suffix-fontSize;
	font-style: $author-info_name-suffix-fontStyle;
	font-weight: $author-info_name-suffix-fontWeight;

	@media (max-width: $mobile-breakpoint-max) {
		font-size: $author-info_name-suffix-fontSize-mobile;
	}
}

.sidebar {
	.sidebar__column {
		.author-info__name {
			margin-bottom: 0.5rem;
		}
	}
}

// Benutzung mit Verfasser Artikelfeld bentigt custom-article__author am Artikelfeld Widget
.article__field__multi {
	.author-info {
		address {
			margin: 0;
		}
	}
	.author-info__image ~ .article__header__author__wrapper {
		grid-column: author-right;
	}

	.author-info__name {
		margin-bottom: 0;
		display: inline;
	}
}

.author-info_wrapper {
	.author-info__name {
		margin-bottom: 0.5rem;
	}
}

.author-info__name-link {
	.author-info__name-text {
		color: inherit;
	}
}