.article {
	.adcontainer-billboard {
		p {
			color: $color-text-light;
			font-size: $font-size-extras-tiny;
			font-weight: $font-weight-regular;
			margin: 0;
			padding: $vertical-space-widgets-half;
		}
	}

	.hero-img__caption {
		display: inline-block;
		width: 100%;
	}

	.hero-img__text,
	.hero-img__credits {
		margin-top: $vertical-space-widgets-half;
	}
}
