.page__404 {
	@include span-columns(16 of 16);
	@include omega;
	background: $white;
	padding: 1.5em;
	margin-bottom: 16px;
	padding-left: 3em;
	padding-right: 3em;

	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(16 of 16);
		@include shift(0);
		@include pad(0 $space-innerpage);
	}

	.page__404__heading {
		font-family: inherit;
		border-bottom: 1px solid $black;
		font-size: 2em;
		padding-bottom: 16px;

		@include media(max-width $mobile-breakpoint-max) {
			font-size: $article-intro-fontsize;
		}
	}

	.page__404__text {
		font-size: $article-intro-fontsize;

		@include media(max-width $mobile-breakpoint-max) {
			font-size: $article-text-fontsize;
		}

		a {
			color: $color-emph;

			&:hover {
				color: $color-bg-dark;
			}
		}
	}

	.page__404__button {
		@include span-columns(4 of 12);
		@include omega;
		@include clearfix;
		border-radius: .3125em;
		background-color: $white;
		border-color: $dark-grey;
		border-style: solid;
		border-width: 2px;
		cursor: pointer;
		font-family: $font-family-meta;
		font-weight: $font-weight-bold;
		padding: .3125em 0;
		text-align: center;

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
			margin-bottom: 1.25em;
			margin-top: 1.25em;
			text-align: center;
		}

		&:hover {
			text-decoration: none;
		}
	}
}
