// teaser--large
.teaser--large {
	@include media(max-width $mobile-breakpoint-max) {
		padding: $vertical-space-widgets 0;
	}

	.more {
		font-size: $article-heroimage-credits-fontsize;
	}

	.teaser__body {
		@include span-columns(8 of 12);

		@include media(max-width $mobile-breakpoint-max) {
			display: none;
		}
	}

	.teaser__identifier {
		~ .teaser__header {
			@include span-columns(12 of 12);
		}
	}
}

@include media(max-width $mobile-breakpoint-max) {
	.teaser--large {
		border-bottom: 1px solid $nearly-lightest-grey;
	}
}

.teaser--large .teaser__header .teaser__headline {
	@include media(max-width $mobile-breakpoint-max) {
		font-size: 1.125em;
	}

	@include media(max-width $mobile-breakpoint-medium-max) {
		font-size: 1em;
	}
}

// TODO: Constants!
.teaser--large .teaser__headline {
	font-size: 1.4375em;

	@include media(max-width $mobile-breakpoint-max) {
		font-size: 1em;
	}
}

.teaser--large .teaser__img.teaser__img--left,
.teaser--large .teaser__img.teaser__img--right {
	@include span-columns(3 of 8);

	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(4 of 12);
		margin-bottom: .4em;
		margin-top: 5px;
	}

	+ .teaser__header,
	~ .teaser__body {
		@include span-columns(5 of 8);

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(8 of 12);
		}
	}
}

.teaser--large .teaser__img.teaser__img--right {
	float: right;
	margin-right: 0;
}

.teaser.teaser--large .teaser__header,
.teaser--large .teaser__body {
	@include span-columns(5 of 8);
	display: inherit;
}

.teaser--large.no--img .teaser__header,
.teaser--large.no--img .teaser__body {
	@include span-columns(8 of 8);
}

.teaser--large .teaser__img--left + .teaser__header,
.teaser--large .teaser__img--right {
	@include clearfix;
}

.teaser--large.teaser--noimg {
	.teaser__body,
	.teaser__header {
		@include span-columns(12 of 12);
		@include omega;
		@include clearfix;
	}
}

.teaser.teaser--large .teaser__body {
	font-weight: normal;

	@include media(max-width $mobile-breakpoint-medium-max) {
		display: none;
	}
}
