.swiper-container {
	font-family: $font-extra;
	margin: 0 auto;
	overflow: hidden;
	position: relative;
	/* Fix of Webkit flickering */
	z-index: 1;

	.teaser-media-img__text,
	.hero-img__text,
	.hero-media__caption-text,
	.inline-media__caption-text {
		@include span-columns(12);
		margin-right: 0;
		text-align: left;

		@media (max-width: $mobile-breakpoint-max) {
			@include span-columns(12);
			@include omega;
		}
	}

	figure {
		margin: 0;
		width: 100%;
	}

	.hero-img__credits {
		text-align: right;
	}

	.hero-img__text,
	.hero-img__credits {
		margin-top: $vertical-space-widgets-half;
	}

	.teaser-media .teaser-media__caption {
		margin: 0;
	}

	.teaser-media-img__text a,
	.hero-media__caption-text a {
		color: $color-text-light;
		font-size: .75em;
		font-weight: normal;
	}

	.teaser-media-img__credits,
	.hero-img__credits,
	.hero-media__caption-credits,
	.inline-media__caption-credits {
		@include span-columns(12);
		color: $color-swiper-caption-credits;
		font-weight: normal;
		font-style: normal;
		text-align: right;

		@media (max-width: $mobile-breakpoint-max) {
			@include span-columns(12);
			@include omega;
			text-align: left;
		}
	}

	.hero-media__caption-credits,
	.inline-media__caption-credits {
		text-align: right;
	}

	.inline-media .inline-media__caption {
		line-height: 1.5em;
	}

	.hero-media__caption-text + .hero-media__caption-credits {
		margin-top: 1em;
	}

	.hero-img__text + .hero-img__credits,
	.inline-media__caption-text + .inline-media__caption-credits {
		margin-top: 1em;
	}

	.inline-block {
		margin-bottom: 1em;
		display: flex;
	}

	.hero-block {
		margin-bottom: 1em;
		display: flex;
	}

	.swiper-buttons {
		color: $color-swiper-buttons;
		font-size: 48px;
		opacity: 1;
		padding: 0 0.25em;
		pointer-events: none;
		position: absolute;
		transition: $transition-fast;
		top: 40%;
		width: 100%;
		z-index: 100;

		@media (max-width: $mobile-breakpoint-max) {
			font-size: 36px;
		}
	}

	img {
		margin: auto;
	}
}

/* Arrows */
.swiper-button-prev,
.swiper-button-next {
	cursor: pointer;
	display: inline-flex;
	pointer-events: auto;
	position: relative;

	&.swiper-button-disabled,
	&.swiper-button-next.swiper-button-disabled {
		cursor: auto;
		opacity: .35;
		pointer-events: none;
	}
}

.swiper-button-prev {
	float: left;

	&:before {
		@include icon;
		@include icon--arrow-left;
		font-weight: 900;
	}
}

.swiper-button-next {
	float: right;

	&:before {
		@include icon;
		@include icon--arrow-right;
		font-weight: 900;
	}
}

.swiper-pagecount {
	color: $color-swiper-pagecount;
	display: block;
	font-weight: bold;
	font-size: 14px;
	padding: 0 1em;
	position: relative;
	text-align: center;
	width: 100%;
}

.swiper-galerie-content {
	margin-bottom: $vertical-space-widgets;
	position: relative;

	figure {
		text-align: center;
	}
}

.galerie-icon {
	&:before {
		@include icon;
		@include icon--galery;
	}
	color: $color-text-light;
	display: none;
	position: absolute;
	right: 1em;
	top: 1em;
	z-index: 5;
}

.swiper-container-no-flexbox .swiper-slide {
	float: left;
}

.swiper-container-vertical > .swiper-wrapper {
	/* stylelint-disable */
	box-orient: vertical;
	/* stylelint-enable */
	flex-direction: column;
}

.swiper-wrapper {
	box-sizing: content-box;
	display: flex;
	transform-style: preserve-3d;
	transition-property: transform;
	height: 100%;
	position: relative;
	width: 100%;
	z-index: 1;
}

figure.hero-media__media {
	margin: 0;
	position: relative;
}

figure.teaser-media__media {
	margin-top: 0;
	position: relative;
}

.swiper-container-android .swiper-slide,
.swiper-wrapper {
	transform: translate3d(0, 0, 0);
}

.swiper-container-multirow > .swiper-wrapper {
	/* stylelint-disable */
	box-lines: multiple;
	/* stylelint-enable */
	flex-wrap: wrap;
}

.swiper-container-free-mode > .swiper-wrapper {
	transition-timing-function: ease-out;
	margin: 0 auto;
}

.swiper-slide {
	transform-style: preserve-3d;
	flex: 0 0 auto;
	flex-shrink: 0;
	// height: 100%;
	position: relative;
	width: 100%;

	img[data-src*="resources"] {
		opacity: 0;
	}

	img {
		display: block;
		width: 100%;
		height: 100%;
		color: transparent;
		border: none;
	}

	&.swiper-slide-ad {
		display: table;
		height: 100%;
		max-height: 350px;
	}
}

/* a11y */
.swiper-container .swiper-notification {
	left: 0;
	opacity: 0;
	pointer-events: none;
	position: absolute;
	top: 0;
	z-index: -1000;
}

/* IE10 Windows Phone 8 Fixes */
.swiper-wp8-horizontal {
	touch-action: pan-y;
}

.swiper-wp8-vertical {
	touch-action: pan-x;
}

.container-article-content {
	.swiper-buttons {
		font-size: 36px;
	}
}

/* Pagination Styles */
.swiper-pagination {
	transform: translate3d(0, 0, 0);
	transition-duration: 300ms;
	text-align: center;
	z-index: 10;
	display: none;
	width: 100%;
}

.swiper-pagination.swiper-pagination-hidden {
	opacity: 0;
}

.swiper-pagination-bullet {
	background: $color-swiper-bullets;
	border-radius: 100%;
	display: inline-block;
	height: 12px;
	width: 12px;

	&.swiper-pagination-bullet-active {
		background: $color-swiper-bullets-active;
		opacity: 1;
	}
}

.swiper-pagination-clickable .swiper-pagination-bullet {
	cursor: pointer;
}

.swiper-container-vertical > .swiper-pagination {

	right: 10px;
	top: 50%;
}

.swiper-pagination .swiper-pagination-bullet {
	margin: 0 2px;
}

.swiper-container-horizontal > .swiper-pagination .swiper-pagination-bullet {
	margin: 0 5px;
}

/* 3D Container */
.swiper-container-3d {
	perspective: 1200px;
}

.swiper-container-3d .swiper-wrapper,
.swiper-container-3d .swiper-slide,
.swiper-container-3d .swiper-slide-shadow-left,
.swiper-container-3d .swiper-slide-shadow-right,
.swiper-container-3d .swiper-slide-shadow-top,
.swiper-container-3d .swiper-slide-shadow-bottom,
.swiper-container-3d .swiper-cube-shadow {
	transform-style: preserve-3d;
}

.swiper-container-3d .swiper-slide-shadow-left,
.swiper-container-3d .swiper-slide-shadow-right,
.swiper-container-3d .swiper-slide-shadow-top,
.swiper-container-3d .swiper-slide-shadow-bottom {
	height: 100%;
	left: 0;
	pointer-events: none;
	position: absolute;
	top: 0;
	width: 100%;
	z-index: 10;
}

.swiper-container-3d .swiper-slide-shadow-left {
	background-image: linear-gradient(to left, rgba(0, 0, 0, .5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-right {
	background-image: linear-gradient(to right, rgba(0, 0, 0, .5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-top {
	background-image: linear-gradient(to top, rgba(0, 0, 0, .5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-bottom {
	background-image: linear-gradient(to bottom, rgba(0, 0, 0, .5), rgba(0, 0, 0, 0));
}

/* Coverflow */
.swiper-container-coverflow .swiper-wrapper {
	/* Windows 8 IE 10 fix */
	perspective: 1200px;
	//-ms-perspective: 1200;
}

/* Fade */
.swiper-container-fade.swiper-container-free-mode .swiper-slide {
	transition-timing-function: ease-out;
}

.swiper-container-fade .swiper-slide {
	pointer-events: none;
}

.swiper-container-fade .swiper-slide .swiper-slide {
	pointer-events: none;
}

.swiper-container-fade .swiper-slide-active,
.swiper-container-fade .swiper-slide-active .swiper-slide-active {
	pointer-events: auto;
}

.swiper-container-horizontal {
	.galerie-icon {
		display: none;
	}
}

/* Cube */
.swiper-container-cube {
	overflow: visible;
}

.swiper-container-cube .swiper-slide {
	backface-visibility: hidden;
	transform-origin: 0 0;
	height: 100%;
	pointer-events: none;
	visibility: hidden;
	width: 100%;
}

.swiper-container-cube.swiper-container-rtl .swiper-slide {
	transform-origin: 100% 0;
}

.swiper-container-cube .swiper-slide-active,
.swiper-container-cube .swiper-slide-next,
.swiper-container-cube .swiper-slide-prev,
.swiper-container-cube .swiper-slide-next + .swiper-slide {
	pointer-events: auto;
	visibility: visible;
}

.swiper-container-cube .swiper-cube-shadow {
	background: $black;
	bottom: 0;
	filter: blur(50);
	height: 100%;
	left: 0;
	opacity: .6;
	position: absolute;
	width: 100%;
}

.swiper-container-cube.swiper-container-vertical .swiper-cube-shadow {
	z-index: 0;
}

/* Scrollbar */
.swiper-scrollbar {
	background: rgba(0, 0, 0, .1);
	border-radius: 10;
	position: relative;
}

.swiper-container-horizontal > .swiper-scrollbar {
	bottom: 3px;
	height: 5px;
	left: 1%;
	position: absolute;
	width: 98%;
	z-index: 50;
}

.swiper-container-vertical > .swiper-scrollbar {
	height: 98%;
	position: absolute;
	right: 3px;
	top: 1%;
	width: 5px;
	z-index: 50;
}

.swiper-scrollbar-drag {
	background: rgba(0, 0, 0, .5);
	border-radius: 10;
	height: 100%;
	left: 0;
	position: relative;
	top: 0;
	width: 100%;
}

.swiper-scrollbar-cursor-drag {
	cursor: move;
}

/* Preloader */
.swiper-lazy-preloader {
	// animation: swiper-preloader-spin 1s steps(12, end) infinite;
	transform-origin: 50%;
	height: 60px;
	left: 50%;
	margin-left: -30px;
	margin-top: -30px;
	position: absolute;
	top: 50%;
	width: 60px;
	z-index: 10;
}

.swiper-lazy-preloader:after {
	background-image: url(../img/lazyloader.svg);
	background-position: 50%;
	background-repeat: no-repeat;
	background-size: 100%;
	content: "";
	display: block;
	height: 100%;
	width: 100%;
}

.swiper-lazy-preloader-white:after {
	background-image: url(../img/lazyloader.svg);
}

@keyframes swiper-preloader-spin {
	100% {
		transform: rotate(360deg);
	}
}

.teaser-stream,
.article__body {
	.swiper-vertical-image {
		max-height: 380px;
		width: auto;

		@media (max-width: $mobile-breakpoint-max) {
			max-height: 63vw;
		}

		@media (max-width: $mobile-breakpoint-medium-max) {
			max-height: 62vw;
		}

		@media (max-width: $mobile-breakpoint-mini) {
			max-height: 60vw;
		}
	}
}

@media (max-width: $mobile-breakpoint-max) {
	.container-leader .swiper-pagecount {
		display: none;
	}

	.container-leader .swiper-pagination {
		display: block;
		bottom: 0;
		padding: 0.5em;
	}

	.container-leader .teaser-leader.teaser-leader-l,
	.container-leader .teaser-leader.teaser-leader-m,
	.container-leader .teaser-leader.teaser-leader-s {
		background-size: contain;
		margin-bottom: 0 !important;
	}

	.container-leader .swiper-container {
		font-family: Georgia;
	}

	.container-leader .swiper-pagination-bullet {
		background: $color-swiper-bullets;
	}

	.container-leader .swiper-pagination-bullet-active {
		background: $color-swiper-bullets-active;
	}

	.container-leader.swiper-container {
		font-family: inherit !important;
	}

	.container-leader {
		height: auto !important;
	}
}

.swiper-slide.swiper-slide-duplicate.swiper-slide-prev,
.swiper-slide.swiper-slide-duplicate.swiper-slide-next {
	visibility: hidden;
}

ul.swiper-wrapper {
	padding: 0;
	margin: 0;
	padding-left: 0;
}

li.swiper-slide {
	display: table;
	list-style: none;
	table-layout: fixed;
}

li.swiper-slide::-ms-backdrop {
	display: block;
}

.article {
	.swiper-container {
		figure.hero-media__media {
			img {
				margin: auto;
			}
		}
	}

	.article__body {
		ul.swiper-wrapper {
			padding-left: 0;
			margin-bottom: 0;
		}
	}
}

