// Longform Article Umfrage

.theme-longform {
	.article {
		&.article--type-longform {
			.article__field__multi-quote {
				.teaser-article-summary {
					width: 100%;

					&:before {
						display: none;
					}

					.teaser-article-summary-headline {
						font-weight: normal;
						font-size: 30px;
						line-height: 40px;
						font-style: italic;
						color: #666;
					}
				}
			}
		}
	}
}
