header {
	@include outer-container;
	background-color: $color-bg-light;
	position: relative;
	z-index: 100000;

	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(12);
		transition: $transition-fast;
		margin-bottom: 0;
		z-index: 1000;
	}

	.header-top {
		@include span-columns(12);
		@include omega;
		transition: $transition-fast;
		border-bottom: 3px solid $green;
		display: inline-block;
		padding: 0.75em;

		@include media(max-width $mobile-breakpoint-max) {
			background-color: $color-bg-lighter;
			border-bottom: 1px solid $color-text-black;
			padding-bottom: .5em;
			position: fixed;
			top: 0;
		}

		.logo {
			@include span-columns(8);

			@include media(max-width $mobile-breakpoint-mini) {
				@include span-columns(7);
			}

			.emph {
				font-weight: $font-weight-regular;
				position: relative;
				line-height: 1.5em;

				@include media(max-width $mobile-breakpoint-max) {
					font-size: 1em;
					line-height: 1.1em;
				}
			}

			h1 {
				line-height: 0;
			}

			.beta {
				background-color: $color-emph;
				color: $white;
				display: inline-block;
				font-size: 0.5em;
				line-height: 1.5em;
				padding: 0 0.25em;
				vertical-align: top;

				@include media(max-width $mobile-breakpoint-max) {
					display: none;
				}
			}

			.portalname {
				color: $color-text-black;
				float: left;
				margin-right: 0.5em;
				width: 330px;
			}

			.portalname img,
			.portalname svg {
				height: auto;

				@include media(max-width $mobile-breakpoint-max) {
					display: none;
				}
			}

			.portalcity img,
			.portalcity svg {
				transition: $transition-fast;

				@include media(max-width $mobile-breakpoint-max) {
					height: 40px;
					width: auto;
				}

				@include media(max-width $mobile-breakpoint-mini) {
					height: 26px;
					width: auto;
				}
			}

			.portalcity {
				display: none;
				vertical-align: middle;
				margin-right: 0.25em;

				@include media(max-width $mobile-breakpoint-max) {
					color: $black;
					display: inline-block;
				}
			}

			.portalname--text {
				height: 0;
				text-indent: 100%;
				white-space: nowrap;
				overflow: hidden;
				width: 0;
				display: block;
			}
		}

		.meta-menu {
			@include span-columns(4);
			@include omega;
			font-size: $font-size-text-small;
			margin-top: 10px;
			text-align: right;

			@include media(max-width $mobile-breakpoint-mini) {
				@include span-columns(5);
				margin-top: 0;
			}
		}

		.meta-menu > * {
			line-height: $font-size-headline-m;
		}

		.temperature {
			transition: $transition-fast;
			color: $color-text-light;
			font-family: $font-extra;
			font-size: $font-size-extras-tiny;
			font-weight: 500;
			margin-right: 1.5em;
			vertical-align: sub;

			@include media(max-width $mobile-breakpoint-mini) {
				margin-right: $vertical-space-widgets-half;
			}
		}

		.icon-wetter {
			line-height: 1em;
			margin-left: 0.5em;
		}

		.icon-wetter:before {
			transition: $transition-fast;
			color: $color-emph;
			font-size: 2em;
		}

		// Wetter Special Font-size Normalisierung

		.wetter--icon-gefrierender-regen-tag:before,
		.wetter--icon-regenschauer-tag:before {
			font-size: 2.5em;
		}

		.wetter--icon-stark-bewoelkt-nacht:before,
		.wetter--icon-nebel-tag:before,
		.wetter--icon-stark-bewoelkt-tag:before,
		.wetter--icon-schneeregen-tag:before,
		.wetter--icon-heiter-nacht:before {
			font-size: 1.75em;
		}

		.wetter--icon-bedeckt-tag:before,
		.wetter--icon-sonnig-nacht:before {
			font-size: 1.5em;
		}

		.icon {
			transition: $transition-fast;
			color: $color-text-default;
			cursor: pointer;
			font-size: $font-size-headline-s;
		}

		.icon:hover {
			color: $color-text-light;
			cursor: pointer;
			font-size: $font-size-headline-s;
		}
	}
}

.nav-bar {
	.meta-menu {
		display: none;
		float: right;
		padding: 4px 1em;

		.nav-bar .icon-wetter:after {
		}
	}

	.temperature {
		transition: $transition-fast;
		color: $color-text-light;
		font-family: $font-extra;
		font-size: $font-size-extras-tiny;
		font-weight: 500;
		margin-right: 0.5em;
		vertical-align: sub;

		@include media(max-width $mobile-breakpoint-mini) {
			margin-right: $vertical-space-widgets;
		}
	}

	.icon-wetter {
		color: $color-emph;
		line-height: 0.5em;
		margin-left: 0.5em;
	}

	.icon-wetter:after {
		transition: $transition-fast;
		color: $color-emph;
		font-size: 2.5em;
	}

	.icon {
		transition: $transition-fast;
		color: $color-text-default;
		cursor: pointer;
		font-size: $font-size-headline-s;
	}

	.icon:hover {
		color: $color-text-light;
		cursor: pointer;
		font-size: $font-size-headline-s;
	}
}

.header.sticky {
	.header-top {
		transition: $transition-medium;
	}

	.nav-bar {
		.icon-wetter {
			font-size: 18px;
		}
	}

	@include media(max-width $mobile-breakpoint-max) {
		.portalcity img,
		.portalcity svg {
			height: 26px;
		}

		.icon-wetter:after {
			font-size: 2.5em;
		}

		.meta-menu {
			margin-top: 0;
		}
	}

	@include media(max-width $mobile-breakpoint-mini) {
		.portalcity img,
		.portalcity svg {
			height: 20px;
		}

		.logo .emph {
			font-size: 16px;
		}
	}
}

.header-top.hidden {
	top: -60px;
}

.nav-bar {
	@include span-columns(12);
	@include omega;
	background-color: $color-bg-light-medium;
	box-shadow: inset 0 -3px 0 $color-bg;
	display: table;
	font-family: $font-extra;
	margin-bottom: 0;
	max-width: $pagewidth;
	position: relative;

	@include media(max-width $mobile-breakpoint-max) {
		transition: $transition-fast;
	}

	.portalname {
		color: $color-text-black;
	}

	.logo {
		transition: margin .5s;
		background: $white;
		border: none;
		display: block;
		height: 0;
		margin-left: -20%;
		width: 0;

		@include media(max-width $mobile-breakpoint-max) {
			margin-left: -50%;
		}
	}

	@include media(max-width $mobile-breakpoint-max) {
		background-color: transparent;
		bottom: 0;
		box-shadow: none;
		left: 0;
		margin-bottom: 0;
		overflow: hidden;
		position: fixed;
	}

	nav {
		ul {
			font-size: 0;
			list-style: none;
			margin: 0;
			padding: 0;
		}

		li {
			display: inline-block;
			line-height: 2.5em;
			margin: 0;
		}

		a {
			display: inline-block;
			height: 100%;
			padding: 0 $vertical-space-widgets;
			text-decoration: none;
		}

		@include media(max-width $mobile-breakpoint-max) {
			border-top: 1px solid $color-text-black;
		}
	}

	.nav-main {
		//.header nav:first-child {
		@include span-columns(8);
		@include omega;
		transition: $transition-medium;
		overflow: hidden;
		position: relative;
		z-index: 10;

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12);
			@include omega;
			background: $color-bg-light-medium;

			ul {
				box-shadow: inset 0 -3px 0 $color-bg;
				display: table;
				width: 100%;
			}

			li {
				@include span-columns(2 of 10);
			}

			a {
				font-size: $font-size-extras-small;
				padding: 0;
			}
		}

		li {
			transition: $transition-fast;
			background-color: $color-bg-light-medium;
			font-size: $font-size-extras-medium;
		}

		li a {
			border-bottom: 3px solid $color-bg;
			color: $black;
			font-weight: normal;
		}

		li:hover {
			background-color: $color-bg-lighter;
			border-color: $color-bg-lighter;
		}

		.nav-main__anchor--active {
			background-color: $color-bg-lighter;
			border-bottom: 3px solid $color-text-headline-default;
			color: $color-text-headline-default;
			font-weight: bold;
		}

		a {
			color: $color-text-light;
			text-align: center;
			width: 100%;
		}

		.logo {
			font-family: $font-default;
			font-weight: $font-weight-regular;
		}
	}

	.nav-meta {
		display: inline-block;
		float: right;

		@media (max-width: $mobile-breakpoint-max) {
			display: none;
		}

		ul {
			float: right;
			text-align: right;
		}

		li {
			font-size: $font-size-extras-tiny;
			margin-top: 6px;
		}

		.icon--search {
			transition: $transition-fast;
			color: $color-text-default;
			cursor: pointer;
			display: none;
			float: right;
			line-height: 2.5em;
		}

		.icon--search:hover {
			color: $color-text-light;
		}

		a {
			transition: $transition-fast;
			color: $black;
			font-weight: $font-weight-regular;
		}

		a:hover {
			color: $color-text-default;
		}
	}

	ul {
		.nav-main__list--level2 {
			background-color: $color-text-white;
			display: none;
			height: 43px;
			position: absolute;
			left: 0;
			width: 100%;
			z-index: 999;

			@include media(max-width $mobile-breakpoint-max) {
				bottom: 45px;
				height: auto;
			}
		}

		.hover-simulate .nav-main__list--level2 {
			display: block;
			opacity: 1;
			visibility: visible;
		}

		li {
			&:hover {
				transition: $transition-fast;
				background-color: $color-bg-light-medium;
			}
			.has-submenu a:first-child {
				height: 43px;
				top: 0px;
				text-align: left;
				width: 96px;
			}

			a {
				border-bottom: 0;
			}
		}
	}
}

.nav-bar.sticky {
	@include span-columns(12);
	max-width: $pagewidth;
	position: fixed;
	top: 0;
	z-index: 20;

	.meta-menu {
		display: inline-block;
	}

	.header nav:last-child {
		display: block;
	}

	.logo {
		border-bottom: 3px solid $white;
		display: inline-block;
		float: left;
		height: auto;
		line-height: 39px;
		margin-left: 0;
		width: auto;
	}

	a {
		border-bottom: none;
	}

	.portalname img,
	.portalname svg {
		height: 35px;
		margin-top: 3px;
		vertical-align: sub;
		width: 135px;

		@include media(max-width $mobile-breakpoint-max) {
			width: auto;
		}
	}

	.portalname {
		margin-right: 8px;
	}

	.logo .emph {
		font-size: 30px;
	}

	.nav-main {
		height: 43px;
	}

	.logo .emph {
		top: 0;
	}
}

.nav-bar.sticky > .search-bar {
	max-width: 960px;
	position: fixed;
	top: 43px;
	width: 100%;
	z-index: 10000;
}

.nav-bar.hidden {
	bottom: -50px;
}

.nav-bar {
	ul {
		.nav-main__list--level2 {
			li {
				background: $color-bg-lighter;
				border-bottom: 0;

				@include media(max-width $mobile-breakpoint-max) {
					border-top: 1px solid $light-grey;
					border-right: 1px solid $light-grey;
					margin: 0;
					width: 50%;
				}

				a {
					border-bottom: 3px solid $white;

					&:hover {
						border-bottom: 3px solid $black;
					}
				}
			}

			li:nth-child(even) {
				@include media(max-width $mobile-breakpoint-max) {
					border-right: none;
				}
			}
		}
	}
}
