.social-embed {
	transition: all, 1s;
	opacity: 1;
	// overflow: hidden;
	margin-bottom: $vertical-space-widgets;

	&.widget-hidden {
		opacity: 0;
		visibility: hidden;
	}

	.op-interactive {
		margin: 0;
		width: 100%;
	}

	iframe {
		display: block;
	}

	// Twitter
	.twitter-tweet::shadow {
		.EmbeddedTweet {
			max-width: 100%;
		}

		.MediaCard-widthConstraint {
			max-width: 100% !important;
		}

		.NaturalImage-image {
			width: 100%;
		}
	}

	.twitter-tweet {
		margin: 0 !important;
		max-width: 100% !important;
		width: 100% !important;
		min-width: 20px !important;
	}

	// Instagram
	iframe.instagram-media {
		width: 100% !important;
		max-width: 100% !important;
	}

	// Youtube
	iframe.youtubePlayer {
		width: 100% !important;
		max-width: 100% !important;
	}

	.fb_iframe_widget_fluid_desktop iframe {
		min-width: 20px !important;
	}

	.fb_iframe_widget span {
		width: 100% !important;
	}

	// Positions 100%, 50% , 25%, left and right
	&.inline-block--left-25,
	&.inline-block--right-25,
	&.inline-block--left,
	&.inline-block--right {
		width: 50%;

		@include media(max-width $mobile-breakpoint-max) {
			width: 100%;
		}
	}

	&.inline-block--right-25,
	&.inline-block--right {
		float: right;
		margin-left: 1.25em;
	}

	&.inline-block--left-25,
	&.inline-block--left {
		float: left;
		margin-right: 1.25em;
	}

	&.inline-block--wide,
	&.inline-block--left-25,
	&.inline-block--right-25,
	&.inline-block--left,
	&.inline-block--right {
		@include media(max-width $mobile-breakpoint-max) {
			float: none;
			margin-left: 0;
			margin-right: 0;
		}
	}
}

.scribble-live.inline-block--wide {
	margin-right: $vertical-space-widgets;
}

.article .article__body .social-embed iframe {
	padding: 0;
	margin-bottom: 0;
}

.padding-t {
	padding-top: 1em;
}

.padding-b {
	padding-bottom: 1em;
}

.mtop-1em {
	margin-top: -1em;
}

.article__header .social-embed {
	clear: both !important;
	text-align: center;

	.twitter-video,
	.twitter-tweet {
		max-width: $social-media-embed_twitter-width !important;
		margin: 0 auto !important;
	}
}