.article {
	.article__field__multi-quote {
		.article__header__intro,
		.teaser-article-summary {
			margin-left: 0;
			width: 100%;
			margin-bottom: $vertical-space-widgets-half;

			@include media(max-width $mobile-breakpoint-max) {
				margin-bottom: $vertical-space-widgets-half;
			}

			.article__header__intro__text,
			.teaser-article-summary-headline {
				font-size: 1.5em;
				font-style: italic;
				line-height: 1.4em;
				margin-bottom: 0;


				@include media(max-width $mobile-breakpoint-max) {
					font-size: 1.25em;
				}
			}
		}

		&.article__field__multi-quote_left,
		&.article__field__multi-quote_right {
			.article__header__intro,
			.teaser-article-summary {
				.article__header__intro__text,
				.teaser-article-summary-headline {
					font-size: 1em;
				}
			}
		}
	}

	.article__body {
		.article__field__multi-quote {
			.teaser-article-summary {
				margin-bottom: $vertical-space-widgets-half;
				padding: 2rem 0;

				.teaser-article-summary-headline {
					text-transform: none;
				}

				p {
					&:before {
						content: none;
					}
				}
			}
		}
	}
}

.article__field__multi-quote {
	display: table;
	margin-bottom: $vertical-space-widgets;

	.article__header__intro,
	.teaser-article-summary {
		border-top: 1px solid $color-emph;
		border-bottom: 1px solid $color-emph;
		color: #666;
		padding: 2rem 0;
		position: relative;
		overflow: visible;

		&:before {
			background: #fff;
			content: "\201c";
			color: $color-emph;
			font-size: 4em;
			height: 2rem;
			font-family: Georgia;
			left: 1rem;
			line-height: 1;
			margin-right: 1rem;
			padding: 0 0.25rem;
			position: absolute;
			top: -1rem;
		}
	}

	.article__header__originator {
		font-family: Arial, Helvetica, sans-serif;
		font-style: italic;
		font-weight: normal;
		color: #999;
		font-size: 12px;
	}

	&.article__field__multi-quote_left {
		float: left;
		width: 50%;
		margin-right: 1rem;
	}

	&.article__field__multi-quote_right {
		float: right;
		width: 50%;
		margin-left: 1rem;
	}
}
