.topic {
	.topic--entries {
		@include clearfix;
	}

	.topic--entries__column {
		@include span-columns(6 of 12);
		@include omega(2n);
		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
			@include omega();
			padding: 0 .1875em .3125em;
		}
	}

	.topic--filter {
		margin-bottom: 2.5em;
	}

	.topic--filter__item {
		border-bottom: .0625em solid $nearly-lightest-grey;
		padding: 0 .1875em .3125em;

		&.topic--filter__item--active {
			padding-bottom: .25em;
			padding-right: .75em;
		}
	}

	.topic--filter--mobile {
		clear: both;
		@include media(min-width $mobile-breakpoint-min) {
			display: none;
		}
	}

	.topic--filter--desktop {
		@include media(max-width $mobile-breakpoint-max) {
			display: none;
		}
	}
	.topic--filter__item {
		&.topic--filter__item--active {
			border-bottom: .125em solid $black;
		}
	}
	.topic--filter__headline,
	.topic--filter__picker {
		@include span-columns(9 of 12);

		@include media(max-width $mobile-breakpoint-max) {
			margin-left: $space-innerpage;
			margin-right: $space-innerpage;
		}
	}

	.teaser-list a {
		text-decoration: none;
	}
}