.search--fullsize {
	@include span-columns(8 of 12);
	@include shift(2);
	@include clearfix;
	@include omega;

	margin-bottom: 1.5em;
	font-family: $font-family-meta;
	margin-top: 6em;

	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(12 of 12);
		@include shift(0);
		@include pad(0 $space-innerpage);
		margin-top: 2em;
	}

	legend {
		@include clearfix;
	}

	.search__input {
		@include pad(0 .9375em);
		@include span-columns(9 of 12, block-collapse);
		appearance: none;
		border: 1px solid $light-grey;
		border-radius: 0;
		border-right: none;
		color: $black;
		height: 2.8125em;
		vertical-align: middle;

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(9 of 12, block-collapse);
			appearance: none;
			border-radius: 0;
		}

		&::-webkit-input-placeholder {
			color: $black;
			font-weight: $font-weight-bold;
		}
		&:-moz-placeholder {
			color: $black;
			font-weight: $font-weight-bold;
		}
		&::-moz-placeholder {
			color: $black;
			font-weight: $font-weight-bold;
		}
		&:-ms-input-placeholder {
			color: $black;
			font-weight: $font-weight-bold;
		}
	}

	.search__button {
		@include span-columns(3 of 12, block-collapse);
		@include omega;
		box-sizing: border-box;
		background-color: $green;
		border: 0;
		color: $white;
		height: 2.8125em;
		line-height: 2.8125em;
		padding: 0;
		position: relative;

		@include media(max-width $mobile-breakpoint-max) {
			color: transparent;
			text-indent: -99px;

			&:before {
				color: $white;
				left: 0;
				right: 0;
				text-align: center;
				text-indent: 0;
			}
		}

		&:before {
			@include icon;
			@include icon--search;
			bottom: 0;
			font-size: 1.5em;
			left: .9375em;
			line-height: 1.875em;
			margin-right: .3125em;

			@include media(max-width $mobile-breakpoint-max) {
				left: 0;
				margin: 0;
				position: absolute;
			}
		}
	}

	.search__input {
		@include omega;
		box-sizing: border-box;
	}

	.search--fullsize__hint {
		color: $lighter-grey;
		font-family: $font-default;
		font-size: $article-author-fontsize;
	}
}