// Custom Sectionklasse diese muss an der Section per Rubrikparameter gesetzt sein. Verwendet wird immer die homeSection des Artikels
// Bsps: Futurezone > games > Rebrikparameter > section.cssClass=games ergibt section-games am Element Teaser oder Artikel

$sections: (
	netzpolitik: #5da390,
	b2b: #82b478,
	produkte: #145ad2,
	digitallife: #c83c82,
	science: #d23c1e,
	meinung: #ffb432,
	games: #e75d01,
	apps: #6bc504,
	startups: #6624b3,
	community: #3aa6e3
);

@each $sectionName, $sectionColor in $sections {
	.section-#{$sectionName} {
		.emph,
		a.emph,
		.header--category__title,
		&.teaser--large a.emph,
		&.teaser-stream-video a.emph,
		&.teaser-stream-gallery a.emph,
		.headline a.emph,
		.headline.on-dark a.emph {
			color: $sectionColor;

			&:hover {
				color: darken($sectionColor, 20);
				background: transparent;
			}
		}

		&.article {
			.hero-img__caption,
			.hero-media__caption,
			.inline-media__caption,
			.teaser-article-summary p:not(.teaser-article-summary-headline) {
				&:before {
					color: $sectionColor;
				}
			}

			.article__header__tag {
				background: $sectionColor;
			}

			.inline-media__caption,
			.hero-img__caption {
				border-color: $sectionColor;
			}

			.swiper-container .hero-media__caption {
				border-color: $sectionColor;
			}
		}

		&.teaser-stream {
			.teaser-stream-content {
				.socialbar {
					&.teaser__icons {
						.share-button__icon {
							background: $sectionColor;
						}
					}
				}
			}
		}
	}
}

// Navigation

.nav-bar .nav-main {
	ul:not(.nav-main--mobile) {
		li {
			a {
				transition: all .5s;
			}
		}

		&:hover {
			li {
				a {
					border-color: transparent;
				}
			}

			@each $sectionName, $sectionColor in $sections {
				.nav-main__section-#{$sectionName} {
					&:hover {
						a {
							border-color: $sectionColor;
						}
					}
				}
			}
		}
	}

	@each $sectionName, $sectionColor in $sections {
		.nav-main__section-#{$sectionName} {
			a {
				border-color: $sectionColor;
			}
		}
	}

	// Section active

	.nav-main--section-active:not(.nav-main--mobile) {
		li a {
			border-color: transparent;
		}

		@each $sectionName, $sectionColor in $sections {
			.nav-main__section-#{$sectionName} {
				a {
					&:hover {
						border-color: $sectionColor;
					}
				}

				&.nav-main__item--active {
					a {
						border-color: $sectionColor;

						&.nav-main__anchor--active {
							color: $sectionColor;
						}
					}
				}
			}
		}
	}
}
