.inline-carousel {
	&.carousel-ajax {
		@media (max-width: $mobile-breakpoint-max) {
			width: 100%;
		}

		.inline-media__wrapper {
			background: transparent;
		}

		.teaser-carousel.swiper-container {
			margin-top: $vertical-space-widgets;
			padding-bottom: $vertical-space-widgets;

			@include media(max-width $mobile-breakpoint-max) {
				margin-left: $vertical-space-widgets-half;
				margin-right: $vertical-space-widgets-half;
			}
		}
	}

	.inline-media {
		.inline-media__caption {
			display: block;
		}
	}

	// Ein Inline .inline-carousel Element verwendet die .hero-media__caption hier msste man die Klasse ind er JSP mal umbennen.
	.hero-media__caption {
		display: block;

		.hero-media__caption-credits {
			margin-bottom: 0;
		}
	}
}

.article {
	.inline-block--wide {
		.swiper-container {
			.hero-img__text + .hero-img__credits,
			.inline-media__caption-text + .inline-media__caption-credits {
				margin-top: 0;
			}
		}
	}

	.inline-block--right-25,
	.inline-block--left-25 {
		.inline-carousel {
			.swiper-pagecount {
				font-size: 12px;
			}
		}

		.swiper-container {
			.swiper-buttons {
				padding: 0 0.15em;
			}
		}
	}

	.inline-block--left,
	.inline-block--right {
		.carousel-ajax {
			.swiper-buttons {
				font-size: 34px;
			}
		}
	}

	.inline-block--left-25,
	.inline-block--right-25 {
		.carousel-ajax {
			.swiper-buttons {
				font-size: 28px;
			}
		}
	}
}
