// Longform Article Compare-Slider

.theme-longform {
	.article--type-longform {
		.compare-slider {
			margin-bottom: 0;

			.compare-slider--wrapper {
				margin-bottom: $longform-caption-space;
			}

			.inline-media__caption,
			.compare-slider--intro {
				background: transparent;

			}

			.compare-slider--intro {
				color: #333;
				font-family: $longform-font;
				font-weight: normal;
				font-style: italic;
				font-size: 26px;
				line-height: 1.4em;
				padding-left: 0;
				padding-top: 0;

				@media screen and (max-width: $longform-breakpoint-mobile) {
					font-size: 18px;
				}
			}

			.inline-media__caption {
				padding-top: 0;
				padding-left: 0;
				padding-bottom: 0;
			}

			.copyright {
				color: #999;
				text-align: left;
			}

			.txt + .copyright {
				margin-top: 1em;
			}

			.txt {
				line-height: 1.5em;
			}
		}

		.inline-block--full {
			.compare-slider {
				.compare-slider--intro {
					padding-left: 1em;

					@media screen and (max-width: $longform-breakpoint-mobile) {
						padding-left: $element-space-smaler;
					}
				}
			}
		}

		.inline-block--wide {
			.compare-slider {
				margin: auto;
				width: $longform-split-column-smale;

				@media screen and (max-width: $longform-breakpoint-tablet) {
					width: 80%;
				}

				@media screen and (max-width: $longform-breakpoint-mobile) {
					width: 100%;
				}
			}
		}

		&.article {
			.inline-block--left,
			.inline-block--right {
				.compare-slider {
					.compare-slider--wrapper {
						margin-bottom: 0;

						@media screen and (max-width: $longform-breakpoint-mobile) {
							margin-bottom: $longform-caption-space;
						}
					}

					.compare-slider--intro {
						padding-top: 0;
					}

					.inline-media__caption {
						float: left;
						position: relative !important;
						margin-left: 1em;

						@media screen and (max-width: $longform-breakpoint-mobile) {
							margin-left: 0.5em;
						}
					}
				}
			}

			.inline-block--left {
				.compare-slider--wrapper {
					float: left;
					width: $longform-split-column-large;

					@media screen and (max-width: $longform-breakpoint-mobile) {
						width: 100%;
					}
				}
			}

			.inline-block--right {
				.compare-slider--wrapper {
					float: right;
					width: $longform-split-column-large;

					@media screen and (max-width: $longform-breakpoint-mobile) {
						margin-left: 0;
						width: 100%;
					}
				}

				.compare-slider {
					.inline-media__caption {
						margin-left: 0.5em;
					}
				}
			}

			.inline-block--left-25,
			.inline-block--right-25 {
				.compare-slider {
					.compare-slider--intro {
						font-size: 16px;
						padding-top: 0;
					}
				}
			}
		}
	}
}