.trending-themen {
	background-color: $white;
	display: inline-block;
	margin-bottom: $vertical-space-widgets;
	padding: $vertical-space-widgets;

	@include media(max-width $mobile-breakpoint-max) {
		padding: 24px $vertical-space-widgets-half 0;
	}

	.content-triple {
		margin: $vertical-space-widgets 0;
	}

	.block-header {
		color: #000;

		@include media(max-width $mobile-breakpoint-max) {
			padding: $vertical-space-widgets-half 0;
		}
	}

	.teaser .teaser__headline {
		color: $color-emph;
		margin-bottom: 0;
	}

	.teaser-list .teaser {
		padding: $vertical-space-widgets-half 0;
	}

	.header--category__title {
		color: $black;
		font-size: 3em;
		font-weight: normal;
		margin-bottom: .25em;

		@include media(max-width $mobile-breakpoint-max) {
			font-size: 2em;
		}

		@include media(max-width $mobile-breakpoint-mini) {
			font-size: 1.375em;
		}
	}

	.header--category__intro {
		font-size: 1.125em;
		line-height: 1.625em;
		font-weight: normal;
		color: $black;

		@include media(max-width $mobile-breakpoint-max) {
			font-size: 1em;
			line-height: 1.5em;
		}
	}

	.teaser--small .teaser__headline {
		transition: $transition-fast;
	}

	.teaser--small .teaser__headline:hover {
		color: $black;
	}

	.topic {
		display: none;
	}

	.topic.content--teaser--container {
		display: block;
	}
}
