.teaser-stream-share-container {
	float: left;
	margin-top: -20px;
	transition: all .5s;
	width: 100%;

	@include media(max-width $mobile-breakpoint-max) {
		margin-top: 0;
	}

	&.social-open {
		margin-top: 2em;

		@include media(max-width $mobile-breakpoint-max) {
			margin-top: 0;
		}

		.socialbar {
			height: auto;
			opacity: 1;
		}
	}

	.teaser-stream-sharebutton {
		@include span-columns(1);
		cursor: pointer;
		color: #666;
		font-size: $font-size-extras-tiny;
		font-weight: $font-weight-regular;
		text-align: center;
		margin-left: -0.5em;
		margin-top: -16px;
		transition: all .3s;
		user-select: none;
		text-transform: uppercase;

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12);
			@include omega;
			margin-top: 0;
			margin-left: 0;
			margin-bottom: 0;
			text-align: center;
		}

		&:before {
			@include icon;
			@include icon--stream-share;
			display: block;
			font-size: 1.5em;
			margin-bottom: 5px;

			@include media(max-width $mobile-breakpoint-max) {
				text-align: center;
				width: 100%;
			}
		}

		&:hover {
			color: $red;
		}
	}

	.socialbar {
		@include span-columns(11);
		bottom: 8px;
		height: 0;
		left: 3.7em;
		position: absolute;
		opacity: 0;
		overflow: hidden;
		transition: all .2s ease-in-out;

		@include media(max-width $mobile-breakpoint-max) {
			bottom: 0;
			left: 0;
			margin: 0;
			position: relative;
			top: $vertical-space-widgets-half;
			width: 100%;
		}
	}
}
