.topic {
	background-color: $white;
	padding: $vertical-space-widgets;

	@media (max-width: $mobile-breakpoint-max) {
		@include span-columns(12);
	}

	.topic--filter {
		border-bottom: .125em solid $nearly-lightest-grey;
		display: inline-block;
		height: 23px;
		margin-bottom: 0;

		@include media(max-width $mobile-breakpoint-max) {
			border: none;
			max-width: 320px;
		}
	}

	.topic--filter__item__link,
	.topic__list li a {
		transition: $transition-fast;
		display: inline-block;

		&:hover {
			color: $color-text-black;
		}
	}

	.topic--filter__item__link {
		padding: 0 .125em;
	}

	.topic--filter__item {
		display: inline-block;
		font-size: $font-size-extras-medium;

		.topic--filter__item__link:hover {
			color: $color-text-black;
		}

		&.topic--filter__item--active .topic--filter__item__link {
			color: $color-text-black;
		}
	}

	.topic--filter__item,
	.topic--filter__item.topic--filter__item--active {
		padding: 0;
		border: none;
	}

	.topic--filter__item:hover {
		border-bottom: .125em solid $black;
	}

	.topic--filter__headline {
		@include media(max-width $mobile-breakpoint-max) {
			margin-bottom: $vertical-space-widgets-half;
		}
	}

	.topic--filter__headline,
	.topic--filter__picker {
		@include span-columns(12 of 12);
	}

	.topic--entries ul {
		clear: both;

		@include media(max-width $mobile-breakpoint-min) {
			margin: 0;
			padding: 0;
		}
	}

	.teaser-list li {
		font-size: .875em;
		line-height: 1.5em;
	}

	.teaser-list li:before {
		@include icon;
		@include icon--arrow-right-circle-invert;
		transition: $transition-fast;
		color: $color-text-black;
		font-size: .75em;
		line-height: 1.75em;
		margin-right: $vertical-space-widgets-half;
	}

	.teaser-list li:hover:before {
		margin-left: .25em;
	}

	.teaser-list li + li .teaser {
		border: none;
	}

	.teaser-list .teaser {
		padding: 0;
	}

	.teaser .teaser__headline {
		color: $color-emph;

		&:hover {
			color: $black;
		}
	}
}
