.teaser-leader {
	&.themen-teaser-opener {
		font-size: 36px;
		height: 360px;
		margin: 0 0 .45em;
		@include media(max-width $mobile-breakpoint-max) {
			height: 240px;
		}

		.teaser-img__credits,
		a:not(.teaser__link) {
			display: none;
		}

		.headline-l {
			font-size: 1em;
			line-height: 1;
			margin: 0.5em;

			.teaser__link {
				text-transform: uppercase;
				font-weight: 500;
			}
			@include media(max-width $mobile-breakpoint-max) {
				font-size: 24px;
				margin: .7em .4em;
			}
		}
	}
}
