.container-leader-more {

	.container-leader-more-inner {
		background: #fff;
	}
	.container-leader-more-headline {
		color: #242424;
		margin-top: 0;
	}

	.teaser-leader .headline.headline-s,
	.teaser-leader .headline.headline-s .teaser__link {
		color: #242424;
		font-size: 16px;
		font-weight: 500;
		display: block;
	}

	.teaser-leader .headline_section {
		font-size: 11px;
		display: block;
		text-transform: uppercase;
		font-weight: 500;
		margin-bottom: 4px;
	}

	.teaser-leader .headline_section:hover {
		background: transparent;
		color: $color-text-default;
	}

	.teaser-leader a.emph {
		text-transform: uppercase;
		font-weight: normal;
		font-size: 14px;
		&:first-of-type {
			font-size: 11px;
		}
	}
}

.content-wide .container-leader-more .container-leader-more-headline {
	margin-top: 16px;
}