//Use with swiper plugin only

.carousel-ajax {
	transition: $transition-fast;

	.carousel-ajax-wrapper {
		margin-bottom: 1em;
		display: flex;
	}

	&.swiper-container {
		margin-bottom: 1em;
	}

	.swiper-pagecount {
		border-top: 1px solid $color-swiper-pagecount-border;
		padding-top: 1em;
	}

	.swiper-button-prev,
	.swiper-button-next {
		text-shadow: 0 1px #000;
	}

	figure.teaser-media {
		display: inline-block;
		width: 100%;
		margin: 0;
	}
}

.content--teaser--container {
	.carousel-ajax {
		li:last-child {
			border: none;

			@include media(max-width $mobile-breakpoint-max) {
				border: none;
			}
		}
	}
}
