// Longform Article Footer

.theme-longform {
	.footer {
		background: #333;
		font-family: $longform-font;
		max-width: 100%;
		padding: 0;

		.footer__column-wide {

			.small-print {
				color: #fff;

				.small-print__list__item {
					color: #fff;
					font-size: 16px;

					@include media(max-width $mobile-breakpoint-max) {
						padding-bottom: .5em;
						display: block;
					}

					&:after {
						color: #fff;
						@include media(max-width $mobile-breakpoint-max) {
							content: "";
						}
					}

					.small-print__list__item__link {
						color: #fff;

						@include media(max-width $mobile-breakpoint-max) {
							width: 100%;
							display: inline-block;
							font-size: 14px;
						}
					}
				}

				.small-print__summary:first-child:after {
					content: "";
				}
			}
		}
	}
}