.remodal {
	background-color: $white;
}

#newsletter-popup {
	overflow: hidden;
	@include media(max-width $mobile-breakpoint-max) {
		overflow-y: scroll;
		height: 600px;
	}
}

#newsletter-popup fieldset {
	border: none;
}

#newsletter-popup .form__input--no_thank_you_link:hover,
#newsletter-popup .form__input--no_thank_you_link:active,
#newsletter-popup .form__input--no_thank_you_link.remodal-cancel {
	background-color: $white;
	color: $black;
	font-size: 0.8em;
	width: 70%;
	border: none;
	font-weight: normal;
}

#newsletter-popup .remodal-cancel {
	text-align: left;
	padding: 0;
}

#newsletter-popup .form__input--submit {
	height: 2.1875em;
	vertical-align: middle;
	padding: 0.1875em;
	padding-left: 10px;
	padding-right: 10px;
	background: $white;
	border: 2px solid $grey;
	color: $black;
}

#newsletter-popup .form__input--submit:hover,
#newsletter-popup .form__input--submit:active {
	background: $dark-grey;
	color: $white;

	@include media(max-width $mobile-breakpoint-max) {
		width: 100%;
	}
}

.lightbox--newsletter_orderbox {
	background: $white;
	padding: $vertical-space-section;
	text-align: left;
	width: 37.5em;

	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(12 of 12);
		height: auto;
		padding: $space-innerpage;

		.teaser {
			display: none;
		}
	}

	.teaser {
		margin-bottom: .5em;
		margin-left: 0;
		margin-right: 0;
	}
}

#newsletter-popup .hero-media__wrapper img {
	width: 100%;
	height: auto;
}

#newsletter-popup #msg1 {
	padding-top: 10px;
}

#newsletter-popup #msg2 {
	color: #008000;
	display: none;
	top: 15px;
	position: relative;
	width: 70%;
}

.lightbox--newsletter_orderbox__form__input,
.lightbox--newsletter_orderbox__form__label {
	@include span-columns(6 of 12);
}

.lightbox--newsletter_orderbox__form__input {
	@include omega;
	@include clearfix;
}

.captcha .lightbox--newsletter_orderbox__form__label {
	@include span-columns(12 of 12);
	@include omega;
	@include clearfix;
}

.lightbox--newsletter_orderbox__dachzeile {
	margin-top: .625em;
	background-color: $grey;
	color: $white;
	font-size: 1.75em;
	font-weight: normal;
	text-align: center;
	text-transform: uppercase;

	@include media(max-width $mobile-breakpoint-max) {
		font-size: .875em;
	}
}

.lightbox--newsletter_orderbox__title {
	font-size: 1.5em;
	margin-top: 1.25em;
	margin-bottom: .625em;
	font-weight: normal;
}

.lightbox--newsletter_orderbox__teaser {
	margin-bottom: 1.25em;
	font-weight: normal;
}

.lightbox--newsletter_orderbox__form__label,
.lightbox--newsletter_orderbox__form__captcha {
	@include span-columns(4 of 12);
}

.lightbox--newsletter_orderbox__form__input {
	@include span-columns(8 of 12);
	@include clearfix;
	@include omega;
	border: .0625em solid $grey;
	height: 2.8125em;
}

.lightbox--newsletter_orderbox__form__text {
	@include span-columns(8 of 12);
	@include clearfix;
	@include omega;
	height: 1.25em;
	margin-bottom: $vertical-space-widgets;
	@include media(max-width $mobile-breakpoint-max) {
		font-size: .75em;
	}
}

.lightbox--newsletter_orderbox__form__captcha {
	@include span-columns(8 of 12);
	@include clearfix;
	@include omega;
	height: 2.8125em;
}

.form__input--no_thank_you_link {
	float: left;
	margin-top: 1.25em;
}

.form__input--submit {
	float: right;
	margin-top: .5em;
}

.lightbox--newsletter_orderbox__form__input,
.lightbox--newsletter_orderbox__form__label,
.lightbox--newsletter_orderbox__hint,
.lightbox--newsletter_orderbox__form__submit {
	appearance: none;
	background: none;
	font-family: $font-family-meta;
	margin-bottom: $vertical-space-widgets;
	border-radius: 0;
	line-height: 2.1875em;
	padding-top: 5px;
}

.lightbox--newsletter_orderbox__error {
	color: $light-red;
}

.lightbox--newsletter_orderbox__form__label,
.lightbox--newsletter_orderbox__form__input {
	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(12 of 12);
		@include clearfix;
		@include omega;
		margin-bottom: 0;
	}
}

.captcha .lightbox--newsletter_orderbox__form__label {
	text-align: center;
}

.lightbox--newsletter_orderbox__form__input--textarea {
	border: .0625em solid $grey;
	height: 10.9375em;
	resize: none;
}

.captcha {
	margin-bottom: $vertical-space-widgets;

	@include media(max-width $mobile-breakpoint-max) {
		.lightbox--newsletter_orderbox__form__captcha,
		.lightbox--newsletter_orderbox__form__input {
			@include span-columns(6 of 12);
		}
	}
}

.lightbox--newsletter_orderbox__form__submit {
	@include span-columns(4 of 12);
	@include shift(8);
	@include omega;
	@include clearfix;
	border-radius: .3125em;
	background-color: $white;
	border-color: $dark-grey;
	border-style: solid;
	border-width: 2px;
	padding: .3125em 0;

	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(6 of 12);
		@include shift(3);
		margin-bottom: 1.25em;
		margin-top: 1.25em;
		text-align: center;
	}
}
