.kachel-teaser.teaser-stream.prio-1,
.kachel-teaser.teaser-stream.prio-2,
.kachel-teaser.teaser-stream.prio-3,
.kachel-teaser.teaser-stream.prio-4,
.kachel-teaser.teaser-stream.prio-5,
.kachel-teaser.teaser-stream.prio-6 {
	line-height: inherit;

	.teaser-media {
		width: 100%;
		height: 100%;
		position: relative;
		float: none;
		display: inline-block;

		img {
			height: auto;
			width: 100%;
		}
	}

	.teaser__link {
		display: inline-block;
	}

	.headline {
		font-weight: 500;
		width: 100%;
		display: inline-block;
	}

	.hashtag-bar {
		float: none;
	}
	.teaser__link-image {
		@include media(max-width $mobile-breakpoint-max) {
			float: none;
		}
	}
}
