// Longform Article Video
.theme-longform {
	.article--type-longform {
		.article__body {
			figure.video-inline {
				width: 33%;
				display: block;
				margin: auto;
				background: transparent;
				h1 {
					display: none;
				}
				.hero-img__caption--video {
					display: block;
					float: none;
					width: calc(100% - 16px);
					line-height: 1.5;
					.hero-img__text {
						font-weight: normal;
						width: 100%;
					}
				}
				&.inline-block--left {
					.player_container {
						padding-right: 33%;
						@media screen and (max-width: $longform-breakpoint-mobile) {
							padding-right: 0;
						}
					}
					.hero-img__caption--video {
						right: $element-outer-space;
					}
				}
				&.inline-block--right {
					.player_container {
						padding-left: 33%;
						@media screen and (max-width: $longform-breakpoint-mobile) {
							padding-left: 0;
						}
					}
					.hero-img__caption--video {
						left: $element-outer-space;
					}
				}
				&.inline-block--left,
				&.inline-block--right {
					.hero-img__caption--video {
						top: $element-outer-space;
						width: calc(33% - 48px);
						@media screen and (max-width: $longform-breakpoint-mobile) {
							width: 100%;
							position: static;
							.hero-img__text {
								float: none;
							}
						}
					}
					.player_container {
						margin-bottom: .625em;
					}
				}
				&.inline-block--wide {
					margin: 24px auto;
					width: 100%;
					padding: 0;

					.hero-img__caption {
						width: 33%;
						margin: auto;
						display: block;
						.hero-img__text {
							float: none;
						}
						@media screen and (max-width: $longform-breakpoint-tablet) {
							width: 80%;
						}
						@media screen and (max-width: $longform-breakpoint-mobile) {
							width: calc(100% - 32px);
						}
					}
					.player_container {
						margin-bottom: 0.625em;
					}
				}
				.hero-img__text {
					margin-top: 0;
					color: #000;
				}

				.inline-media__caption {
					.inline-media__caption-text {
						@media screen and (max-width: $longform-breakpoint-mobile) {
							font-size: 14px;
						}
					}
				}
			}
		}
	}
}