$social-color-facebook: #3b5998;
$social-color-twitter: #1da1f2;
$social-color-instagram: #939599;

.social-channels {
	@include span-columns(3);

	@include media(max-width $mobile-breakpoint-max) {
		display: none;
	}

	display: inline-block;
	float: left;
	padding-left: $vertical-space-widgets;

	.social-channels__icon {
		float: left;
		font-size: 1.75rem;
		margin-right: 0.5rem;
		margin-top: 0.45rem;

		a {
			&:before {
				@include icon;
			}
		}
	}

	.social-channels__facebook {

		a {
			color: $social-color-facebook;

			&:before {
				@include icon--nav-facebook;
			}
		}
	}

	.social-channels__twitter {
		a {
			color: $social-color-twitter;

			&:before {
				@include icon--nav-twitter;
			}
		}
	}

	.social-channels__instagram {

		a {
			color: $social-color-instagram;

			&:before {
				@include icon--nav-instagram;
			}
		}
	}
}

