.remodal {
	background-color: $white;
}

.lightbox--recommend-article {
	background: $white;
	padding: 2.5em;
	text-align: left;
	width: 37.5em;

	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(12 of 12);
		height: auto;
		padding: 3em 1em 1em;
	}

	.teaser {
		margin-bottom: .5em;
		margin-left: 0;
		margin-right: 0;

		@include media(max-width $mobile-breakpoint-max) {
			display: none;
		}
	}
}

.lightbox--recommend-article__form__input,
.lightbox--recommend-article__form__label {
	@include span-columns(6 of 12);
}

.lightbox--recommend-article__form__input {
	@include omega;
	@include clearfix;
}

.captcha .lightbox--recommend-article__form__label {
	@include span-columns(12 of 12);
	@include omega;
	@include clearfix;
}

.lightbox--recommend-article__title {
	font-size: 1.5em;
	margin-bottom: 1em;

	@include media(max-width $mobile-breakpoint-max) {
		margin-bottom: .3125em;
	}
}

.lightbox--recommend-article__form__label,
.lightbox--recommend-article__form__captcha {
	@include span-columns(4 of 12);
}

.lightbox--recommend-article__form__input {
	@include span-columns(8 of 12);
	@include clearfix;
	@include omega;
	border: .0625em solid $grey;
	height: 2.8125em;
}

.lightbox--recommend-article__form__label {
	line-height: 2.5em;
}

.lightbox--recommend-article__form__input,
.lightbox--recommend-article__form__label,
.lightbox--recommend-article__hint,
.lightbox--recommend-article__form__submit {
	appearance: none;
	background: none;
	border-radius: 0;
	font-family: $font-family-meta;
	margin-bottom: $vertical-space-widgets;
}

.lightbox--recommend-article__error {
	color: $light-red;
}

.lightbox--recommend-article__form__label,
.lightbox--recommend-article__form__input {
	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(12 of 12);
		@include clearfix;
		@include omega;
		margin-bottom: 0;
	}
}

.captcha .lightbox--recommend-article__form__label {
	text-align: center;
}

.lightbox--recommend-article__form__input--textarea {
	border: .0625em solid $grey;
	height: 10.9375em;
	resize: none;
}

.captcha {
	margin-bottom: $vertical-space-widgets;

	@include media(max-width $mobile-breakpoint-max) {
		.lightbox--recommend-article__form__captcha,
		.lightbox--recommend-article__form__input {
			@include span-columns(6 of 12);
		}
	}
}

.lightbox--recommend-article__form__submit {
	@include span-columns(4 of 12);
	@include shift(8);
	@include omega;
	@include clearfix;
	border-radius: .3125em;
	background-color: $white;
	border-color: $dark-grey;
	border-style: solid;
	border-width: 2px;
	padding: .3125em 0;

	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(12 of 12);
		@include shift(0);
		margin-bottom: 1.25em;
		margin-top: 1.25em;
		text-align: center;
	}
}

.lightbox--recommend-article__form fieldset > div {
	clear: both;
	float: right;
	margin-bottom: 20px;

	@media screen and (max-width: $mobile-breakpoint-max) {
		display: inline-block;
		float: none;
		margin-bottom: 0;
		margin-top: 20px;
	}
}
