.search--results {
	@include span-columns(12 of 12);
	background: $white;
	padding-top: 1em;

	@include media(max-width $mobile-breakpoint-max) {
		margin-top: 0;
	}

	.search--results__results {
		@include span-columns(8 of 12);

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
			@include omega;
			@include clearfix;
		}

		.search--fullsize,
		.search--result__list__header,
		.search--result__list__entries {
			@include media(max-width $mobile-breakpoint-max) {
				@include span-columns(12 of 12);
			}
		}

		.teaser-list {
			@include media(max-width $mobile-breakpoint-max) {
				padding: 0;
			}
		}

		.filters__list__items {
			@include media(max-width $mobile-breakpoint-max) {
				@include clearfix;
				border-radius: 5px;
				border: 1px solid $nearly-lightest-grey;
				clear: both;
			}

			li {
				@include media(max-width $mobile-breakpoint-max) {
					@include span-columns(12);
					@include omega;
					border-radius: 0;
					border-left: none;
					border-top: none;
				}
			}

			.item-tag--active {
				@include media(max-width $mobile-breakpoint-max) {
					border-bottom: 1px solid $nearly-lightest-grey;
					border-top: 1px solid $nearly-lightest-grey;

					&:first-child {
						border-top: none;
					}

					&:last-child {
						border-bottom: none;
					}
				}
			}
		}

		.teaser .teaser__headline {
			clear: both;
			font-weight: bold;
		}

		.teaser .teaser__date {
			float: right;
			text-align: right;
		}

		.teaser .teaser__body {
			@include media(max-width $mobile-breakpoint-max) {
				@include span-columns(12 of 12);
				@include omega;
				@include clearfix;
			}
		}

		.item-tag__link {
			display: block;
			height: 100%;
			padding: .5em;
			width: 100%;
		}

		.item-tag__link:hover {
			text-decoration: none;
		}

		.search--fullsize {
			@include span-columns(12);
			@include omega;
			@include shift(0);
			@include clearfix;
			margin-top: 0;
		}

		.search--fullsize .search__input {
			@include span-columns(9 of 12, block-collapse);
			appearance: none;
			border-radius: 0;
			font-weight: $font-weight-bold;
			font-size: 16px;
		}

		.search--fullsize .search__button {
			@include span-columns(3 of 12, block-collapse);
			font-weight: $font-weight-bold;
			font-size: 16px;
		}

		.search--fullsize .search__button:before {
			left: .25em;
		}

		.search--fullsize .search--fullsize__hint {
			display: none;
		}

		.search--fullsize,
		.search--result__list__header {
			font-family: $font-family-meta;
		}

		.search--result__list__header {
			@include media(max-width $mobile-breakpoint-max) {
				position: relative;
				padding: 0 .625em;
			}
		}
	}

	.search--result__list {
		clear: both;
	}

	.search--results__asides {
		@include span-columns(4 of 12);
		@include omega;

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
		}

		.search--asides {
			@include span-columns(12);
			@include omega;
			@include shift(0);
			display: flex;
		}

		.search--asides__archive,
		.search--asides__events,
		.search--asides__recents,
		.search--asides__obituaries,
		.search--asides__experts {
			@include shift(0);
			@include span-columns(12);
			@include omega;
		}

		.search--asides__recents {
			margin-bottom: 3.125em;
		}

		.search--asides__archive__icon,
		.search--asides__events__icon {
			@include span-columns(3 of 12, border-collapse);
			text-align: center;
		}

		.search--asides__archive__text,
		.search--asides__events__text {
			@include span-columns(9 of 12, border-collapse);
			@include omega;
		}
	}

	.sort__list__title,
	.search--results__options__filters__title,
	.search--results__options__sort__hitcount {
		color: $lighter-grey;
		display: block;
		float: left;
		font-size: .75em;
		padding: .75em 1em .75em 0;
	}

	.sort__list__title,
	.search--results__options__filters__title {
		font-size: .6875em;
		font-weight: $font-weight-bold;
		line-height: 2.9090909091em;
		padding: 0 1em 0 0;
	}

	.sort__list__title {
		color: $color-emph;
	}

	.search--results__options__filters__list .sort__list__title {
		padding-left: 3.6363636364em;
	}

	.search--results__options__filters__title,
	.search--results__options__filters__items {
		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12);
			@include omega;
		}
	}

	.sort__list__title,
	.search--results__options__filters__title {
		text-transform: uppercase;

		@include media(max-width $mobile-breakpoint-max) {
			@include clearfix;
		}
	}

	.search--results__options__sort__list,
	.sort__list__items li {
		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12);
			@include omega;
		}
	}

	.filters__list__items,
	.sort__list__items {
		font-size: 14px;
		list-style-type: none;
		margin: 0;
		padding: 0;
	}

	.search--results__options {
		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
			border-top: 1px solid $nearly-lightest-grey;
			margin-top: 1.25em;
		}

		.filters__list__items .item-tag__ri {
			color: $light-grey;
		}

		.filters__list__items span + span {
			margin-left: .5em;
		}

		.item-tag--active .item-tag__text {
			font-weight: $font-weight-bold;
		}

		.search--results__options__filters,
		.search--results__options__sort {
			@include span-columns(12);
			@include omega;
			clear: both;
			margin-bottom: 1.5em;

			@include media(max-width $mobile-breakpoint-max) {
				@include span-columns(6 of 12);
				border-top: none;
				clear: none;
			}
		}
	}

	.filters__list__items {
		li {
			border: 1px solid $nearly-lightest-grey;
			border-right: 0;
			display: block;
			float: left;
		}

		li:first-child {
			border-top-left-radius: 5px;
			border-bottom-left-radius: 5px;

			@include media(max-width $mobile-breakpoint-max) {
				border-top-right-radius: 5px;
				border-bottom-right-radius: 5px;
				border-top-left-radius: 0;
				border-bottom-left-radius: 0;
			}
		}

		li:last-child {
			border-top-right-radius: 5px;
			border-bottom-right-radius: 5px;
			border: 1px solid $nearly-lightest-grey;

			@include media(max-width $mobile-breakpoint-max) {
				border: none;
			}
		}

		.item-tag--active {
			background-color: $lightest-grey;
		}
	}

	.search--results__options__sort {
		border-top: 1px solid $nearly-lightest-grey;
	}

	.search--results__options__sort__hitcount {
		@include span-columns(4 of 12);

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
			@include pad(0 .8125em);
			left: 0;
			position: absolute;
			top: 0;
		}

		.count {
			font-weight: $font-weight-bold;
		}
	}

	.search--results__options__sort__list {
		@include span-columns(8 of 12);
		@include omega;
		float: right;
		text-align: right;

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(9 of 12);
			@include omega;
			float: right;
		}
	}

	.search--result__list__entries {
		clear: both;
		margin-bottom: 1em;

		.teaser.teaser--large {
			.teaser__header,
			.teaser__body {
				@include span-columns(7 of 12);
				display: inherit;
			}

			.teaser__body {
				@include media(max-width $mobile-breakpoint-max) {
					@include span-columns(12 of 12);
				}
			}

			&.teaser--noimg {
				.teaser__header,
				.teaser__body {
					@include span-columns(12 of 12);
					display: inherit;
				}
			}
		}

		.teaser-list a {
			color: inherit;
		}
	}

	.sort__list__items {
		float: right;

		li {
			display: block;
			float: left;
			padding: .5em 1em;
			position: relative;

			@include media(max-width $mobile-breakpoint-max) {
				@include span-columns(11 of 12);
				@include shift(0);
				padding: .5em 0;
				text-align: left;
			}

			&:first-child {
				padding-left: 0;
			}

			&:after {
				color: $nearly-lightest-grey;
				content: "|";
				line-height: 2em;
				position: absolute;
				right: 0;
				top: 0;

				@include media(max-width $mobile-breakpoint-max) {
					content: "";
				}
			}

			&:last-child:after {
				content: "";
			}
		}

		.item__button {
			margin-right: .5em;
		}


	}
}
