.teaser-stream-video {
	background: $color-bg-dark;

	.hero-img__text {
		color: $color-text-light;
		font-size: .75em;
		font-weight: normal;
		text-align: left;
	}

	.hero-img__text,
	.hero-img__credits {
		margin-top: $vertical-space-widgets-half;
	}

	figure {
		margin: 0;
		text-align: center;
	}

	.hero-img__caption-datum {
		font-weight: $font-weight-bold;
		margin-bottom: .3125em;
	}

	.hero-img__credits {
		@include span-columns(12);
		text-align: right;

		@media (max-width: $mobile-breakpoint-max) {
			@include span-columns(12);
			@include omega;
			text-align: left;
		}
	}

	.hero-img__text,
	.hero-img__credits {
		color: $color-text-light;
		font-family: $font-family-meta;
		font-size: .75em;
		font-weight: normal;
		padding: 0;
	}
}

