// voting global Styles
.voting {

	@include media(max-width $mobile-breakpoint-max) {
		margin-left: $space-innerpage;
		margin-right: $space-innerpage;
	}

	input {
		@include span-columns(1 of 12);
		height: 1.25em;
	}

	fieldset {
		border: none;
		margin: 1em 0;
		padding: 0;
	}

	label {
		@include span-columns(11 of 12);
		@include omega;
		@include clearfix;
		margin-right: $vertical-space-widgets;
	}

	legend {
		font-size: 1em;
		margin-bottom: $vertical-space-imageblock;
	}

	p {
		margin: 0;
	}

	.voting__row {
		@include span-columns(12 of 12);
		@include omega;
		@include clearfix;
		margin-bottom: $vertical-space-imageblock;
		text-align: left;

		&:last-child {
			margin-bottom: 0;
		}
	}

	.voting__row--submit {
		text-align: center;
	}

	.voting__row__submit {
		border: none;
		color: $white;
		font-family: $font-family-meta;
		line-height: 2;
		padding: 0 .5em;
	}
}


