.container-leader {
	@include outer-container;
	background-color: $color-bg-dark;
	clear: both;
	margin-bottom: $vertical-space-widgets;
	position: relative;

	@media (max-width: $mobile-breakpoint-max) {
		background-color: $white;
	}

	.teaser-leader {
		margin: 0;
	}

	.teaser-image-block {
		cursor: pointer;
	}
}

.container-leader-more {
	@include outer-container;
	margin-bottom: $vertical-space-widgets;

	.teaser-leader.teaser-leader-s {
		@include span-columns(1 of 3);
		height: inherit;
		margin-bottom: 0;

		@media (max-width: $mobile-breakpoint-max) {
			@include span-columns(3 of 3);
		}
	}

	.teaser-leader .headline.headline-s {
		background: none;
	}

	.container-leader-more-headline {
		color: $white;
		font-size: 1.25em;
		font-weight: normal;
		margin-bottom: $vertical-space-widgets;
		margin-top: $vertical-space-widgets;
	}

	.container-leader-more-inner {
		background: $dark-grey;
		background-size: cover;
		display: inline-block;
		padding: 0 $vertical-space-widgets;
		width: 100%;
	}
}
