.topics-container {
	@include span-columns(12);
	background-color: $color-bg-lighter;
	padding: 32px 64px;

	@media (max-width: $mobile-breakpoint-max) {
		padding-left: $vertical-space-widgets;
	}

	.topic-container {
		@include span-columns(6);
		width: 48%;

		@media (max-width: $mobile-breakpoint-max) {
			@include span-columns(12);
		}

		h2 {
			color: $color-text-headline-default;
			font-size: $font-size-headline-s;
			margin-top: 16px;
		}

		ul {
			list-style: none;
			margin: $vertical-space-widgets 0;
			padding: 0;
		}

		a {
			font-size: $font-size-headline-s;
			line-height: 36px;
		}
	}
}
