.hero-carousel {
	@include span-columns(16 of 16);
	border-bottom: none;
}

.hero-carousel .carousel__list {
	@include span-columns(16 of 16);
}

.hero-media {
	margin: 0;
}

.hero-carousel .hero-media__caption,
.hero-carousel .hero-media__media {
	@include span-columns(16 of 16);
	float: none;
}

.hero-media__media img {
	height: auto;
	width: 100%;
}

.hero-media__media.mediaportrait {
	background: $dark-grey;
}

.hero-media__media.mediaportrait img {
	display: block;
	margin: 0 auto;
	width: auto;
}

.hero-media__wrapper {
	margin-bottom: $vertical-space-imageblock;
	position: relative;

	.carousel__control {
		text-align: center;

		@include media(max-width $mobile-breakpoint-max) {
			width: 1.25em;
		}
	}
}

.hero-media__caption {
	border-bottom: none;
	padding-bottom: 0;

	.hero-media__caption-index,
	.hero-media__caption-credits,
	.hero-media__caption-datum,
	.hero-media__caption-text {
		color: $lighter-grey;
		font-size: $article-meta-fontsize;
		line-height: $default-lineheight;
	}

	.hero-media__caption-index,
	.hero-media__caption-credits {
		margin-bottom: $vertical-space-imageblock;
	}

	.hero-media__caption-index,
	.hero-media__caption-datum {
		@include span-columns(6 of 12);
		display: inline-block;
		font-weight: $font-weight-bold;
		margin-bottom: $vertical-space-imageblock;

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(5 of 12);
		}
	}

	.hero-media__caption-datum {
		margin-top: .5em;

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(8 of 12);
			margin-top: .5em;
		}
	}

	.hero-media__caption-index {
		float: left;
	}

	.hero-media__caption-credits {
		float: right;
	}

	.hero-media__caption-text {
		clear: both;
	}

	.hero-media__caption-teasertext {
		color: $lighter-grey;
		font-size: .8125em;
		font-style: italic;
		margin-top: -1em;

		a {
			color: $lighter-grey;
		}
	}
}
