.header {
	background: transparent;

	.nav-bar.main-menu-mobile {
		display: none;
		visibility: hidden;
	}

	.header-top {
		padding: $vertical-space-widgets 0;
		background: #f4f4f4;
		border-bottom: none;

		.icon {
			margin-right: 20px;
			&.toggle-search {
				transform: rotate(270deg);
				transition: .2s color;
				&.icon--close {
					font-size: 18px;
					transition: none;
					margin-right: 25px;
				}
			}
			@include media(max-width $mobile-breakpoint-max) {
				&:not(.toggle-search) {
					display: none;
				}
				&.toggle-search {
					margin-right: $vertical-space-widgets-half;
					&.icon--close {
						margin-right: 12px;
					}
				}
			}
		}

		@include media(max-width $mobile-breakpoint-max) {
			padding: $vertical-space-widgets-half;
			background: #fff;
			border: none;
		}

		.logo {
			@include span-columns(6);
			text-align: center;

			.logo-container {
				display: inline-flex;
			}

			.portalname {
				display: inline-block;
				float: none;
				margin: 0;

				@include media(max-width $mobile-breakpoint-max) {
					width: 100%;
				}

				img,
				svg {
					display: inline-block;
					height: 45px;
					width: auto;

					@include media(max-width $mobile-breakpoint-max) {
						height: 40px;
						display: block;
						width: 100%;
					}
				}
			}

			@include media(max-width $mobile-breakpoint-max) {
				display: inline-flex;

				a {
					display: inline-flex;
				}
			}
		}

		.meta-menu {
			@include span-columns(3);

			@include media(max-width $mobile-breakpoint-max) {
				margin-top: 6px;
			}
		}
	}

	.ad {
		margin-bottom: 0;
		padding: 1em;
	}

	&.header.fixed {
		position: fixed;
		width: 960px;
		z-index: 99999;
	}

	@include media(max-width $mobile-breakpoint-max) {
		position: fixed;
		z-index: 99999;
		.header-top {
			position: relative;
			&.hidden {
				top: 0;
				transform: translateY(-75px);
			}
		}
		.header-top,
		&.sticky .header-top {
			transition: .5s transform ease-in-out;
		}
	}
}