// inline-table

.metainfo {
	@include span-columns(16 of 16);
	font-family: $font-family-meta;
	font-size: $article-heroimage-credits-fontsize;
}

.article figure.inline-table {
	margin-bottom: $vertical-space-widgets;
}

.inline-table {
	@include span-columns(32 of 32);
	font-family: $font-family-meta;
	margin: 0;
	margin-bottom: 2em;
	padding-left: $vertical-space-widgets;
	padding-right: $vertical-space-widgets;

	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(12 of 12);
		padding-left: 0;
		padding-right: 0;
	}

	tr {
		&:first-child {
			td {
				font-weight: $font-weight-bold;
			}
		}
	}

	table {
		border-collapse: separate;
		width: 100%;

		td,
		th {
			@include pad(.625em);
			border-bottom: 1px solid $nearly-lightest-grey;
			border-left: .5em solid $white;
			border-right: .5em solid $white;
			text-align: center;

			@include media(max-width $mobile-breakpoint-max) {
				border-left: 0;
				border-right: 0;
			}

			&:first-child {
				border-left: 0;
				padding-left: 0;
				text-align: left;

				@include media(max-width $mobile-breakpoint-max) {
					border-right: 1em solid $white;
					width: 50%;
				}
			}
		}

		th {
			vertical-align: bottom;

			@include media(max-width $mobile-breakpoint-max) {
				width: 50%;
			}
		}

		td {
			font-size: $font-size-text-small;

			@include media(max-width $mobile-breakpoint-max) {
				width: 50%;
			}
		}

		tr {
			&:first-child > td:first-child {
				vertical-align: bottom;
			}

			&:last-child {
				border-bottom: none;
			}
		}

		img {
			height: auto;
			width: 100%;

			@include media(max-width $mobile-breakpoint-max) {
				height: auto;
				width: 100%;
			}
		}
	}

	.inline-table-hidden {
		display: none;
	}

	.inline-table-show {
		padding-left: 0;
		padding-right: 0;
	}

	.inline-table-nav {
		@include span-columns(12 of 12);
		@include omega;
		display: none;
		margin-bottom: 1em;

		@include media(max-width $mobile-breakpoint-max) {
			display: block;
		}
	}

	.inline-table-desc {
		@include span-columns(8 of 12);
		@include omega;
		font-weight: $font-weight-bold;
	}

	.inline-table-button-prev,
	.inline-table-button-next {
		@include span-columns(2 of 12);
		border-radius: .3125em;
		border: 1px solid $lighter-grey;
		color: $lighter-grey;
		cursor: pointer;
		font-size: $article-header-fontsize-mobile;
		height: $article-header-fontsize-mobile;
		text-align: center;
	}

	.inline-table-button-prev {
		&:before {
			@include icon;
			@include icon--arrow-left-fat;
		}
	}

	.inline-table-button-next {
		&:before {
			@include icon;
			@include icon--arrow-right-fat;
		}
	}
}
