.poll__kicker {
	@include clearfix;
	clear: both;
	color: $lighter-grey;
	float: left;
	font-family: $font-family-meta;
	letter-spacing: .03125em;
	margin-bottom: $vertical-space;
	text-transform: uppercase;
	width: auto;

	@include media(max-width $mobile-breakpoint-max) {
		@include span-columns(6);
	}
}

.poll__headline {
	@include clearfix;
	@include span-columns(12);
	clear: both;
	color: $black;
	font-size: $article-header-fontsize;
	font-weight: $font-weight-regular;
	line-height: $default-lineheight;
	margin-bottom: $vertical-space;
	margin-top: 0;

	@include media(max-width $mobile-breakpoint-max) {
		font-size: $article-header-fontsize-mobile;
	}
}

.poll__text {
	clear: both;
	font-size: $article-intro-fontsize;
	font-weight: $font-weight-light;
	line-height: $default-lineheight;
	margin-bottom: $vertical-space-widgets;
}

.article .article__body .poll__text {
	p {
		margin-bottom: 0;
	}
}

.poll__form,
.poll__result {
	clear: both;
	margin: 0;
	margin-bottom: 0;
	overflow: hidden;

	@include media(max-width $mobile-breakpoint-max) {
		padding-left: $space-innerpage;
		padding-right: $space-innerpage;
	}
}

.poll__row {
	@include span-columns(12 of 12);
	@include clearfix;
	padding: .9375em 0;
	position: relative;
}

.poll__answer {
	border-bottom: 1px solid $nearly-lightest-grey;
}

fieldset .poll__answer:last-child {
	border-bottom: none;
}

.poll__answer__input {
	float: left;
	margin: 0 0.75em 0 0.25em;
	margin-top: 0.15em;
}

.poll__answer__label {
	@include span-columns(20 of 24);
	font-family: $font-family-meta;
	line-height: $bigger-lineheight;
}

.poll__submit {
	padding: 1.875em 0 0;

	.poll__answer__input {
		@include span-columns(4 of 12);
		@include shift(4);
		border-radius: #{.3125em};
		background-color: $white;
		border: .07142857143em solid $black;
		clear: both;
		float: none;
		font-family: $font-family-meta;
		font-size: .875em;
		font-weight: $font-weight-bold;
		line-height: 1.5714285714em;

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(6 of 12);
			@include shift(3);
		}
	}

	&:after {
		width: 100%;
	}
}

.poll__result {
	.poll__answer__label {
		@include span-columns(7 of 8);
	}

	.poll__row {
		border: .0625em solid $nearly-lightest-grey;
		display: inline-block;
		margin-bottom: 1.25em;
		overflow: visible;
		padding-left: 1.25em;
		padding-right: 1.25em;

		&:after {
			border-bottom: none;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}

	.poll__answer__share {
		@include span-columns(1 of 8);
		@include clearfix;
		@include omega;
		font-family: $font-default;
		font-size: 1.5em;
		line-height: $bigger-lineheight;
		text-align: right;
	}

	.poll__answer__share-bar {
		bottom: -1px;
		clear: both;
		height: 5px;
		left: -1px;
		position: absolute;
	}
}

.sidebar .poll__result {
	.poll__answer {
		margin-bottom: .75em;
		padding: .5em;
	}

	.poll__answer__share {
		@include span-columns(5 of 20);
		@include clearfix;
		@include omega;
	}

	.poll__answer__label--text {
		@include span-columns(15 of 20);
		font-family: $font-default;
	}
}

.poll--image {
	display: inline-block;

	.poll__row {
		clear: none;
	}

	.poll__row {
		@include span-columns(6 of 12);
		@include omega(2n);

		&:after {
			width: 100%;
		}
	}

	.poll__submit {
		@include span-columns(12 of 12);
		@include omega;
		@include clearfix;
		border-bottom-width: 0;

		@include media(max-width $mobile-breakpoint-max) {
			@include span-columns(12 of 12);
		}

		.poll__answer__input {
			@include span-columns(6 of 12);
			@include omega;
			height: auto;
		}
	}

	.poll__answer__input {
		@include span-columns(1 of 12);
		height: 1.25em;
	}

	.poll__answer__label {
		@include span-columns(11 of 12);
		@include omega;
		@include clearfix;
	}

	.poll__answer__label--image {
		@include span-columns(12 of 12);
		@include clearfix;
		@include omega;
	}

	.media {
		display: inline-block;
		float: none;

		img {
			float: none;
		}
	}

	.poll__answer.selected {
		.poll__answer__label {
			font-weight: $font-weight-bold;
		}

		.poll__answer__label--image .media {
			position: relative;
		}

		.poll__answer__label--image .media:after {
			bottom: 0;
			content: "";
			left: 0;
			position: absolute;
			right: 0;
			top: 0;
		}
	}

	.poll__result .poll__answer {
		padding: 0 0 3.75em;
		position: relative;
	}

	.poll__result .poll__answer__label--text {
		@include span-columns(12 of 12);
		@include clearfix;
		@include omega;
		padding: 0 $vertical-space-imageblock;
	}

	.poll__result .poll__answer__share {
		bottom: .3125em;
		left: .3125em;
		position: absolute;
	}
}
