// author-info-image

.author-info_wrapper-image {
	grid-template-columns: [author-left] auto [author-right] 1fr;
	grid-template-rows: max-content max-content max-content;
	margin-bottom: 1rem;
	margin-left: 6.35987%;
	width: 100%;
	float: left;

	@media (max-width: $mobile-breakpoint-max) {
		margin-left: 0;
	}
}

.author-info__image {
	margin-right: 1rem;
	float: left;

	img {
		border-radius: 50%;
		height: $author-info_image-size;
		width: $author-info_image-size;
	}
}

.sidebar {
	.sidebar__column {
		.author-info_wrapper-image {
			margin-bottom: 1rem;
		}
	}
}