/* class for microdata content you do not want to display */
.microdata {
	display: none;
}

.video__content {
	iframe {
		display: table;
		margin-bottom: 0;
	}
}

.video-youtube {
	.video__content {
		width: 100%;
		padding-bottom: 56.25%;
		position: relative;
	}

	&.video {
		margin-bottom: 10px;
	}
}

.player_brightcove {
	overflow: hidden;
}

.video-inline {
	&.inline-block--left {
		padding-right: 1em;
		padding-top: 0.25em;

		@media (max-width: $mobile-breakpoint-max) {
			padding-top: 0;
			padding-left: 0;
			padding-right: 0;
		}
	}

	&.inline-block--right {
		padding-left: 1em;
		padding-top: 0.25em;

		@media (max-width: $mobile-breakpoint-max) {
			padding-top: 0;
			padding-left: 0;
			padding-right: 0;
		}
	}

	.inline-media {
		width: 100%;
	}

	.player_container {
		width: 100%;
	}

	.hero-img__caption--video {
		width: 100%;
	}

	.hero-img__author {
		&:before {
			content: "Video:";
			margin-right: 0.25rem;
		}
	}
}

.video-autoplay {
	display: inline-block;
	font-size: 1em;
	font-family: Arial, Helvetica, sans-serif;
	float: right;
	padding: 0.125em;
	background: #eee;
	border-radius: 1em;
	position: relative;
	text-transform: uppercase;
	margin-right: 0.05em;
	margin-left: 1em;
	width: 8.5em;

	/* @include media (max-width $mobile-breakpoint-max) {
		display: none;
	} */

	& ~ .hero-media__caption-credits {
		margin-top: 0.25em;
	}

	&:before {
		content: "Autoplay";
		font-size: 0.75em;
		padding: 0.3em 0.65em;
		position: absolute;
	}

	input {
		display: none;

		& ~ .video-autoplay-button {
			background: #ccc;
			border-radius: 1em;
			display: inline-flex;
			float: right;
			height: 1.25em;
			padding: 0.125em;
			position: relative;
			width: 3.5em;

			&:before {
				transition: all 0.25s;
				content: " ";
				background: #fff;
				display: inline-block;
				border-radius: 1em;
				padding: 0.5em;
				position: absolute;
				left: 0.125em;
				z-index: 1;
				box-shadow: 0 0 0.1em #333;
			}

			&:after {
				transition: all 0.25s;
				content: "an";
				font-size: 0.75em;
				font-weight: bold;
				padding: 0.125em;
				position: absolute;
				left: 1.85em;
			}
		}

		// display: none;
		&:checked {
			& ~ .video-autoplay-button {
				&:before {
					left: 2.375em;
				}

				&:after {
					content: "aus";
					left: 0.6em;
				}
			}
		}
	}
}

.video-opener {
	figcaption {
		position: relative;
	}
}

.vjs-playlist--checkbox {
	display: none;
	position: absolute;
	z-index: 2;
}

.vjs-playlist--wrapper {
	background: #030201;

	.vjs-playlist--inner {
		position: absolute;
		top: 0;
		right: 0;
		display: inline-block;
		overflow: hidden;
		height: 100%;
	}

	.vjs-playlist--inner {
		transition: all 0.5s;
	}

	.vjs-playlist--inner-wrapper {
		height: 100%;
	}

	.vjs-playlist--button {
		background: #030201;
		position: relative;
		top: calc(50% - 30px);
		float: left;
		padding: 30px 0.5em;
		border-radius: 20px 0 0 20px;
		opacity: 1;
		transition: opacity 0.5s;

		&:before {
			@include icon;
			@include icon--arrow-right-fat;
			color: $color-emph;
			font-size: 1.5em;
			font-weight: bold;
			padding-left: 0.2em;

			@include media (max-width $mobile-breakpoint-max) {
				font-size: 1em;
			}
		}
	}
}

.vjs-playlist {
	background: #030201 !important;
	height: 100%;
	padding: 0.5em !important;
	width: 250px;
	-webkit-overflow-scrolling: touch;

	@include media (max-width $mobile-breakpoint-max) {
		width: 170px;
	}

	.vjs-playlist-title-container {
		text-align: left;
		text-shadow: none !important;
		top: inherit !important;
		bottom: 2em !important;

		@include media (max-width $mobile-breakpoint-max) {
			bottom: 1.5em !important;
		}
	}

	ol.vjs-playlist-item-list {
		padding: 0 !important;
		margin: 0 !important;

		.vjs-playlist-item {
			margin-bottom: 2px !important;
			text-align: center;
			line-height: 1.4em;

			opacity: 1;
			transition: opacity 0.2s;

			&:hover {
				opacity: 0.8;
			}

			&:after {
				@include icon;
				@include icon--video;
				color: $color-emph;
				background: #fff;
				border-radius: 50%;
				bottom: 0;
				font-size: 16px;
				text-align: center;
				height: 1.75em;
				line-height: 1.75em;
				margin-top: -1.75em;
				padding-left: 0.2em;
				position: relative;
				width: 1.75em;
				z-index: 1;

				@include media (max-width $mobile-breakpoint-max) {
					font-size: 12px;
					height: 1.5em;
					width: 1.5em;
					line-height: 1.5em;
					margin-top: -1.5em;
				}
			}

			picture {
				&:after {
					bottom: 0;
					left: 0;
					content: "";
					position: absolute;
					height: 100%;
					width: 100%;
					background: linear-gradient(to top, rgba(0, 0, 0, 0.9) 17%, transparent 90%);
				}
			}

			.vjs-playlist-name,
			.vjs-playlist-duration,
			.vjs-playlist-now-playing-text,
			.vjs-up-next-text {
				color: #fff;
				font-size: 15px;

				@include media (max-width $mobile-breakpoint-max) {
					font-size: 12px;
				}
			}
			.vjs-up-next-text {
				position: relative;
				left: 60px;
			}

			.vjs-playlist-now-playing-text,
			.vjs-up-next-text,
			.vjs-playlist-duration {
				display: none !important;
			}
		}

		.vjs-selected,
		.vjs-playlist.vjs-mouse .vjs-selected {
			background: $color-emph;
		}

		.vjs-playlist-name {
			display: inline-table;
			line-height: inherit;
			z-index: 2;
			position: relative;
		}
	}
}

.vjs-playlist--checkbox:checked {
	& ~ .vjs-playlist--wrapper {
		.vjs-playlist--inner {
			transform: translateX(250px);

			@include media (max-width $mobile-breakpoint-max) {
				transform: translateX(170px);
			}

			.vjs-playlist--button {
				opacity: 0.4;

				&:before {
					@include icon--arrow-left-fat;
					padding-left: 0.2em;
				}
			}
		}
	}

	& ~ .video-js {
		.vjs-control-bar {
			z-index: 5;
		}
	}
}

// Video Inline
.article {
	&.article--type-news {
		.article__body {
			.video-inline {
				iframe {
					margin-bottom: 10px;
				}

				&.inline-block--wide,
				&.inline-block--left,
				&.inline-block--right {
					.inline-media__caption-text {
						width: 100%;
					}

					.inline-media__caption-text + .inline-media__caption-author,
					.inline-media__caption-text + .inline-media__caption-credits {
						display: inline-block;
						margin-top: 0.5em;
					}
				}
			}
		}
	}
}

.hero-img.video-opener.video-opener--background {
	overflow: hidden;
	clear: both;
}

$replayButtonSize: 30px;
.video-brightcove {
	.video__replay {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0px;
		bottom: 0px;
		right: 0px;
		left: 0px;
	}
}

.vjs-big-replay-button {
	color: $color-emph;
	display: block;
	top: 50%;
	left: 50%;
	cursor: pointer;
	opacity: 1;
	margin-left: -1em;
	margin-top: -1em;
	width: 2em;
	height: 2em;
	line-height: 2em;
	border: none;
	border-radius: 50%;
	position: absolute;
	background-color: rgba(255, 255, 255, .6);
	transition: border-color .4s, outline .4s, background-color .4s;

	&:hover {
		background-color: rgba(255, 255, 255, 1);
	}

	.vjs-control-text {
		&:before {
			@include icon;
			@include icon--replay;

			font-size: 1.3em;
			margin-top: -0.125em;
		}
	}
}

.video-js .vjs-big-play-button,
.vjs-big-replay-button {
	font-size: 3rem;

	@include media (max-width $mobile-breakpoint-max) {
		font-size: 2.5rem;
	}
}

.teaser--large {
	.video-js .vjs-big-play-button,
	.vjs-big-replay-button {
		font-size: 1.75rem;

		@include media (max-width $mobile-breakpoint-max) {
			font-size: 1.4rem;
		}
	}

	.video-js {
		.vjs-dock-title,
		.vjs-dock-description {
			background: transparent;
			color: transparent;
		}
	}
}
