/*
 * ads
 */
.ad {
	clear: both;
	font-family: $font-extra;
	position: relative;

	@include media(max-width $mobile-breakpoint-max) {
		overflow: hidden;
		width: 100%;
	}

	img {
		@include media(max-width $mobile-breakpoint-max) {
			display: block;
			height: auto;
			max-width: 100%;
		}
	}
}

.page .ad {
	padding: $vertical-space-widgets 0;

	@include media(max-width $mobile-breakpoint-max) {
		padding: $vertical-space-widgets-half 0;
	}
}

.page .container-sidebar .ad,
.page .teaser-leader.adcontainer .ad {
	padding: $vertical-space-widgets-half 0;

	@include media(max-width $mobile-breakpoint-max) {
		padding: $vertical-space-widgets-half;
	}
}

.page .article .sidebar {
	.ad {
		padding: 0;
	}
}

.ad--desktop {
	@include media(max-width $mobile-breakpoint-max) {
		display: none;
	}
}

/*
 * desktop ads
 */

.ad--banners {
	margin: 0 auto;
	max-width: 60em;
	position: relative;
}

// superbanner
.ad .ad__superbanner {
	margin: 0px auto;
	position: static;
	text-align: center;
}

// skyscraper
.ad .ad__skyscraper {
	left: 100%;
	position: absolute;
	right: -10em;
	top: 0;
	width: 7.5em;
}

// billboard
.ad .ad__billboard {
	margin: 1.25em auto;
}

// medium rectangle
.ad .ad__rectangle {
	margin: 0 auto 1.25em;
	max-width: 18.75em;
}

.ad .ad__rectangle img {
	height: auto;
	width: 100%;
}

// Don't panic! We will show the ad, when it returned from adserver.
.ad--asmi {
	display: block;
	padding: 0;
}

/*
 * mobile ads
 */
.ad--mobile {
	@include media(min-width 48em) {
		display: none;
	}
}

.lt_Wrap_Inner {
	@include media(max-width $mobile-breakpoint-max) {
		padding: 0 10px;
	}

	.lt_Wrap_Section_3 .lt_AD,
	.lt_Wrap_Section_1 {
		display: none;
	}

	.lt_Wrap_Section_3 a {
		color: $green;
		font-family: $font-family-meta;
		font-size: 12px;
		font-weight: $font-weight-bold;

		&:first-child {
			float: left;
		}

		&:first-child:after {
			@include icon;
			@include icon--search;
			font-size: 20px;
		}

		&:last-child {
			float: right;
		}

		&:last-child:after {
			@include icon;
			@include icon--arrow-circle-right;
			font-size: 20px;
		}
	}

	.lt_Wrap_Section_2 {
		margin-bottom: .5em;
		text-align: center;
	}

	.lt_Wrap_Section_3 {
		@include clearfix;
	}

	img {
		max-width: 100%;
	}
}

.body__print {
	.ad__skyscraper,
	.ad__superbanner,
	.ad__billboard,
	.ad--banners {
		display: none;
	}
}

.sidebar .ad {
	margin-bottom: $vertical-space-widgets;
}

.content-wide .ad {
	margin-bottom: $vertical-space-section;
}

.article .article__body {
	.ad--marker {
		.ad--marker-inner {
			@include media(max-width $mobile-breakpoint-max) {
				padding: 0;
			}
		}
	}
}

.teaser.advertisement {
	.ad--marker {
		text-align: left;
	}

	.ad.ad--google.ad--marker,
	.ad.ad--asmi.ad--marker {
		display: block;
		width: auto;

		@include media(max-width $mobile-breakpoint-max) {
			margin-bottom: $vertical-space-widgets;
		}
	}

	// Special vom Anbieter
	// Plista

	.plista_widget_innerDisplay {
		@include media(max-width $mobile-breakpoint-max) {
			@include pad(0 $space-innerpage);
		}
	}

	.ad {
		clear: both;
		position: relative;

		@include media(max-width $mobile-breakpoint-max) {
			overflow: hidden;
			width: 100%;
		}

		img {
			@include media(max-width $mobile-breakpoint-max) {
				height: auto;
				max-width: 100%;
			}
		}

		.oneTag {
			@include media(max-width $mobile-breakpoint-max) {
				margin: auto;
			}
		}
	}
}

.ad--note {
	font-size: .625em;
	font-weight: normal;
	margin-bottom: .125em;
}

.ad__billboard {
	margin-bottom: .5em;
}

.ad-top {
	margin: 0 auto;
	margin-top: 1em;
	max-width: 960px;
	text-align: right;
}

//toDo dynamic left value
.ad__fireplace-skyscraper-left {
	display: inline-block;
	left: -200px;
	position: absolute;
	top: 0;

	@include media(max-width $mobile-breakpoint-max) {
		display: none;
	}
}

//toDo dynamic right value
.ad__fireplace-skyscraper-right {
	display: inline-block;
	position: absolute;
	right: -200px;
	top: 1px;

	@include media(max-width $mobile-breakpoint-max) {
		display: none;
	}
}

.page-wrapper {
	position: relative;
}

//toDo dynamic left value
.ad__skyscraper {
	display: inline-block;
	position: absolute;
	right: -160px;
	top: 237px;
}

.ad__skyscraper.add--desktop {
	@include media(max-width $mobile-breakpoint-max) {
		display: none;
	}
}

.ad--mobile .ad__content {
	padding: $vertical-space-widgets-half;
}

.teaser-leader .ad--mobile .ad__content {
	padding: 0;
}

.ad__fireplace-billboard {
	position: relative;
	margin: 0 auto;
	margin-top: $vertical-space-widgets-half;

	.ad--note {
		margin-left: .5em;
	}
}

.ad_superbanner-top {
	display: block;
	position: static;

	.ad__superbanner {
		margin: 0 auto;
	}
}

.teaser-leader-s .ad--marker:before,
.teaser-leader-m .ad--marker:before {
	display: block;
	left: 0;
	margin-bottom: 5px;
	margin-top: 7px;
	position: relative;
}

.container-sidebar-widget-wrapper {
	.ad {
		margin-bottom: $vertical-space-widgets;
	}
}

.ad--marker.madvertise:before {
	color: #d3d3d3;
	content: "anzeige";
	display: block;
	font-family: $font-family-meta;
	font-size: 10px;
	text-transform: uppercase;
	margin-bottom: 2px;

	@include media(min-width $mobile-breakpoint-min) {
		display: none;
	}
}

.ad__fireplace-billboard .ad--marker-inner:before {
	padding-left: 5px;

	@include media(max-width $mobile-breakpoint-max) {
		padding-left: 0;
	}
}

.article .article__body .ad iframe {
	padding: 0;
}

.gallerie-teaser {
	.ad--marker {
		.ad--marker-inner {
			&:before {
				color: $color-text-light;
				content: "Anzeige";
				font-size: 10px;
				font-family: Arial, sans-serif;
				text-transform: uppercase;
				margin-bottom: 2px;
			}
		}
	}
}

